import {MessageDoesNotExist} from "candied/lib/dbc/Errors";
import importedFileManager from "../settings/importedFilesManager";
import {dbcFile} from "./dbcFile";


export class dbcManager extends importedFileManager{

    dbcFileList = [];

    /*
    todo: process the can messages through candied to usable values
    todo: dynamically add all at least once received messages to data distribution
    */

    constructor(){
        super("dbcGUIMgr", "dbc");

        this.setFileListUpdatedCallback(()=>{
            this.readInAllFiles();
        });
    }

    readInAllFiles(){
        let files = this.getAllFilesFromLocalStorage();

        // clear out the list before
        this.dbcFileList = [];

        // and read in all the dbc files
        files.forEach(async (file) => {
            await this.addDBCFromContents(file.fileContent);
        });

        console.log(this.dbcFileList);
    }

    async addDBCFromContents(fileContent){
        let file = new dbcFile();
        let success = await file.openNewDBC(fileContent);

        if(success){
            this.dbcFileList.push(file);
        }else{
            console.log("[DBC GUI Manager] couldn't read in dbc file");
        }
    }

    getMessages(){
        let messagesList = [];

        for (const dbcListElement of this.dbcFileList) {
            for (const message of dbcListElement.dbcJSObj.messages) {
                if(message.length > 1 && typeof message[1] !== "string") {
                    messagesList.push(message[1]);
                }
            }
        }

        return messagesList;
    }

    getMessageForId(id){
        let message = null;

        for (const element of this.dbcFileList) {
            let messageBefore = message;
            try{
                message = element.dbc.getMessageById(id);
            }catch(e){
                if(e instanceof MessageDoesNotExist){
                    continue;
                }
            }

            if(typeof messageBefore !== null && typeof message !== undefined){
                console.log("[dbcManager] multiple Messages for same ID!");
            }
        }

        return message;
    }
}












export default class dbcGUIManager extends dbcManager{

    constructor(){
        super();


        this.setGUI(
            document.getElementById("dbc-file-manager-table"),
            document.getElementById("dbc-file-manager-add-file")
        );
        this.readInAllFiles();
        this.fillGUIWithFileList();
    }
}