import sf from "./helpers/small_functions";
import {zoom} from "./helpers/zoom";
import gui from "./gui";

let selected_cell = null;

export default class rearrangeManager extends gui{

    tileMoveMode = false;

    constructor(bmsObj) {
        super();

        this.bmsObj = bmsObj;

        this.addEventListeners();
    }

    addEventListeners(){
        this.table.addEventListener("click", (e)=> {
            this.tableEventListener(e.target);
        });

        sf.getId("capture-mode-table").addEventListener("click", (e)=> {
            this.tableEventListener(e.target);
        });


        this.table.addEventListener('dblclick', (e) => {

            let objToZoomOn = e.target;
            // loop through all the subcomponents to only zoom on the target td, not for example the button
            while(typeof objToZoomOn != "undefined" && !objToZoomOn.classList.contains("main-table-td-2row")){
                objToZoomOn = objToZoomOn.parentNode;
            }
            zoom.to({element: objToZoomOn, padding: 0, pan: false});

            // hide settings since otherwise you can weirdly scroll into the settings
            this.hideAllSettings();
        })

    }


    toggleTileMoveMode(){
        this.tileMoveMode = !this.tileMoveMode;

        if(this.tileMoveMode){
            this.tileBarInterfaceDropdown.classList.add("breathe-good");
            this.tileBarEditMode.classList.add("breathe-good");
        }else{
            this.tileBarInterfaceDropdown.classList.remove("breathe-good");
            this.tileBarEditMode.classList.remove("breathe-good");

            if(selected_cell){
                // same code as below to "unselect" the selected cell
                if (this.bmsObj.isConnected()) {
                    selected_cell.classList.remove("disabled");
                } else {
                    selected_cell.classList.add("disabled");

                    // remove the disabled property from all children of the selected cell
                    selected_cell.childNodes.forEach((child) => {
                        if (child === this.inlineGaugeDiv) {
                            selected_cell.classList.remove("disabled");
                        }
                    });
                }
                selected_cell = null;
            }
        }
    }

    tableEventListener(target){
        if(this.tileMoveMode) {

            let targetTd = target;
            // loop through all the subcomponents to only zoom on the target td, not for example the button
            while(typeof targetTd != "undefined" && !targetTd.classList.contains("main-table-td-2row")){
                targetTd = targetTd.parentNode;
            }

            // if there is no selected cell to swap the event target with, event target becomes the selected cell
            if (selected_cell == null) {

                selected_cell = targetTd;
                // blur out the selected cell
                selected_cell.classList.add("disabled");

            } else {

                if (this.bmsObj.isConnected()) {
                    selected_cell.classList.remove("disabled");
                } else {
                    selected_cell.classList.add("disabled");

                    // remove the disabled property from all children of the selected cell
                    selected_cell.childNodes.forEach((child) => {
                        if (child === this.inlineGaugeDiv) {
                            selected_cell.classList.remove("disabled");
                        }
                    });
                }

                // actually do the swap
                sf.swap(selected_cell, targetTd);

                // reset the selected cell
                selected_cell = null;

            }

        }


    }

}