// noinspection JSUnusedAssignment

import gui_elements from "../gui_elements";

import sf from "./small_functions";







// extends here is just a trick to inherit also the gui elements to the localConfigCalibGUI
export class localConfigCalibManager extends gui_elements{
    constructor(bmsObj) {
        super();

        this.bmsConfigObj = bmsObj.bmsConfigObj;
        this.bmsCalibObj = bmsObj.bmsCalibObj;
    }


    getCalibStorage(){
        const calibStorageText = localStorage.getItem("calibStorage");

        if(calibStorageText === null
            || calibStorageText === "undefined"){
            console.log("[calib storage] no calibs stored yet.");
            return {};
        }

        let calibStorage;
        try{
            calibStorage = JSON.parse(calibStorageText);
        }catch(e){
            console.log("[calib storage] error parsing json: " + e);
            return;
        }

        return calibStorage;
    }

    getCalibsForSerialNumber(serialNumber){
        let calibStorage = this.getCalibStorage();

        // no calibs saved yet for this serial number
        if(typeof calibStorage[serialNumber] === "undefined"){
            return {};
        }

        return calibStorage[serialNumber];
    }

    getLatestCalibForSerialNumber(serialNumber){
        let calibs = this.getCalibsForSerialNumber(serialNumber);

        let newestCalibUnixTime;
        let newestCalib;
        for (const timestamp in calibs) {
            if(typeof newestCalibUnixTime === "undefined"){
                newestCalibUnixTime = calibs[timestamp].unixTime;
                newestCalib = calibs[timestamp];
                continue;
            }
            if(newestCalibUnixTime < calibs[timestamp].unixTime){
                newestCalibUnixTime = calibs[timestamp].unixTime;
                newestCalib = calibs[timestamp];
            }
        }

        return newestCalib;
    }

    getOldestCalibForSerialNumber(serialNumber){
        let calibs = this.getCalibsForSerialNumber(serialNumber);

        let oldestCalibUnixTime;
        let oldestCalib;
        for (const timestamp in calibs) {
            if(typeof oldestCalibUnixTime === "undefined"){
                oldestCalibUnixTime = calibs[timestamp].unixTime;
                oldestCalib = calibs[timestamp];
                continue;
            }
            if(oldestCalibUnixTime > calibs[timestamp].unixTime){
                oldestCalibUnixTime = calibs[timestamp].unixTime;
                oldestCalib = calibs[timestamp];
            }
        }



        return oldestCalib;
    }

    deleteCalibObj(calib){
        let calibStorage = this.getCalibStorage();

        delete calibStorage[calib.serialNumber][calib.timestamp]

        this.saveCalibStorage(calibStorage);
    }

    deleteOldestCalibObjForSerialNumber(serialNumber){
        let oldestCalib = this.getOldestCalibForSerialNumber(serialNumber);
        this.deleteCalibObj(oldestCalib);
    }

    saveCalibStorage(calibStorage){
        if(typeof calibStorage === "undefined" || calibStorage === null){
            return;
        }

        let calibStorageText = "";
        try{
            calibStorageText = JSON.stringify(calibStorage);
        }catch(e){
            console.log("[calib storage] something went wrong during json.stringify: " + e);
            return;
        }

        localStorage.setItem('calibStorage', calibStorageText);
    }

    addNewCalib(calib, bmsObj){
        let calibStorage = this.getCalibStorage();

        // this static "bms" is okay since the method is only ever called with a valid bms connected
        const serialNumber = bmsObj.deviceInfoMgr.devicesInfo.getDeviceInfo("bms").serialNumber;

        if(typeof calibStorage === "undefined" || calibStorage === null){
            return;
        }

        const timestamp = sf.getDateStamp();
        const unixTime = sf.getUnixTime();

        let toBeStored = {
            unixTime: unixTime,
            timestamp: timestamp,
            serialNumber: serialNumber,
            displayName: bmsObj.name,
            calib: calib.getCalibObject()
        }

        // catch if no calib has been saved for this serial number yet
        if(typeof calibStorage[serialNumber] === "undefined"){
            calibStorage[serialNumber] = {};
        }

        if(typeof this.getLatestCalibForSerialNumber(serialNumber) !== "undefined"){

            // always automatically override the latest calib with the to be added if there are no changes
            if(sf.objectsEqual(this.getLatestCalibForSerialNumber(serialNumber).calib, toBeStored.calib)){
                this.deleteCalibObj(this.getLatestCalibForSerialNumber(serialNumber));
                calibStorage = this.getCalibStorage();
            }
        }


        // save the to be added calib
        calibStorage[serialNumber][timestamp] = toBeStored;
        this.saveCalibStorage(calibStorage);


        // automatically limit to 10 calibs per serial number
        while(Object.keys(this.getCalibsForSerialNumber(serialNumber)).length > 10){
            this.deleteOldestCalibObjForSerialNumber(serialNumber);
        }
    }


}












