import gui_elements from "./gui_elements";
import SVGInject from "@iconfu/svg-inject";

export default class vehicleModel extends gui_elements{

    /*
    todo: implement rpm values
    todo: make the limits configurable & save them
    todo: make the svg configurable and save it in localstorage
     */

    expectedDataLabels = [
        "torqueFL",
        "torqueFR",
        "torqueRL",
        "torqueRR",

        "rpmFL",
        "rpmFR",
        "rpmRL",
        "rpmRR",

        "tempFL",
        "tempFR",
        "tempRL",
        "tempRR",
    ]

    constructor() {
        super();

        SVGInject(document.getElementsByClassName('vehicle-model-image'));

        this.torqueGaugeFL = new torqueGauge("vehicle-model-torque-fl");
        this.torqueGaugeFR = new torqueGauge("vehicle-model-torque-fr");
        this.torqueGaugeRL = new torqueGauge("vehicle-model-torque-rl");
        this.torqueGaugeRR = new torqueGauge("vehicle-model-torque-rr");

        this.enabled = true;
    }

    initDataDistribution(dd){
        this.dd = dd;

        // todo: add identifier, which component expects the label, then economic (on demand) updating will be possible
        this.dd.addExpectedDataLabels(this.expectedDataLabels);

        // updateOnDataInput will be called when data distribution has new data available
        this.dd.registerExpecterUpdate((data)=>{
            this.updateOnDataInput(data);
        });
    }

    updateOnDataInput(data){

        if(typeof data["torqueFL"] !== "undefined"){
            if(typeof data["rpmFL"] !== "undefined"){
                let direction = ((data["rpmFL"] < 0) ? 'backward' : 'forward')
                this.torqueGaugeFL.setTorqueDirection(data["torqueFL"], direction);
            }else{
                this.torqueGaugeFL.setTorqueDirection(data["torqueFL"], "forward");
            }
        }

        if(typeof data["torqueFR"] !== "undefined"){
            if(typeof data["rpmFR"] !== "undefined"){
                let direction = ((data["rpmFR"] < 0) ? 'backward' : 'forward')
                this.torqueGaugeFR.setTorqueDirection(data["torqueFR"], direction);
            }else{
                this.torqueGaugeFR.setTorqueDirection(data["torqueFR"], "forward");
            }
        }

        if(typeof data["torqueRL"] !== "undefined"){
            if(typeof data["rpmRL"] !== "undefined"){
                let direction = ((data["rpmRL"] < 0) ? 'backward' : 'forward')
                this.torqueGaugeRL.setTorqueDirection(data["torqueRL"], direction);
            }else{
                this.torqueGaugeRL.setTorqueDirection(data["torqueRL"], "forward");
            }
        }

        if(typeof data["torqueRR"] !== "undefined"){
            if(typeof data["rpmRR"] !== "undefined"){
                let direction = ((data["rpmRR"] < 0) ? 'backward' : 'forward')
                this.torqueGaugeRR.setTorqueDirection(data["torqueRR"], direction);
            }else{
                this.torqueGaugeRR.setTorqueDirection(data["torqueRR"], "forward");
            }
        }

        // to be implemented: rpm and temp


    }







}



class torqueGauge{

    forward = "forward";
    backward = "backward";

    constructor(className){

        let gaugeElements = document.getElementsByClassName(className);


        // catch the case that HTML is configured wrong
        if(gaugeElements.length !== 2){
            return;
        }

        // set positive and negative gauges
        for (let gauge of gaugeElements) {
            if(gauge.classList.contains("vehicle-model-torque-positive")){
                this.positiveGauge = gauge;
            }

            if(gauge.classList.contains("vehicle-model-torque-negative")){
                this.negativeGauge = gauge;
            }
        }

    }

    setTorqueDirection(torquePercent, direction){
        if(direction === this.forward){

            // make the positive gauge green
            this.positiveGauge.classList.remove("vehicle-model-negative-bg");
            this.positiveGauge.classList.add("vehicle-model-positive-bg");

            // make the negative gauge red
            this.negativeGauge.classList.remove("vehicle-model-positive-bg");
            this.negativeGauge.classList.add("vehicle-model-negative-bg");


            if(torquePercent >= 0){
                this.negativeGauge.style.backgroundSize = "0 0";
                this.positiveGauge.style.backgroundSize = "100% " + torquePercent + "%";
            }

            if(torquePercent < 0){
                this.positiveGauge.style.backgroundSize = "0 0";
                this.negativeGauge.style.backgroundSize = "100% " + (-torquePercent) + "%";
            }
        }


        if(direction === this.backward){

            // make the positive gauge red
            this.positiveGauge.classList.remove("vehicle-model-positive-bg");
            this.positiveGauge.classList.add("vehicle-model-negative-bg");

            // make the negative gauge green
            this.negativeGauge.classList.remove("vehicle-model-negative-bg");
            this.negativeGauge.classList.add("vehicle-model-positive-bg");


            if(torquePercent >= 0){
                this.negativeGauge.style.backgroundSize = "0 0";
                this.positiveGauge.style.backgroundSize = "100% " + torquePercent + "%";
            }

            if(torquePercent < 0){
                this.positiveGauge.style.backgroundSize = "0";
                this.negativeGauge.style.backgroundSize = "100% " + (-torquePercent) + "%";
            }
        }

    }
}