import sf from "./helpers/small_functions";
import gui_elements from "./gui_elements";
import {zoom} from "./helpers/zoom";

export default class captureModeManager extends gui_elements{

    video;

    constraints;

    constructor(){
        super();

        this.video = sf.getId("capture-mode-video");

        this.constraints = {
            video: {
                height: {
                    min: 800
                },
                width: {
                    min: 600
                },
                facingMode: {
                    exact: "environment"
                }
            }
        };

        window.addEventListener('scroll', ()=>{
            if(sf.zoomedIn() && sf.isScrolledIntoView(this.captureModeContainer)){
                zoom.out();
            }
        });

        this.captureModeContainer.addEventListener("dblclick", ()=>{

            if(this.constraints.video.facingMode.exact === "user"){
                this.constraints.video.facingMode.exact = "environment";
            }else{
                this.constraints.video.facingMode.exact = "user";
            }

            console.log(this.constraints);

            try{
                this.stopStream();
            }catch(e){
                console.log("probably no video started");
                console.log(e);
            }
            this.startStream(this.constraints).then();
        });
    }

    activateCaptureMode(){
        this.captureModeContainer.classList.remove("display-none");

        this.constraints.video.aspectRatio = { exact: (this.video.scrollHeight/this.video.scrollWidth)};

        if ('mediaDevices' in navigator && navigator.mediaDevices.getUserMedia) {
            this.startStream(this.constraints).then();
        }
    }

    closeCaptureMode(){
        this.captureModeContainer.classList.add("display-none");

        this.stopStream();
    }



    startStream = async (constraints) => {
        const stream = await navigator.mediaDevices.getUserMedia(constraints);
        this.handleStream(stream);
    };

    handleStream = (stream) => {
        this.video.srcObject = stream;
    };

    stopStream(){
        // return if there is no video stream to close
        if(this.video.srcObject === null || typeof this.video.srcObject === "undefined"){
            return;
        }
        this.video.srcObject.getTracks().forEach(function(track) {
            track.stop();
        });
    }

}