import sf from "../helpers/small_functions";
import wakeLock from "../helpers/wakelock";
import colorMode from "../helpers/color_mode";
import gui from "../gui";

import Coloris from "@melloware/coloris";


export default class interfaceConfig extends gui {

    colorModeObj = null;

    filterBMSConnection;
    connectionStepTimeout;

    updateConnectionWatchDogCB;

    constructor() {
        super();
        this.wakeLock1 = new wakeLock();

        // get config values from localStorage
        this.updateConfig();

        //enable wakelock if set
        if (this.wakeLock) {
            this.wakeLock1.enable();
        }

        // hack to make color pickers render before filling them with their color
        setTimeout(async () => {
            await sf.until(()=>{return colorMode !== null})
            // write values from localStorage into the input fields
            this.setInterfaceConfigValues();
        }, 100);


        this.addEventListeners();


        this.colorisInit();
    }

    initColorModeObj(colorModeObj){
        this.colorModeObj = colorModeObj;
    }

    updateConfig() {
        // default is true for mobile devices, false for desktop;
        this.filterBMSConnection = JSON.parse(localStorage.getItem("filterBMSConnection") || sf.mobileDevice());
        // default is true for mobile devices, false for desktop;
        this.wakeLock = JSON.parse(localStorage.getItem("wakeLockEnabled") || sf.mobileDevice());

        this.colorMode = localStorage.getItem("lastColorMode") || "dark";

        this.customColorForeground = localStorage.getItem("customColorForeground") || "rgb(255,255,255)";
        this.customColorBackground = localStorage.getItem("customColorBackground") || "rgb(0,0,0)";
        this.customColorAccent = localStorage.getItem("customColorAccent") || "rgb(0,193,220)";

        this.customColorCaptureModeBackground = localStorage.getItem("customColorCaptureModeBackground") || "rgba(255,255,255, 0.6)";

        // JSON.parse for string to boolean conversion
        this.autoReadBMSConfigCalib = JSON.parse(localStorage.getItem("autoReadBMSConfigCalib") || true);

        this.devTileActive = JSON.parse(localStorage.getItem("devTileActive") || true);

        this.interfaceMode = localStorage.getItem("interfaceMode") || "normal";

        // Times for data logging averaging intervals for BMS
        this.averagingIntervalU = parseFloat(localStorage.getItem('averagingIntervalU') || "0.25");
        this.averagingIntervalI = parseFloat(localStorage.getItem("averagingIntervalI") || "0.3");
        this.averagingIntervalT = parseFloat(localStorage.getItem("averagingIntervalT") || "0.6");
        this.averagingIntervalCalib = parseFloat(localStorage.getItem("averagingIntervalCalib") || "0.5");

        // Times for data logging averaging intervals for tacho
        this.tachoDataAveragingIntervalV = parseFloat(localStorage.getItem("tachoAveragingIntervalV") || "0.7");
        this.tachoDataAveragingIntervalT = parseFloat(localStorage.getItem("tachoAveragingIntervalT") || "5");

        this.maxAveragingInterval = Math.max(
            this.averagingIntervalU,
            this.averagingIntervalI,
            this.averagingIntervalT,
            this.averagingIntervalCalib
        );

        this.maxTachoAveragingInterval = Math.max(
            this.tachoDataAveragingIntervalV,
            this.tachoDataAveragingIntervalT
        );

        // time for the max value gauges
        this.maxValueHoldOnTime = parseFloat(localStorage.getItem("maxValuesHoldOnTime") || "20");

        // time per step of the connection process before the watchdog cancels the process
        this.connectionStepTimeout = parseFloat(localStorage.getItem("connectionStepTimeout") || "10.0")

        this.showBMSExtTemp = JSON.parse(localStorage.getItem("showBMSExtTemp") || true);
        this.showBMSIntTemp = JSON.parse(localStorage.getItem("showBMSIntTemp") || true);
        this.showMotorTemp = JSON.parse(localStorage.getItem("showMotorTemp") || true);
        this.showTachoExtTemp = JSON.parse(localStorage.getItem("showTachoExtTemp") || true);
    }


    setInterfaceConfigValues(){
        this.colorModeObj.adjust(this.colorMode);

        this.interfaceBMSAutoReadConfigCalib.checked = this.autoReadBMSConfigCalib;

        this.interfaceWakeLock.checked = this.wakeLock;

        this.interfaceAveragingTimeU.value = this.averagingIntervalU;
        this.interfaceAveragingTimeI.value = this.averagingIntervalI;
        this.interfaceAveragingTimeT.value = this.averagingIntervalT;
        this.interfaceAveragingTimeCalib.value = this.averagingIntervalCalib;

        this.interfaceAveragingTimeTachoV.value = this.tachoDataAveragingIntervalV;
        this.interfaceAveragingTimeTachoT.value = this.tachoDataAveragingIntervalT;

        this.interfaceMaxValuesHoldOnTime.value = this.maxValueHoldOnTime;

        this.interfaceConnectionStepTimeout.value = this.connectionStepTimeout;

        this.interfaceBMSFilterConnection.checked = this.filterBMSConnection;

        this.interfaceShowBMSExtTemp.checked = this.showBMSExtTemp;
        this.interfaceShowBMSIntTemp.checked = this.showBMSIntTemp;
        this.interfaceShowMotorTemp.checked = this.showMotorTemp;
        this.interfaceShowTachoExtTemp.checked = this.showTachoExtTemp;

        this.setTempGaugeVisibility(this);

        this.enterInterfaceMode(this.interfaceMode);
    }


    addEventListeners() {
        this.interfaceBMSFilterConnection.addEventListener("change", () => {
            localStorage.setItem('filterBMSConnection', this.interfaceBMSFilterConnection.checked);

            this.updateConfig();
        });

        this.interfaceWakeLock.addEventListener("change", () => {
            localStorage.setItem('wakeLockEnabled', this.interfaceWakeLock.checked);

            this.updateConfig();

            if (interfaceConfig.wakeLock) {
                this.wakeLock1.enable();
            } else {
                this.wakeLock1.disable();
            }

        });

        this.interfaceBMSAutoReadConfigCalib.addEventListener("change", () => {
            localStorage.setItem('autoReadBMSConfigCalib', this.interfaceBMSAutoReadConfigCalib.checked);

            this.updateConfig();
        });

        this.interfaceAveragingTimeU.addEventListener("change", () => {
            let val = this.interfaceAveragingTimeU.value;
            if(!sf.isFloat(val) || parseFloat(val) === 0){
                return;
            }
            localStorage.setItem('averagingIntervalU', String(parseFloat(this.interfaceAveragingTimeU.value)));
            this.updateConfig();
        });
        this.interfaceAveragingTimeI.addEventListener("change", () => {
            let val = this.interfaceAveragingTimeI.value;
            if(!sf.isFloat(val) || parseFloat(val) === 0){
                return;
            }
            localStorage.setItem('averagingIntervalI', String(parseFloat(this.interfaceAveragingTimeI.value)));
            this.updateConfig();
        });
        this.interfaceAveragingTimeT.addEventListener("change", () => {
            let val = this.interfaceAveragingTimeT.value;
            if(!sf.isFloat(val) || parseFloat(val) === 0){
                return;
            }
            localStorage.setItem('averagingIntervalT', String(parseFloat(this.interfaceAveragingTimeT.value)));
            this.updateConfig();
        });
        this.interfaceAveragingTimeCalib.addEventListener("change", () => {
            let val = this.interfaceAveragingTimeCalib.value;
            if(!sf.isFloat(val) || parseFloat(val) === 0){
                return;
            }
            localStorage.setItem('averagingIntervalCalib', String(parseFloat(this.interfaceAveragingTimeCalib.value)));
            this.updateConfig();
        });

        this.interfaceAveragingTimeTachoV.addEventListener("change", () => {
            let val = this.interfaceAveragingTimeTachoV.value;
            if(!sf.isFloat(val) || parseFloat(val) === 0){
                return;
            }
            localStorage.setItem('tachoAveragingIntervalV', String(parseFloat(this.interfaceAveragingTimeTachoV.value)));
            this.updateConfig();
        });

        this.interfaceAveragingTimeTachoT.addEventListener("change", () => {
            let val = this.interfaceAveragingTimeTachoT.value;
            if(!sf.isFloat(val) || parseFloat(val) === 0){
                return;
            }
            localStorage.setItem('tachoAveragingIntervalT', String(parseFloat(this.interfaceAveragingTimeTachoT.value)));
            this.updateConfig();
        });

        this.interfaceMaxValuesHoldOnTime.addEventListener("change", () => {
            let val = this.interfaceMaxValuesHoldOnTime.value;
            if(!sf.isFloat(val) || parseFloat(val) === 0){
                return;
            }
            localStorage.setItem('maxValuesHoldOnTime', String(parseFloat(this.interfaceMaxValuesHoldOnTime.value)));
            this.updateConfig();
        });

        this.interfaceConnectionStepTimeout.addEventListener("change", () => {
            let val = this.interfaceConnectionStepTimeout.value;
            if(!sf.isFloat(val) || parseFloat(val) === 0){
                return;
            }
            localStorage.setItem('connectionStepTimeout', String(parseFloat(this.interfaceConnectionStepTimeout.value)));
            this.updateConfig();

            // call the hopefully registered callback to actually update the value within the bms class
            if(typeof this.updateConnectionWatchDogCB === "function"){
                this.updateConnectionWatchDogCB();
            }
        });


        this.interfaceShowBMSExtTemp.addEventListener("change", () => {
            localStorage.setItem('showBMSExtTemp', this.interfaceShowBMSExtTemp.checked);
            this.updateConfig();
            this.setTempGaugeVisibility(this);
        });
        this.interfaceShowBMSIntTemp.addEventListener("change", () => {
            localStorage.setItem('showBMSIntTemp', this.interfaceShowBMSIntTemp.checked);
            this.updateConfig();
            this.setTempGaugeVisibility(this);
        });
        this.interfaceShowMotorTemp.addEventListener("change", () => {
            localStorage.setItem('showMotorTemp', this.interfaceShowMotorTemp.checked);
            this.updateConfig();
            this.setTempGaugeVisibility(this);
        });
        this.interfaceShowTachoExtTemp.addEventListener("change", () => {
            localStorage.setItem('showTachoExtTemp', this.interfaceShowTachoExtTemp.checked);
            this.updateConfig();
            this.setTempGaugeVisibility(this);
        });
    }

    updateInterfaceMode(mode){
        // only let the user go down-stream:
        // normal -> experimental -> development
        let modeToSet = "";
        if(this.interfaceMode === "normal"){
            if(mode === "exp"){modeToSet = mode;}
            if(mode === "dev"){modeToSet = mode;}
        }
        if(this.interfaceMode === "exp"){
            if(mode === "dev"){modeToSet = mode;}
        }

        localStorage.setItem('interfaceMode', modeToSet);

        // update the config object
        this.updateConfig();

        // execute the changes in the gui
        this.setInterfaceConfigValues();
    }


    // Color Picker configuration (don't touch xD)
    colorisInit() {

        Coloris.init();

        Coloris({
            theme: 'polaroid',
        });


        /** Default configuration **/

        Coloris({
            el: '.coloris',
            swatches: [
                '#264653',
                '#2a9d8f',
                '#e9c46a',
                '#f4a261',
                '#e76f51',
                '#d62828',
                '#023e8a',
                '#0077b6',
                '#0096c7',
                '#00b4d8',
                '#48cae4'
            ],
            focusInput: false,
            selectInput: false,
        });

        Coloris.setInstance('.foreground-color', {
            theme: 'polaroid'
        });
        Coloris.setInstance('.background-color', {
            theme: 'polaroid'
        });
        Coloris.setInstance('.accent-color', {
            theme: 'polaroid'
        });

        Coloris.setInstance('.capture-mode-background-color', {
            theme: 'polaroid'
        });

    }
}