import sf from "../helpers/small_functions";
import localConfigCalibManager from "../helpers/configcalib_processing";
import localConfigCalibGUI from "../helpers/configcalib_processing";
import {CharacteristicUnavailableError} from "../ble/ble";

/* BMS Calib functions */

export default class bmsCalib extends localConfigCalibGUI{
    inVolt1CorrectionVal;
    inVolt2CorrectionVal;
    inVolt3CorrectionVal;
    outVoltCorrectionVal;

    shunt1Val;
    shunt2Val;
    shunt3Val;

    constructor(bmsObj){
        super(bmsObj);

        // add event listeners here since localConfigCalibGUI may be called when a standard calib device is connected

        this.initialised = false;

        this.bmsObj = bmsObj;
    }

    addEventListeners(){

         this.addCalibGUIEventListeners();

        // Calculate Button Event listeners
        sf.getId("calculateInV1").addEventListener("click", () => {
            this.inVolt1Correction.value = (this.inVolt1CorrectionVal * (parseFloat(this.inVolt1Actual.innerText) / parseFloat(this.inVolt1Target.value))).toFixed(0);
        });
        sf.getId("calculateInV2").addEventListener("click", () => {
            this.inVolt2Correction.value = (this.inVolt2CorrectionVal * (parseFloat(this.inVolt2Actual.innerText) / parseFloat(this.inVolt2Target.value))).toFixed(0);
        });
        sf.getId("calculateInV3").addEventListener("click", () => {
            this.inVolt3Correction.value = (this.inVolt3CorrectionVal * (parseFloat(this.inVolt3Actual.innerText) / parseFloat(this.inVolt3Target.value))).toFixed(0);
        });
        sf.getId("calculateOut").addEventListener("click", () => {
            this.outVoltCorrection.value = (this.outVoltCorrectionVal * (parseFloat(this.outVoltActual.innerText) / parseFloat(this.outVoltTarget.value))).toFixed(0);
        });
        sf.getId("calculateShunt1").addEventListener("click", () => {
            this.shunt1.value = (this.shunt1Val * (parseFloat(this.shunt1Actual.innerText) / parseFloat(this.shunt1Target.value))).toFixed(0);
        });
        sf.getId("calculateShunt2").addEventListener("click", () => {
            this.shunt2.value = (this.shunt2Val * (parseFloat(this.shunt2Actual.innerText) / parseFloat(this.shunt2Target.value))).toFixed(0);
        });
        sf.getId("calculateShunt3").addEventListener("click", () => {
            this.shunt3.value = (this.shunt3Val * (parseFloat(this.shunt3Actual.innerText) / parseFloat(this.shunt3Target.value))).toFixed(0);
        });



        sf.getId("board-calib-read").addEventListener("click", () => {
            this.readBMSCalib((success, change)=>{
                if(success){
                    if(change){
                        // indicate success on change
                        this.indicateSuccess(500);
                    }else{
                        // warning on "read successful but nothing changed"
                        this.indicateWarning(500);
                    }
                }else{
                    this.indicateFailure();
                }
            });
        });


        sf.getId("board-calib-write").addEventListener("click", () => {
            // getting GUI element data
            this.readInGUIValues();

            this.writeBMSCalib();
        });


    }


    isStandardCalib(){
        return (this.inVolt1CorrectionVal === 6000
            &&  this.inVolt2CorrectionVal === 6000
            &&  this.inVolt3CorrectionVal === 6000
            &&  this.shunt1Val === 500
            &&  this.shunt2Val === 500
            &&  this.shunt3Val === 500)
    }

    indicateSuccess(duration) {
        this.boardCalibTable.classList.add("success");
        setTimeout(() => {
            this.boardCalibTable.classList.remove("success");
        }, duration);
    }

    indicateWarning(duration){
        this.boardCalibTable.classList.add("warning");
        setTimeout(() => {
            this.boardCalibTable.classList.remove("warning");
        }, duration);
    }

    indicateFailure() {
        this.boardCalibTable.classList.add("fault");
        setTimeout(() => {
            this.boardCalibTable.classList.remove("fault");
        }, 2000);
    }

    readInCalibObject(c){
        this.inVolt1CorrectionVal = c.inVolt1Correction;
        this.inVolt2CorrectionVal = c.inVolt2Correction;
        this.inVolt3CorrectionVal = c.inVolt3Correction;
        this.outVoltCorrectionVal = c.outVoltCorrection;
        this.shunt1Val = c.shunt1;
        this.shunt2Val = c.shunt2;
        this.shunt3Val = c.shunt3;
    }

    getCalibObject(){
        let calib = {};

        calib.inVolt1Correction = this.inVolt1CorrectionVal;
        calib.inVolt2Correction = this.inVolt2CorrectionVal;
        calib.inVolt3Correction = this.inVolt3CorrectionVal;
        calib.outVoltCorrection = this.outVoltCorrectionVal;
        calib.shunt1 = this.shunt1Val;
        calib.shunt2 = this.shunt2Val;
        calib.shunt3 = this.shunt3Val;


        return calib;
    }

    readInJSON(json){
        // read as in past tense
        let readInCalibObject;
        try{
            readInCalibObject =  JSON.parse(json);
        }catch(e){
            console.log("[bms calib] unable to read in JSON: " + e);
        }

        if(typeof readInCalibObject === "undefined"){
            return;
        }

        this.readInCalibObject(readInCalibObject);
    }

    getJSON(){
        if(!this.initialised){
            return;
        }

        return JSON.stringify(this.getCalibObject());
    }

    readInBuffer(buffer){
        let bufferBefore = this.getBuffer();

        this.inVolt1CorrectionVal = (buffer.getUint8(1) << 8) | buffer.getUint8(0);
        this.inVolt2CorrectionVal = (buffer.getUint8(3) << 8) | buffer.getUint8(2);
        this.inVolt3CorrectionVal = (buffer.getUint8(5) << 8) | buffer.getUint8(4);
        this.outVoltCorrectionVal = (buffer.getUint8(7) << 8) | buffer.getUint8(6);

        this.shunt1Val = (buffer.getUint8(9) << 8) | buffer.getUint8(8);
        this.shunt2Val = (buffer.getUint8(11) << 8) | buffer.getUint8(10);
        this.shunt3Val = (buffer.getUint8(13) << 8) | buffer.getUint8(12);


        this.initialised = true;

        // return if change has occurred
        return !sf.arraysEqual(bufferBefore, this.getBuffer());
    }

    readInGUIValues(){
        this.inVolt1CorrectionVal = parseInt(this.inVolt1Correction.value);
        this.inVolt2CorrectionVal = parseInt(this.inVolt2Correction.value);
        this.inVolt3CorrectionVal = parseInt(this.inVolt3Correction.value);

        this.outVoltCorrectionVal = parseInt(this.outVoltCorrection.value);

        this.shunt1Val = parseInt(this.shunt1.value);
        this.shunt2Val = parseInt(this.shunt2.value);
        this.shunt3Val = parseInt(this.shunt3.value);
    }

    setGUIValues(){
        this.inVolt1Correction.value = this.inVolt1CorrectionVal;
        this.inVolt2Correction.value = this.inVolt2CorrectionVal;
        this.inVolt3Correction.value = this.inVolt3CorrectionVal;
        this.outVoltCorrection.value = this.outVoltCorrectionVal;

        this.shunt1.value = this.shunt1Val;
        this.shunt2.value = this.shunt2Val;
        this.shunt3.value = this.shunt3Val;
    }

    getBuffer(){
        let buffer = [];

        // voltage dividers
        buffer[0] = sf.to16bit(this.inVolt1CorrectionVal)[0];
        buffer[1] = sf.to16bit(this.inVolt1CorrectionVal)[1];

        buffer[2] = sf.to16bit(this.inVolt2CorrectionVal)[0];
        buffer[3] = sf.to16bit(this.inVolt2CorrectionVal)[1];

        buffer[4] = sf.to16bit(this.inVolt3CorrectionVal)[0];
        buffer[5] = sf.to16bit(this.inVolt3CorrectionVal)[1];

        buffer[6] = sf.to16bit(this.outVoltCorrectionVal)[0];
        buffer[7] = sf.to16bit(this.outVoltCorrectionVal)[1];

        // shunts
        buffer[8] = sf.to16bit(this.shunt1Val)[0];
        buffer[9] = sf.to16bit(this.shunt1Val)[1];

        buffer[10] = sf.to16bit(this.shunt2Val)[0];
        buffer[11] = sf.to16bit(this.shunt2Val)[1];

        buffer[12] = sf.to16bit(this.shunt3Val)[0];
        buffer[13] = sf.to16bit(this.shunt3Val)[1];


        return buffer;
    }

    updateActualData(data){
        this.inVolt1Actual.innerHTML = data.u1;
        this.inVolt2Actual.innerHTML = data.u2;
        this.inVolt3Actual.innerHTML = data.u3;
        this.shunt1Actual.innerHTML = data.i1;
        this.shunt2Actual.innerHTML = data.i2;
        this.shunt3Actual.innerHTML = data.i3;
        this.outVoltActual.innerHTML = data.uOut;
    }






    readBMSCalib(cb){
        this.bmsObj.characteristics.bmsCalibCharacteristic.readValue().then(calibValues => {

            const originallyInitialised = this.initialised;

            // first read in GUI Values so that there is actually a change when reading in the new Buffer
            if(this.initialised){
                this.readInGUIValues();
            }
            let changeOccurred = this.readInBuffer(calibValues);

            // cheap way to not make it warn on the first read
            if(!originallyInitialised){
                changeOccurred = true;
            }

            this.setGUIValues();


            // to make calib "un-fuck-up-able"
            if(!this.isStandardCalib()){
                this.addNewCalib(this, this.bmsObj);
            }else{

                // this is a problem when autostart is enabled!
                // not the case in the default config tho, so probably alright
                let localManager = new localConfigCalibManager(this.bmsObj);

                let latestCalib = localManager.getLatestCalibForSerialNumber(this.bmsObj.serialNumber);

                // only load backup and write it automatically if backup actually exists
                if(typeof latestCalib !== "undefined"){
                    this.readInCalibObject(latestCalib.calib);
                    this.setGUIValues();

                    this.writeBMSCalib();
                }

            }

            this.fillInCalibBackupGUI();

            cb(true, changeOccurred);
        }).catch(()=>{
            cb(false, false);
        });
    }


    writeBMSCalib(){
        // writing it to characteristic
        this.bmsObj.characteristics.bmsCalibCharacteristic.writeValue(Uint8Array.from(this.getBuffer()).buffer).then(_ => {
            console.log("[bmsCalib] successfully wrote calib");

            setTimeout(() => {
                this.readBMSCalib((success)=>{
                    if(success){
                        // ignore change here since every successful write operation should give positive feedback
                        // otherwise users will get annoyed
                        this.indicateSuccess(3000);
                    }else{
                        this.indicateFailure();
                    }
                });
            }, 200);
        }).catch(error => {
            this.indicateFailure();

            // catch the "characteristic no longer valid" error
            if(error instanceof DOMException && error.message.includes("no longer valid")){
                throw new CharacteristicUnavailableError();
            }
        });
    }


}
