import gui from "../gui";
import MCUManager from "./otaHelpers/mcumgr";
import tilebar from "../tilebar";
import sf from "../helpers/small_functions";

export default class otaController extends gui{

    /*
    todo: disable everything while ota update is in progress
     */

    file = null;
    images = [];

    onUpdateSuccessfulCB;


    MGMT_GROUP_ID_IMAGE = 1;
    IMG_MGMT_ID_STATE = 0;

    constructor(bleDevice) {
        super();

        this.bleDevice = bleDevice;

        this.mcumgr = new MCUManager(bleDevice);


        this.mcumgr.onImageUploadProgress(({ percentage }) => {
            this.otaUpdateInfoText.innerText = "Uploading... " + percentage.toFixed(1) + "%";
        });


        this.mcumgr.onMessage(({ _, group, id, data }) => {
            switch (group) {
                case this.MGMT_GROUP_ID_IMAGE:
                    switch (id) {
                        case this.IMG_MGMT_ID_STATE:
                            this.images = data.images;
                            /*
                            this.images.forEach(image => {
                                console.log(image);
                            });
                            */
                            break;
                    }
                    break;
                default:
                    console.log('Unknown group');
                    break;
            }
        });



        this.mcumgr.onImageUploadFinished(async () => {
            this.otaUpdateInfoText.innerText = 'Upload complete';
            console.log("done uploading image");
            try{
                await this.mcumgr.cmdImageState();
            }catch (e){
                this.onUpdateFailureCB();
                return;
            }



            // fill the serviceInitDone Callback, which is called when the connection process was successful
            // in this case, the process should occur after resetting and auto-reconnecting after the update
            this.bleDevice.serviceInitDoneCB = ()=>{
                setTimeout(async ()=>{

                    // scroll to the board information section to let the user see the update completed text

                    // luckily, this works without any set parameters and doesn't initialize event listeners
                    let tb = new tilebar();
                    tb.scrollToBoardInfo();

                    // Initializing OTA Update Service
                    this.otaUpdateInfoText.innerText = 'Initialising Update Service';
                    try{
                        await this.mcumgr.connect();
                    }catch (e){
                        this.onUpdateFailureCB();
                        return;
                    }


                    // get the image information,
                    // which is needed
                    // for checking successful copying to slot 0 before confirming
                    try{
                        await this.mcumgr.cmdImageState();
                    }catch (e){
                        this.onUpdateFailureCB();
                        return;
                    }


                    // this wait block need to be here since the above-called cmsImageState() is non-blocking
                    // so the callback doesn't instantly arrive
                    let wait = true;
                    setTimeout(()=>{
                        wait = false;
                    }, 2000);

                    await sf.until(()=>{return !(wait && this.images[0].confirmed)});



                    this.otaUpdateInfoText.innerText = 'Confirming Upload';

                    console.log(this.images);

                    //CONFIRM
                    // confirms the image if the slot 0 image (the one that is booted) is not yet confirmed
                    // this is only the case if it's the new firmware
                    if (this.images.length > 0 && this.images[0].confirmed === false) {
                        try{
                            console.log("confirming image");
                            await this.mcumgr.cmdImageConfirm(this.images[0].hash);

                            this.otaUpdateInfoText.innerText = 'Auto update COMPLETE!';
                            this.onUpdateSuccessfulCB();

                        }catch (e){
                            this.onUpdateFailureCB();
                            return;
                        }
                    }else{
                        this.otaUpdateInfoText.innerText = 'Auto update failed!';
                    }


                    // indicate that the hard part of the update - the upload
                    // is over and the disconnect watchdog can be re-enabled
                    this.bleDevice.otaUpdateInProgress = false;


                    // reset this very callback, so it doesn't affect the normal connection process
                    this.bleDevice.serviceInitDoneCB = null;
                }, 2000);
                // this delay with 2s is for user experience
                // with less of a delay, everything happens simultaneously, and the UX goes down the drain

            };



            setTimeout(async()=>{
                // continuation of the automated upload process

                //TEST

                this.otaUpdateInfoText.innerText = 'Testing Image';
                if (this.images.length > 1 && this.images[1].pending === false) {
                    try{
                        await this.mcumgr.cmdImageTest(this.images[1].hash);
                    }catch (e){
                        this.onUpdateFailureCB();
                        return;
                    }
                }

                //RESET
                this.otaUpdateInfoText.innerText = 'Will reset shortly';

                setTimeout(async ()=>{
                    try{
                        await this.mcumgr.cmdReset();
                    }catch (e){
                        this.onUpdateFailureCB();
                    }
                }, 1500);
            }, 1500);
        });

    }

    // callback is called when the file was successfully loaded
    updateSelectedFile(file, cb){
        const reader = new FileReader();

        // this callback is called when the reader has successfully read in the file (call for that is below)
        reader.onload = async () => {
            this.fileData = reader.result;
            cb();
        };

        reader.readAsArrayBuffer(file);
    }

    async startUpdateProcess(onSuccessCB, onFailureCB){

        this.onUpdateSuccessfulCB = onSuccessCB;

        this.onUpdateFailureCB = onFailureCB;

        this.bleDevice.otaUpdateInProgress = true;

        // the update process follows this rather strict schedule to be successful:

        /*
            Connect
            Erase
            Upload

            (Callback since this takes lots of time)

            Test
            Reset

            (reconnect the device)

            Confirm

            DONE!
        */

        // some of these actions are asynchronous in the sense that they work based on callbacks.

        // Initializing OTA Update Service
        this.otaUpdateInfoText.innerText = 'Initialising Update Service';
        try{
            await this.mcumgr.connect();
        }catch (e){
            this.onUpdateFailureCB();
            return;
        }


        setTimeout(async ()=>{
            // ERASE
            this.otaUpdateInfoText.innerText = 'Erasing';
            try{
                await this.mcumgr.cmdImageErase();
            }catch (e){
                this.onUpdateFailureCB();
                return;
            }


            setTimeout(()=>{
                //UPLOAD
                this.otaUpdateInfoText.disabled = true;
                //event.stopPropagation();
                if (this.fileData) {
                    this.mcumgr.cmdUpload(this.fileData).catch(()=>{
                        this.onUpdateFailureCB();
                    });
                }else{
                    this.otaUpdateInfoText.innerText = 'No firmware loaded!';
                }
            },1000);

            // as this is not asynchronous but with a callback function.
            // this continues in the onImageUploadFinished callback registered in constructor
        }, 1000);
    }

}