import bleDevice from "./bleDevice";
import tachoConfig from "../settings/tacho-config";
import tachoDataProcessor from "../dataProcessing/tacho_processing";
import sf from "../helpers/small_functions";
import bleConnectionStates from "./ble";
import canBridge from "../can/canBridge";

// noinspection ShiftOutOfRangeJS

export default class tacho extends bleDevice{

    services = {
        tachoDataLoggingServiceUuid: {
            serviceUuid: "ffd70200-fe1b-4b6d-aba1-36cc0bab3e3d",
            characteristics: {
                tachoDataLoggingCharacteristicUuid: "ffd70201-fe1b-4b6d-aba1-36cc0bab3e3d"
            },
            required: true
        },

        tachoConfigServiceUuid: {
            serviceUuid: "ffd70100-fe1b-4b6d-aba1-36cc0bab3e3d",
            characteristics: {
                tachoConfigCharacteristicUuid: "ffd70101-fe1b-4b6d-aba1-36cc0bab3e3d",
                tachoOdometerCharacteristicUuid: "ffd70102-fe1b-4b6d-aba1-36cc0bab3e3d"
            },
            required: true
        },

        otaUpdateServiceUuid: {
            serviceUuid: '8d53dc1d-1db7-4cd3-868b-8a527460aa84',
            characteristics: {},
            required: false
        },

        voltaicCanBridgeServiceUuid: {
            serviceUuid: "ffd70300-fe1b-4b6d-aba1-36cc0bab3e3d",
            characteristics: {
                canBridgeTXCharacteristicUuid: "ffd70301-fe1b-4b6d-aba1-36cc0bab3e3d",
                canBridgeRXConfigCharacteristicUuid: "ffd70302-fe1b-4b6d-aba1-36cc0bab3e3d",
                canBridgeRXStreamCharacteristicUuid: "ffd70303-fe1b-4b6d-aba1-36cc0bab3e3d"
            },
            required: false
        }
    }

    canBridgeSupported = true;

    tachoDataProcessor;

    interfaceConfig;


    constructor(interfaceConfig, dataLoggingMgr, deviceInfoMgr) {
        super("Tacho", interfaceConfig);

        this.connectionState = bleConnectionStates.disconnected;

        // initialize constructor arguments to class attributes
        this.interfaceConfig = interfaceConfig;
        this.dataLoggingMgr = dataLoggingMgr;
        this.deviceInfoMgr = deviceInfoMgr;


        // create a new config object
        this.tachoConfigObj = new tachoConfig(this);
        // adding event listeners separately to avoid having multiple listeners in the future
        this.tachoConfigObj.addEventListeners();

        this.tachoDataProcessor = new tachoDataProcessor(this, interfaceConfig, dataLoggingMgr);


        let guiOps = {};
        guiOps.setOverlayText = (text)=>{ this.setAutoconnectTachoText(text); }
        guiOps.resetOverlayText = ()=>{ this.resetAutoconnectTachoSilentInstant(); }
        this.setGUIOperations(guiOps);

        this.setServices(this.services);
        this.setServiceInitDoneCallback(() => { this.#serviceInitDone(); });
        this.setDisconnectedCallback(()=>{ this.#deviceDisconnected(); });

        //this.print();
    }

    print() {
        setTimeout(()=>{
            console.log("tacho: " + this.connectionState);
            this.print();
        }, 200);
    }

    initDataDistribution(dd){
        this.dd = dd;
        this.tachoDataProcessor.gatherer.initDataDistribution(this.dd);
        this.tachoDataProcessor.initDataDistribution(this.dd);
    }

    initBMSObj(bmsObj){
        this.bmsObj = bmsObj;
    }


    // this function is called when the bluetooth connection and service initialization is over
    // that of course does not mean the device is ready to use though, still the config needs to be read,
    // and the data logging needs to be enabled
    async #serviceInitDone(){

        try{
            await this.#getDeviceInfo();
        }catch(error){
            console.warn("[bms connection] failed to get device info!");
        }

        this.setAutoconnectTachoText("Reading Config");
        console.log("[tacho connection] auto-reading tacho config");

        this.connectionWatchDog.start();
        this.tachoConfigObj.readTachoConfig(async () => {
        if(this.connectionWatchDog.timedOut){return;}

            // enabling the notifications on the CANBridge RX Stream characteristic,
            // but only if the characteristics are actually supported!
            if(typeof this.characteristics.canBridgeTXCharacteristic !== "undefined"
            && typeof this.characteristics.canBridgeRXConfigCharacteristicUuid !== "undefined"
            && typeof this.characteristics.canBridgeRXStreamCharacteristicUuid !== "undefined"){
                this.canBridgeSupported = true;

                // initialize the new canBridge object!
                // this automatically registers all the listeners for the characteristics
                this.connectionWatchDog.start();
                this.cB = new canBridge(this.characteristics.canBridgeTXCharacteristic, this.characteristics.canBridgeRXConfigCharacteristic, this.characteristics.canBridgeRXStreamCharacteristic);
                if(this.connectionWatchDog.timedOut){return;}
                this.connectionWatchDog.stop();

                this.addPfuschListeners();
            }else{
                document.getElementById("reverse-fr").disabled = true;
                document.getElementById("reverse-fl").disabled = true;
                document.getElementById("reverse-rl").disabled = true;
                document.getElementById("reverse-rr").disabled = true;
            }

            // enabling data logging
            this.setAutoconnectTachoText("Enabling data logging");
            this.connectionWatchDog.start();
            await this.characteristics.tachoDataLoggingCharacteristic.startNotifications();
            if(this.connectionWatchDog.timedOut){return;}
            this.connectionWatchDog.stop();

            this.setAutoconnectTachoText("Successfully connected!");
            this.characteristics.tachoDataLoggingCharacteristic.addEventListener('characteristicvaluechanged', (event)=>{
                this.tachoDataProcessor.processData(event);
            });
        });


        // wait until all initializations are done
        // (or in case of disconnect during reading - block the execution until the watchdog runs out)
        await sf.until(() => {
            return this.tachoConfigObj.initialised;
        });

        // unsure what happens with a new connection - does this just get executed twice?


        this.reconnect = true;
        this.connectionState = bleConnectionStates.connected;



        this.tachoDataProcessor = new tachoDataProcessor(this, this.interfaceConfig, this.dataLoggingMgr);
        this.tachoDataProcessor.gatherer.initDataDistribution(this.dd);
        this.tachoDataProcessor.initDataDistribution(this.dd);

        if(typeof this.serviceInitDoneCB !== "undefined" && this.serviceInitDoneCB !== null){
            this.serviceInitDoneCB();
        }



        // longer time window to see the connection process of the bms if it's either searching or connecting
        if (this.bmsObj.connectionState === bleConnectionStates.connecting
            || this.bmsObj.connectionState === bleConnectionStates.searching) {
            setTimeout(() => {
                if(!this.isConnected()){
                    // catch if disconnect happened between success and enabling!
                    return;
                }
                this.inlineConnected();
                this.disableNothingConnectedOverlay();
            }, 1000);
        } else {
            // shorter window as bms is disconnected anyway
            setTimeout(() => {
                if(!this.isConnected()){
                    // catch if disconnect happened between success and enabling!
                    return;
                }
                this.inlineConnected();
                this.disableNothingConnectedOverlay();
            }, 500);
        }
    }




    async #getDeviceInfo(){
        let decoder = new TextDecoder('utf-8');


        // Device information service
        let service = await this.gattServer.getPrimaryService('device_information');

        let deviceInfo = {};

        await service.getCharacteristic(BluetoothUUID.getCharacteristic('hardware_revision_string')).then(async (characteristic) => {
            await characteristic.readValue().then(value => {
                deviceInfo.hardwareRevision = decoder.decode(value);
            });
        });

        await service.getCharacteristic(BluetoothUUID.getCharacteristic('firmware_revision_string')).then(async (characteristic) => {
            await characteristic.readValue().then(value => {
                deviceInfo.firmwareRevision = decoder.decode(value);
            });
        });

        await service.getCharacteristic(BluetoothUUID.getCharacteristic('model_number_string')).then(async (characteristic) => {
            await characteristic.readValue().then(value => {
                deviceInfo.modelNumber = decoder.decode(value);
            });
        }).catch(()=>{
            deviceInfo.modelNumber = "not supported";
        });

        await service.getCharacteristic(BluetoothUUID.getCharacteristic('software_revision_string')).then(async (characteristic) => {
            await characteristic.readValue().then(value => {
                deviceInfo.serialNumber = decoder.decode(value);
            });
        }).catch(()=>{
            deviceInfo.serialNumber =  "not supported";
        });

        this.#updateDeviceInfo(deviceInfo);
    }


    #updateDeviceInfo(deviceInfo) {

        this.deviceInfoMgr.devicesInfo.updateDeviceInfo("Tacho", deviceInfo);

        // in bms, there's a "check for firmware update" here

        // more can-specific stuff to be added!
    }



    #deviceDisconnected(){
        this.resetInlineValues();

        this.inlineDisconnected();

        // enable nothingConnectedOverlay if bms was the last device to disconnect
        if (!this.bmsObj.isConnected()) {
            this.enableNothingConnectedOverlay();
        }

        this.resetAutoconnectTachoSilentInstant();

    }

    async pauseDataLogging(){
        await this.characteristics.tachoDataLoggingCharacteristic.stopNotifications();
    }
    async unPauseDataLogging(){
        await this.characteristics.tachoDataLoggingCharacteristic.startNotifications();
    }





    resetInlineValues() {
        this.tachoDataProcessor.tachoAveragingBuffer = [];
        this.tachoDataProcessor.maxInlineValueBuffer = [];

        // reset range calculation stuff
        this.tachoDataProcessor.drivenDistanceOffset = -1;
    }
}