// this file is supposed to serve as a parent class for both guis that need to handle imported files
// - the dbc manager gui and the plugin gui

import sf from "../helpers/small_functions";
import fileObject from "../helpers/fileObject";

export default class importedFileManager{

    guiTable;
    guiImportButton;

    fileNames = {};

    constructor(usage, fileType){
        this.usage = usage;
        this.fileType = fileType;
    }

    setFileListUpdatedCallback(cb){
        if(typeof cb === "function"){
            this.fileListUpdatedCallback = cb;
        }else{
            console.warn("[imported Files Manager] passed callback parameter is not a function!");
        }
    }

    setGUI(guiTable, guiImportButton){
        this.guiTable = guiTable;
        this.guiImportButton = guiImportButton;

        this.guiImportButton.addEventListener("click", async () => {

            let fileObj = await new fileObject(this.fileType);
            await this.saveFileToLocalStorage(fileObj);

            // afterwards, update the gui!
            this.fillGUIWithFileList();

            // and finally, call the callback which processes the entire file list depending on the file type
            // (for dbc, all dbc files will be read in, for plugins, the plugins will be loaded)
            this.callFileListUpdatedCallback();
        });
    }

    async saveFileToLocalStorage(fileObj){
        let newFileNamesEntry = {};

        this.fileNames = this.getFileNamesFromLocalStorage();
        let storageLocationsList = this.fileNames
            .map(x => x.storageLocation);

        let newStorageLocation = sf.random10Digit();

        // make sure there are no doubles under any circumstances
        while (storageLocationsList.includes(newStorageLocation)){
            newStorageLocation = sf.random10Digit();
        }

        newFileNamesEntry.storageLocation = newStorageLocation;
        newFileNamesEntry.metadata = fileObj.metadata;

        // get the file contents to write them to local storage
        let file = await fileObj.readFile();
        let fileContent = await file.text();

        // to make sure and to make webstorm happy
        if(typeof fileContent === "string"){

            // write to localstorage
            localStorage.setItem("importedFilesList-" + this.usage + "-" + newStorageLocation, fileContent);

            // also write index to localstorage
            this.addFileEntryToFileNames(newFileNamesEntry);
        }
    }

    removeFileFromLocalStorage(storageLocation){
        const loc = "importedFilesList-" + this.usage + "-" + storageLocation;
        // only remove if it's not yet known
        if(localStorage.getItem(loc) !== null){
            localStorage.removeItem(loc);
        }

        this.removeFileEntryFromFileNames(storageLocation);
    }

    fillGUIWithFileList(){
        // get the inner HTML of all the headers
        let headerContents = [].slice.call(this.guiTable.getElementsByTagName("th")) // get all "th" in an array
            .map(x => x.getElementsByTagName("p")[0].innerHTML); // get their respective p tag's innerHTML

        let files = this.getFileNamesFromLocalStorage();

        // clear out the old rows of the table
        [].slice.call(this.guiTable.getElementsByTagName("tr")).forEach((child) => {
            if(child.childNodes.length > 0 && typeof child.childNodes[0].tagName !== "undefined"){
                if(child.childNodes[0].tagName.toLowerCase() === "td"){
                    this.guiTable.removeChild(child);
                }
            }
        });

        // sequentially fill the table
        for (const fileElement of files) {
            let tr = document.createElement("tr");

            tr.id = fileElement.storageLocation;


            // this callback is used for the actions of the single files
            tr.addEventListener("click", (event) => {
                let target = event.target;

                // ignore the click if it wasn't a td
                if(target.tagName.toLowerCase() !== "td"){
                    return;
                }

                // DELETE FILE CALLBACK
                if(typeof target.id !== "undefined" && target.id === "delete-element-td"){
                    let parent = target.parentNode;

                    if(typeof parent.id !== "undefined" && parent.id !== null){
                        // remove the selected file
                        this.removeFileFromLocalStorage(parent.id);

                        // and update the table
                        this.fillGUIWithFileList();

                        // and finally, call the callback which processes the entire file list depending on the file type
                        // (for dbc, all dbc files will be read in, for plugins, the plugins will be loaded)
                        this.callFileListUpdatedCallback();
                    }
                }
            });

            headerContents.forEach((el) => {

                let td = document.createElement("td");

                switch (el) {

                    case "File Name":
                        td.innerHTML = fileElement.metadata.name;

                        break;

                    case "Size":
                        td.innerHTML = fileElement.metadata.size;

                        break;

                    case "Actions":

                        // delete action
                        // the event listener for this is hooked onto the tr element
                        td.innerHTML = "🗑️";
                        td.id = "delete-element-td";


                        // todo: implement the "details" action

                        break;

                }

                tr.appendChild(td);
            });

            this.guiTable.appendChild(tr);
        }
    }

    getFileContentsFromLocalStorage(storageLocation){
        return localStorage.getItem("importedFilesList-" + this.usage + "-" + storageLocation);
    }

    getAllFilesFromLocalStorage(){
        let fileNames = this.getFileNamesFromLocalStorage();

        let files = [];

        fileNames.forEach((element) => {
            let entry = element;
            entry.fileContent = this.getFileContentsFromLocalStorage(element.storageLocation);
            files.push(entry);
        });

        return files;
    }


    // attention, the usage cannot be the same for multiple inheritors of this class
    getFileNamesFromLocalStorage(){
        // write the defaults if there is nothing set, or it should be reset
        if(localStorage.getItem("importedFilesList-" + this.usage) === null
            || localStorage.getItem("importedFilesList-" + this.usage) === ""){
            console.log("[imported Files Manager] file list not defined! writing default value");
            localStorage.setItem("importedFilesList-" + this.usage, "" +
                '{' +
                    '"fileNames": []' +
                "}");

            // recursive call
            return this.getFileNamesFromLocalStorage();
        }

        try{
            return JSON.parse(localStorage.getItem("importedFilesList-" + this.usage)).fileNames;
        }catch(error){
            if(error instanceof SyntaxError){
                console.warn("[imported Files Manager] resetting file list JSON as it's corrupted");

                localStorage.setItem("importedFilesList" + this.usage, "");

                return this.getFileNamesFromLocalStorage;
            }
        }
    }

    addFileEntryToFileNames(entry){
        this.fileNames = this.getFileNamesFromLocalStorage();

        this.fileNames.push(entry);

        let jsonObj = {
            fileNames: this.fileNames
        }

        localStorage.setItem("importedFilesList-" + this.usage, JSON.stringify(jsonObj));
    }

    removeFileEntryFromFileNames(storageLocation){
        this.fileNames = this.getFileNamesFromLocalStorage();

        // remove the selected storage location
        this.fileNames = this.fileNames.filter((x) => {
            return x.storageLocation !== parseInt(storageLocation);
        });

        // create a json object, parse it and push it into localStorage
        let jsonObj = {
            fileNames: this.fileNames
        }
        localStorage.setItem("importedFilesList-" + this.usage, JSON.stringify(jsonObj));
    }


    callFileListUpdatedCallback(){
        if(typeof this.fileListUpdatedCallback === "function"){
            this.fileListUpdatedCallback();
        }
    }


}