import bleConnectionStates from "./ble/ble";
import gui from "./gui";


export default class connectionGUIManager extends gui{

    dotsCounter = 0;

    constructor(bmsObj, tachoObj) {
        super();

        this.bmsObj = bmsObj;
        this.tachoObj = tachoObj;

        this.inlineDisconnected();

        this.dotterBodge();

        this.addEventListeners();
    }


    addEventListeners(){

        // handler for the Nothing Connected overlay - Connect bms
        this.connectBMSOverlayButton.addEventListener('click', () => {

            // since there is no auto-connect feature now, the user should only be able to connect devices successively
            if (this.tachoObj.connectionState === bleConnectionStates.searching) {
                return;
            }

            this.bmsObj.connect();
        });

        // handler for the nothing connected overlay - Connect tacho
        this.connectTachoOverlayButton.addEventListener('click', () => {

            // since there is no auto-connect feature now,
            // the user should only be able to search for devices successively
            if (this.bmsObj.connectionState === bleConnectionStates.searching) {
                console.log("[connection gui] BMS is in searching state - no tacho connection allowed");
                return;
            }

            this.tachoObj.connect();
        });

        // handler for the overlay over the tacho tile - connect tacho
        this.connectTachoOverlay.addEventListener("click", () => {
            this.tachoObj.connect();
        });


        this.connectBMSOverlay.addEventListener("click", () => {
            this.bmsObj.connect();
        });

    }



    // needed for the auto-connect typing animation
    dotterBodge() {
        this.dotsCounter += 1;
        this.dotsCounter %= 5;
        this.autoconnectingBMSText.innerHTML = "Auto-connecting";
        this.autoconnectingTachoText.innerHTML = "Auto-connecting";
        for (let i = 0; i < this.dotsCounter; i++) {
            this.autoconnectingBMSText.innerHTML += '.';
            this.autoconnectingTachoText.innerHTML += '.';
        }
        setTimeout(()=>{
            this.dotterBodge();
        }, 200);
    }

}