import {dataMap} from "./dataDistribution";

export default class dataDistributionGUIManager{
    constructor(dataDistributionObj) {
        this.ddObj = dataDistributionObj;


        this.dataDistributionGUI = document.getElementById("data-distribution");

        this.dataDistributionMappingTable = document.getElementById("data-distribution-mapping-table");
        this.dataDistributionInputsTable = document.getElementById("data-distribution-inputs-table");
    }



    dragStart(event) {
        event.dataTransfer.setData("text/plain", event.target.id);
    }

    dragLeave(event){
        event.preventDefault();
        if(event.currentTarget.nodeName.toLowerCase() === "tr"){
            event.currentTarget.classList.remove("blend-in-background-accent");
            event.currentTarget.classList.add("blend-out-background-accent");
        }
    }

    allowDrop(event) {
        event.preventDefault();
        if(event.currentTarget.nodeName.toLowerCase() === "tr"){
            event.currentTarget.classList.add("blend-in-background-accent");
        }
    }

    drop(event) {
        event.preventDefault();
        const data = event.dataTransfer.getData("text/plain");

        // important to clone the node, otherwise the original element will be moved
        let element = document.querySelector(`#${data}`).cloneNode(true);
        element.draggable = true;

        let t = event.target;
        while(t.nodeName.toLowerCase() !== "tr"){
            t = t.parentNode;
        }

        // remove the currently connected data label
        if(t.childNodes.length > 1){
            t.removeChild(t.childNodes[1]);
        }

        // attach the dragged one
        try {
            t.appendChild(element);
        } catch (error) {
            console.warn("you can't move the item to the same place")
        }

        // reset animation
        // as there might be an animation-end event right after the drop, this 5ms delay is needed
        setTimeout(()=>{
            t.classList.remove("blend-in-background-accent");
            t.classList.add("blend-out-background-accent");
        }, 5);

        // always update the dataMap when something has changed in the table (something was dropped)
        this.readInMappingTable();
    }

    fillInputTable(){

        // clear out all tds from the table (let the ths stay there)
        this.dataDistributionInputsTable.childNodes.forEach((child) => {
            // skip empty trs
            if(child.childNodes.length === 0){
                return;
            }
            if(child.childNodes[0].nodeName.toLowerCase() === "td"){
                this.dataDistributionInputsTable.removeChild(child);
            }
        });

        let providedDataLabels = this.ddObj.providedDataLabels;

        // loop through all labels
        for (const providedLabel of providedDataLabels) {
                        // check if there is an assigned mapping already
            let mapping = this.ddObj.dataMap.getOutputs(providedLabel);
            if(mapping.length > 0){
                // skip displaying this input since it is already displayed in the mapping table
                continue;
            }

            // create a new tr object for every expected label
            let trObj = document.createElement("tr");

            let td = document.createElement("td");
            td.innerHTML = "<p>" + providedLabel + "</p>";
            td.id = providedLabel;

            td.draggable = true;
            td.ondragstart = (event) => {
                this.dragStart(event);
            };

            trObj.appendChild(td);

            this.dataDistributionInputsTable.appendChild(trObj);
        }
    }

    fillMappingTable(){
        let expectedDataLabels = this.ddObj.expectedDataLabels;

        // loop through all labels
        for (const expectedLabel of expectedDataLabels) {
            // create a new tr object for every expected label
            let trObj = document.createElement("tr");

            let leftTd = document.createElement("td");
            leftTd.innerHTML = "<p>" + expectedLabel + "</p>";

            trObj.appendChild(leftTd);

            // check if there is an assigned mapping already
            let mapping = this.ddObj.dataMap.getInput(expectedLabel);
            if(mapping.length > 0){
                let rightTd = document.createElement("td");
                rightTd.innerHTML = "<p>" + mapping[0] + "</p>";

                // enable dragging for right td for deleting it
                rightTd.draggable = true;

                trObj.appendChild(rightTd);
            }else{
                // attach empty td
                trObj.appendChild(document.createElement("td"));
            }

            // attach listeners for drag/drop actions
            trObj.ondragover = (event)=>{
                this.allowDrop(event);
            };
            trObj.ondrop = (event)=>{
                this.drop(event);
            };
            trObj.ondragleave = (event) => {
                this.dragLeave(event);
            }



            // event listener for removing the animation class once it's faded away
            trObj.addEventListener("animationend", (event)=>{
                if(event.target.classList.contains("blend-out-background-accent")){
                    event.target.classList.remove("blend-out-background-accent");
                    event.target.classList.remove("blend-in-background-accent");
                }
            });


            this.dataDistributionMappingTable.appendChild(trObj);
        }
    }

    readInMappingTable(){
        let children = this.dataDistributionMappingTable.childNodes;

        let map = new dataMap();

        for (const child of children) {
            // filter out any non tr objects
            if(child.nodeName.toLowerCase() !== "tr"){
                continue;
            }

            // filter out empty mappings
            if(child.childNodes.length === 1){
                continue;
            }
            // filter out empty mappings
            let input = child.childNodes[1];
            let output = child.childNodes[0];
            if(input.childNodes.length === 0 || output.childNodes.length === 0){
                continue;
            }

            input = input.childNodes[0].innerHTML;
            output = output.childNodes[0].innerHTML;

            map.addMapping(input, output);
        }
        // update the used data map
        this.ddObj.setDataMap(map);
    }
}