import fileObject from "../helpers/fileObject";
import {Dbc} from "candied";
import sf from "../helpers/small_functions";
import {dbcReader} from "candied/lib/filesystem/DbcWebFs";

export class dbcFile extends fileObject{

    dbc;
    dbcJSObj;

    name;
    size;

    constructor(){
        super("dbc");
    }


    // return true on successful reading in and false on failure
    async openNewDBCFile(){
        this.dbcJSObj = null;


        try{
            let file = await this.readFile();

            this.dbc = new Dbc();

            // read the dbc file into the can-died object
            // use a promise to get the timeout functionality
            this.readInDBC(file).then((fileContent)=>{
                this.dbcJSObj = this.dbc.load(fileContent);
                console.log(this.dbcJSObj);
            }).catch((error)=>{
                if(error === "timeout"){
                    console.warn("[dbcGUIManager] dbc reading timed out!");
                }else{
                    console.warn("[dbcGUIManager] dbc reading error:" + error);
                }

                // return false for the success variable in any way
                return false;
            });

            await sf.until(()=>{return (this.dbcJSObj !== null && (typeof this.dbcJSObj !== "undefined"))})

        }catch(error){
            console.warn("[dbcGUIManager] error:" + error);
            return false;
        }

        return true;
    }


    // return true on successful reading in and false on failure
    async openNewDBC(fileContent){
        this.dbcJSObj = null;


        try{
            this.dbc = new Dbc();

            // read the dbc file into the can-died object
            // use a promise to get the timeout functionality
            this.dbcJSObj = this.dbc.load(fileContent);

            await sf.until(()=>{return (this.dbcJSObj !== null && (typeof this.dbcJSObj !== "undefined"))})

        }catch(error){
            console.warn("[dbcGUIManager] error:" + error);
            return false;
        }

        return true;
    }


    readInDBC(file, timeout=1000) {
        return new Promise((resolve, reject) => {
            setTimeout(() => {
                reject('timeout');
            }, timeout);

            try{
                dbcReader(file, resolve);
            }catch(error){
                reject(error);
            }
        });
    }


}