import sf from "./helpers/small_functions";
import {zoom} from "./helpers/zoom";
import gui from "./gui";


export default class tilebar extends gui{
    constructor(
        bmsObj,
        tachoObj,
        captureModeManager,
        dataLoggingManager,
        colorModeObj,
        interfaceConfigObj,
        rearrangeManager,
        otaManager,
        dataDistributionGUIMgr,
        dbcGUIManager
    ) {
        super();

        this.bmsObj = bmsObj;
        this.tachoObj = tachoObj;
        this.captureModeMgr = captureModeManager;
        this.dataLoggingMgr = dataLoggingManager;
        this.colorModeObj = colorModeObj;
        this.rearrangeManager = rearrangeManager;
        this.interfaceConfigObj = interfaceConfigObj;
        this.otaManager = otaManager;
        this.dataDistributionGUIMgr = dataDistributionGUIMgr;
        this.dbcGUIManager = dbcGUIManager;


        // Chrome detection to warn the user as BLE probably isn't available

        if(!sf.isChrome()){
            this.table.classList.add("visibility-hidden");
            this.nothingConnectedOverlay.classList.add("visibility-hidden");
            this.tilebarDOM.classList.add("disabled");
            alert("This site is tailored around Progressive Wep App services that are only supported in full by Chrome.\nPlease open this site in Chrome only as we can't ensure functionality otherwise ;)");
        }
    }

    addEventListeners(){
        // BLUETOOTH
        document.getElementById("tileBarConnectBMS").addEventListener("click", ()=>{
            this.bmsObj.connect();
        });

        document.getElementById("tileBarConnectTacho").addEventListener("click", ()=>{
            this.tachoObj.connect();
        });

        document.getElementById("tileBarDisconnect").addEventListener("click", async ()=>{
            this.resetAutoconnectBMSSilentInstant();
            this.resetAutoconnectTachoSilentInstant();

            // disable reconnect
            this.bmsObj.reconnect = false;
            this.tachoObj.reconnect = false;

            await this.bmsObj.disconnect();
            await this.tachoObj.disconnect();
        });




        // TILEBAR DBC
/*
        document.getElementById("tileBarDBC").addEventListener("click", async ()=>{
            await this.dbcGUIManager.dbcMgr.addDBC();

            console.log(this.dbcGUIManager.dbcMgr.getMessages());
        });
*/

        document.getElementById("tileBarFileManager").addEventListener("click", async ()=>{
            if(this.dbcFileManagerContainer.classList.contains("display-none")){
                this.dbcFileManagerContainer.classList.remove("display-none");



                // zoom out if not already
                // and only move to settings afterward (otherwise the magnification screws up the destination)
                if(sf.zoomedIn()){
                    zoom.out({
                        callback: ()=>{
                            sf.scrollToElement(this.dbcFileManagerContainer);
                        }
                    });
                }else{
                    sf.scrollToElement(this.dbcFileManagerContainer);
                }
            }else{
                sf.scrollToElement(this.table);
            }
        });


        // BOARD

        document.getElementById("tileBarDeviceInfo").addEventListener("click", ()=>{
            this.captureModeMgr.closeCaptureMode();
            this.hideAllSettings();

            if(this.boardInfoContainer.classList.contains("display-none")){
                if(this.bmsObj.isConnected() || this.tachoObj.isConnected()){
                    this.boardInfoContainer.classList.remove("display-none");

                    // initiate another update for the available OTA images (because franz wished so)
                    this.otaManager.getAvailableOTAImages();

                    // zoom out if not already
                    // and only move to settings afterward (otherwise the magnification screws up the destination)
                    if(sf.zoomedIn()){
                        zoom.out({
                            callback: ()=>{
                                sf.scrollToElement(this.boardInfoContainer);
                            }
                        });
                    }else{
                        sf.scrollToElement(this.boardInfoContainer);
                    }
                }
            }else{
                sf.scrollToElement(this.table);
            }
        });


        document.getElementById("tileBarBoardConfig").addEventListener("click", () => {

            this.captureModeMgr.closeCaptureMode();

            this.hideAllSettings();


            if(this.boardConfigContainer.classList.contains("display-none")){
                if(this.bmsObj.isConnected()){
                    // show settings container
                    this.boardConfigContainer.classList.remove("display-none");

                    // zoom out if not already
                    // and only move to settings afterward (otherwise the magnification screws up the destination)
                    if(sf.zoomedIn()){
                        zoom.out({
                            callback: ()=>{
                                sf.scrollToElement(this.boardConfigContainer);
                            }
                        });
                    }else{
                        sf.scrollToElement(this.boardConfigContainer);
                    }
                }
            }else{
                sf.scrollToElement(this.table);
            }

            // normally there would be an "else: add hidden to the class list"
            // here, there is not, because there is a separate "hide settings" option
        });
        document.getElementById("tileBarBoardCalib").addEventListener("click", () => {

            this.captureModeMgr.closeCaptureMode();

            this.hideAllSettings();

            if(this.boardCalibContainer.classList.contains("display-none")){
                if(this.bmsObj.isConnected()){

                    // zoom out if not already
                    // and only move to settings afterward (otherwise the magnification screws up the destination)
                    if(sf.zoomedIn()){
                        zoom.out({
                            callback: ()=>{
                                this.boardCalibContainer.classList.remove("display-none");

                                sf.scrollToElement(this.boardCalibContainer);
                            }
                        });
                    }else{
                        this.boardCalibContainer.classList.remove("display-none");
                        sf.scrollToElement(this.boardCalibContainer);
                    }
                }
            }else{
                sf.scrollToElement(this.table);
            }
        });
        document.getElementById("tileBarTachoConfig").addEventListener("click", () => {

            this.captureModeMgr.closeCaptureMode();

            this.hideAllSettings();

            if(this.inlineConfigContainer.classList.contains("display-none")){
                if(this.tachoObj.isConnected()){
                    // show settings container
                    this.inlineConfigContainer.classList.remove("display-none");

                    // zoom out if not already
                    // and only move to settings afterward (otherwise the magnification screws up the destination)
                    if(sf.zoomedIn()){
                        zoom.out({
                            callback: ()=>{
                                sf.scrollToElement(this.inlineConfigContainer);
                            }
                        });
                    }else{
                        sf.scrollToElement(this.inlineConfigContainer);
                    }
                }
            }else{
                sf.scrollToElement(this.table);
            }
        });


        // DATA DISTRIBUTION

        document.getElementById("tileBarDataDistribution").addEventListener("click", () => {

            this.captureModeMgr.closeCaptureMode();

            this.hideAllSettings();

            if(this.dataDistributionGUIMgr.dataDistributionGUI.classList.contains("display-none")){

                // remove nothingConnectedOverlay
                // as per Franz's wish, it should enable users to go to the tiles without overlay
                this.disableNothingConnectedOverlay();

                // zoom out if not already
                // and only move to settings afterward
                // (otherwise the magnification screws up the destination)
                if(sf.zoomedIn()){
                    zoom.out({
                        callback: ()=>{
                            // not the nicest solution, but it works
                            this.dataDistributionGUIMgr.dataDistributionGUI.classList.remove("display-none");

                            this.dataDistributionGUIMgr.fillMappingTable();
                            this.dataDistributionGUIMgr.fillInputTable();
                            this.dataDistributionGUIMgr.dataDistributionGUI.scrollIntoView({ behavior: "smooth"});
                        }
                    });
                }else{
                    // I suppose commenting this out works, because it is redundant with the scroll listener
                    // that listener is contained in virtual keyboard
                    // not the nicest solution, but it works

                    this.dataDistributionGUIMgr.dataDistributionGUI.classList.remove("display-none");
                    sf.scrollToElement(this.dataDistributionGUIMgr.dataDistributionGUI);
                }
            }else{
                // reverse all changes when clicking on settings again
                if(!this.bmsObj.isConnected()
                    && !this.tachoObj.isConnected()){
                    this.enableNothingConnectedOverlay();
                }

                this.table.scrollIntoView({ behavior: "smooth"});
                this.dataDistributionGUIMgr.dataDistributionGUI.classList.add("display-none");
            }
        });




        // LOGGING

        document.getElementById("tileBarSelectDataLoggingFile").addEventListener("click", () => {
            // disables button if either no devices are connected or data logging is already running
            if ((this.bmsObj.isConnected() && this.tachoObj.isConnected())
                || this.dataLoggingMgr.dataLoggingEnabled) {
                return;
            }
            this.dataLoggingMgr.newFile().then();
        });


        document.getElementById("tileBarStartDataLogging").addEventListener("click", () => {
            // disables button if no devices are connected
            if (!this.bmsObj.isConnected() && !this.tachoObj.isConnected()) {
                return;
            }

            console.log(this.dataLoggingMgr.loggingFileObj)

            // if there is no logging data file object, get one
            if(typeof this.dataLoggingMgr.loggingFileObj === "undefined" || this.dataLoggingMgr.loggingFileObj === null){
                this.dataLoggingMgr.newFile().then(()=>{

                    this.dataLoggingMgr.dataLoggingEnabled = true;

                    document.getElementById("logging-span").classList.add("recording");
                });
            }else{
                this.dataLoggingMgr.dataLoggingEnabled = true;

                document.getElementById("logging-span").classList.add("recording");
            }
        });

        document.getElementById("tileBarStopDataLogging").addEventListener("click", ()=>{

            // don't do anything if already closed
            if(!this.dataLoggingMgr.dataLoggingEnabled){
                return;
            }

            document.getElementById("logging-span").classList.remove("recording");

            this.dataLoggingMgr.closeFile();
        });



        // ABOUT (INTERFACE SETTINGS)

        // toggle the about-window
        document.getElementById("tileBar-about").addEventListener("click", ()=>{

            this.captureModeMgr.closeCaptureMode();

            this.hideAllSettings();

            if(this.interfaceSettingsContainer.classList.contains("display-none")){

                // remove nothingConnectedOverlay
                // as per Franz's wish, it should enable users to go to the tiles without overlay
                this.disableNothingConnectedOverlay();

                // zoom out if not already
                // and only move to settings afterward (otherwise the magnification screws up the destination)
                if(sf.zoomedIn()){
                    zoom.out({
                        callback: ()=>{
                            // not the nicest solution, but it works
                            //interfaceSettingsContainerLastSeen = Date.now();

                            this.interfaceSettingsContainer.classList.remove("display-none");
                            this.interfaceSettingsContainer.scrollIntoView({ behavior: "smooth"});
                        }
                    });
                }else{
                    // I suppose commenting this out works, because it is redundant with the scroll listener
                    // that listener is contained in virtual keyboard
                    // not the nicest solution, but it works
                    //interfaceSettingsContainerLastSeen = Date.now();

                    this.interfaceSettingsContainer.classList.remove("display-none");
                    sf.scrollToElement(this.interfaceSettingsContainer);
                    //this.interfaceSettingsContainer.scrollIntoView({ behavior: "smooth"});
                }
            }else{
                // reverse all changes when clicking on settings again
                if(!this.bmsObj.isConnected()
                    && !this.tachoObj.isConnected()){
                    this.enableNothingConnectedOverlay();
                }

                this.table.scrollIntoView({ behavior: "smooth"});
                this.interfaceSettingsContainer.classList.add("display-none");
            }
        });




        // DARKMODE

        document.getElementById("tileBar-colormode").addEventListener("click", ()=>{
            this.colorModeObj.toggle();
        });



        this.tileBarEditMode.addEventListener("click", ()=>{
            this.rearrangeManager.toggleTileMoveMode();
        });



        // only show "capture mode" if the device supports and has a camera
        if ('mediaDevices' in navigator && 'getUserMedia' in navigator.mediaDevices) {
            this.tileBarCaptureMode.addEventListener("click", ()=>{

                this.hideAllSettings();

                if(this.captureModeContainer.classList.contains("display-none")){

                    if(sf.zoomedIn()){
                        zoom.out({
                            callback: ()=>{
                                this.captureModeMgr.activateCaptureMode();

                                // scrollIntoView is used here because the offset of the tilebar is unwanted!
                                this.captureModeContainer.scrollIntoView({ behavior: "smooth"});
                            }
                        });
                    }else{
                        this.captureModeMgr.activateCaptureMode();

                        // scrollIntoView is used here because the offset of the tilebar is unwanted!
                        this.captureModeContainer.scrollIntoView({ behavior: "smooth"});
                    }
                }else{
                    this.captureModeMgr.closeCaptureMode();
                    this.table.scrollIntoView({behavior: "smooth"});
                }
            });
        }else{
            this.tileBarCaptureMode.classList.add("display-none");
        }

        document.getElementById("tilebar-interface-mode").addEventListener("click", ()=>{
            let entered_pw = window.prompt("Password","");

            let prev_version = "";
            let prev_version_shortened = "";

            try{
                prev_version = document.getElementsByClassName("version-number-field")[0].innerHTML;
                prev_version_shortened = prev_version.split("-")[0];
            }catch(e){
                console.log("[serviceworker] no version found, serviceworker not installed!");
            }


            // DEV OPTIONS
            if(prev_version.includes("dev")){
                return;
            }
            if(entered_pw === "devpassword"){
                sf.setValueTexts(this.interfaceVersionFields, prev_version_shortened + "-dev");

                this.interfaceConfigObj.updateInterfaceMode("dev");
            }


            // EXPERIMENTAL OPTIONS
            if(prev_version.includes("exp")){
                return;
            }
            if(entered_pw === "exppassword"){
                sf.setValueTexts(this.interfaceVersionFields, prev_version_shortened + "-exp");

                this.interfaceConfigObj.updateInterfaceMode("exp");
            }

        });


    }





    scrollToBoardInfo(){
        this.hideAllSettings();

        this.boardInfoContainer.classList.remove("display-none");

        // should be zoomed out since this is only called after firmware update reset,
        // but double-checking doesn't hurt xD

        // zoom out if not already
        // and only move to settings afterward (otherwise the magnification screws up the destination)
        if(sf.zoomedIn()){
            zoom.out({
                callback: ()=>{
                    sf.scrollToElement(this.boardInfoContainer);
                }
            });
        }else{
            sf.scrollToElement(this.boardInfoContainer);
        }
    }

}