import gui_elements from "../gui_elements";

import sf from "../helpers/small_functions";


export default class tachoGatherer extends gui_elements{
    inlineHertzSampleBuffer = [];
    lastInlineLoggingDataTimeStamp;

    providedDataLabels = [
        "speed",
        "rpm",
        "motorTemp",
        "integratedTemp",
        "tripOdometer",
        "vehicleOdometer"
    ]


    constructor(tachoConfigObj) {
        super();

        this.tachoConfigObj = tachoConfigObj;
    }

    initDataDistribution(dd){
        this.dd = dd;
        this.dd.addProvidedDataLabels(this.providedDataLabels);
    }

    gatherData(event){

        if(!this.tachoConfigObj.initialised){
            console.log("waiting for config to be read...");
            return;
        }

        let data = event.target.value;

        if(data.byteLength === 0) {
            console.log("Something wrong with incoming BLE Data!");
            return;
        }

        /*
        let inlineLoggingDataInterval = Date.now() - lastInlineLoggingDataTimeStamp;
        if(inlineLoggingDataInterval < 25){
            console.log("dropped tacho");
            return;
        }
        */
        this.lastInlineLoggingDataTimeStamp = Date.now();

        this.inlineHertzSampleBuffer.push([
            this.lastInlineLoggingDataTimeStamp
        ]);
        this.inlineHertzSampleBuffer.forEach((element) => {
            if(element[0] + 1000 < Date.now()){
                const index = this.inlineHertzSampleBuffer.indexOf(element);
                if (index > -1) { // only splice array when item is found
                    this.inlineHertzSampleBuffer.splice(index, 1); // 2nd parameter means remove one item only
                }
            }
        });

        sf.setValueTexts(this.tachoDataLoggingFrequencyValues, "T: " +  this.inlineHertzSampleBuffer.length + "Hz");



        let dataPacket = {
            time: this.lastInlineLoggingDataTimeStamp,
            speed: -sf.handleSignedBullshit(((data.getUint8(2) << 8) | data.getUint8(1))) * 0.036,
            rpm: ((data.getUint8(4) << 8) | data.getUint8(3)),
            motorTemp: sf.cap((-sf.handleSignedBullshit(((data.getUint8(6) << 8) | data.getUint8(5))) / 10), -50, 200),
            integratedTemp: sf.cap((-sf.handleSignedBullshit(((data.getUint8(8) << 8) | data.getUint8(7))) / 10), -50, 200),
            tripOdo: sf.handleUnsignedBullshit32(((data.getUint8(12) << 24) | (data.getUint8(11) << 16) | (data.getUint8(10) << 8) | data.getUint8(9))),
            vehicleOdo: sf.handleUnsignedBullshit32(((data.getUint8(16) << 24) | (data.getUint8(15) << 16) | (data.getUint8(14) << 8) | data.getUint8(13))),
            calibPulseCount: -sf.handleSignedBullshit(((data.getUint8(18) << 8) | data.getUint8(17)))
        };


        // update data in the data distribution
        this.dd.updateProvidedData({
            speed: dataPacket.speed,
            rpm: dataPacket.rpm,

            motorTemp: dataPacket.motorTemp,
            integratedTemp: dataPacket.integratedTemp,

            tripOdometer: dataPacket.tripOdo,
            vehicleOdometer: dataPacket.vehicleOdo
        });

        return dataPacket;
    }
}