export default class dataDistribution{

    providedDataLabels = [];
    expectedDataLabels = [];

    providedDataObj = {};
    expectedDataObj = {};

    dataMap = new dataMap();

    expecterUpdateFunctions = [];

    constructor() {
    }

    setDataMap(dataMap){
        this.dataMap = dataMap;
    }


    /*
    CONCEPT:

    saving:
    data distribution gets saved for the combination of devices and will automatically be set to that

    like with the calib auto-backups, there will be a dropdown for choosing between the different saved options
    it should also be downloadable

    gui:
    pairs on the left can be dragged and dropped anywhere on the screen, and the input will be deleted
    implement where the data comes from to group it
    implement groups for the outputs as well
     */


    getMapJson(){
        return JSON.stringify(this.dataMap);
    }

    readInMapJson(j){
        try{
            this.setDataMap(JSON.parse(j));
        }catch(error){
            console.warn("[dataDistribution] error reading in JSON: "+ error);
        }
    }

    addMapping(input, output){
        if(this.providedDataLabels.includes(input) && this.expectedDataLabels.includes(output)){
            this.dataMap.addMapping(input,output);
        }else{
            console.log("[dataDistribution] failed to add mapping: input or output not contained in label lists");
        }
    }

    updateProvidedData(data){
        for (const key of Object.keys(data)) {
            if(this.providedDataLabels.includes(key)){
                this.providedDataObj[key] = data[key];
            }
        }
    }

    executeMapping(){
        for (const inputKey of Object.keys(this.providedDataObj)) {
            let outputKeys = this.dataMap.getOutputs(inputKey);

            for (const outputKey of outputKeys) {
                if(this.expectedDataLabels.includes(outputKey)){
                    this.expectedDataObj[outputKey] = this.providedDataObj[inputKey];
                }else{
                    console.log("[dataDistribution] failed to execute mapping: mapping output not contained in expected data label list");
                }
            }
        }
    }

    updateDataExpecters(){
        for (const expecterUpdateFunction of this.expecterUpdateFunctions) {
            expecterUpdateFunction(this.expectedDataObj);
        }
    }

    registerExpecterUpdate(updateFunction){
        this.expecterUpdateFunctions.push(updateFunction);
    }


    addProvidedDataLabels(dataLabels){
        this.providedDataLabels = this.providedDataLabels.concat(dataLabels);
    }

    addExpectedDataLabels(dataLabels){
        this.expectedDataLabels = this.expectedDataLabels.concat(dataLabels);
    }
}



export class dataMap{

    mapObj = [];

    constructor(){

    }

    addMapping(input, output){
        let mappingObj = {
            input: input,
            output: output
        };

        this.mapObj.push(mappingObj);
    }

    removeMapping(input, output){

        // filter function returns true if an element should stay in an array, false if it should be removed
        this.mapObj = this.mapObj.filter(function (element) {
            return (element.input === input && element.output === output);
        });
    }

    getMap(){
        return this.mapObj;
    }

    getOutputs(input){
        let outputObj = this.mapObj.filter(function (element) {
            return (element.input === input);
        }).map(x => x.output);

        if(outputObj.length > 1){
            // maybe for debugging but right now this is okay since values are pfusched onto vehicle model
            //console.log("[dataMap] more than one output for input!");
        }

        return outputObj;
    }

    getInput(output){
        let inputObj = this.mapObj.filter(function (element) {
            return (element.output === output);
        }).map(x => x.input);

        if(inputObj.length > 1){
            console.log("[dataMap] more than one input for output!");
        }

        return inputObj;
    }
}