import sf from "../helpers/small_functions";
import gui from "../gui";
import {CharacteristicUnavailableError} from "../ble/ble";

// fucking finally,
// let's start with class-based stuff


export default class bmsConfig extends gui{
    battCellCount;
    battNomCapacity;
    protMaxCellVoltage;
    protMinCellVoltage;
    protMaxCurrent;
    protMaxImbalanceCurrent;

    protMaxLogicTemp;
    protMaxPowerTemp;

    prechargeCurrentLimit;
    prechargeNomCapacity;

    dataloggingUpdateInterval;

    boardAutoStart;
    boardPowerOffTime;

    boardEnabledChannels;
    boardUpdateCount;
    protMaxReverseCurrent;

    cellChemistry;
    protSpikeSensitivity;


    // these values are not part of the actual config, they are just calculated from the actual data
    packOverVoltage;
    packUnderVoltage;


    bmsObj;

    #bmsConfigObjBefore;

    constructor(bmsObj, recursive = false){
        super();
        this.initialised = false;

        this.bmsObj = bmsObj;

        if(recursive){
            this.#bmsConfigObjBefore = new bmsConfig(bmsObj);
        }
    }

    addEventListeners(){
        // gui element updates
        [].forEach.call(document.getElementsByClassName("cell-field"), (element) => {
            element.addEventListener("input", ()=>{
                this.#calculatePackVoltages();
            });
        });
        sf.getId("battery-cells").addEventListener("input", ()=>{
            this.#calculatePackVoltages();
        });

        this.boardConfigBatterySelector.addEventListener("change", ()=>{
            this.#blurAppropriateVoltageFields();
            this.#setStandardBatteryValues();
        });

        this.boardConfigTable.addEventListener("mousemove", () => {
            this.#checkBMSConfigPlausibility();
        });
        this.boardConfigTable.addEventListener("change", () => {
            this.#checkBMSConfigPlausibility();
        });


        sf.getId("board-config-read").addEventListener("click", () => {
            this.readBMSConfig((success, change)=>{
                if(success){
                    if(change){
                        // indicate success on change
                        this.indicateSuccess(500);
                    }else{
                        // warning on "read successful but nothing changed"
                        this.indicateWarning(500);
                    }
                }else{
                    this.indicateFailure();
                }
            });
        });



        sf.getId("board-config-write").addEventListener("click", () => {
            this.#bmsConfigObjBefore.readInGUIValues();
            this.writeBoardConfig(0);
        });

    }

    #getCellChemistry(){
        const val = this.boardConfigBatterySelector.options[this.boardConfigBatterySelector.selectedIndex].value;

        if(val === "li-ion")    return 1;
        if(val === "li-po")     return 2;
        if(val === "li-fe-po")  return 3;
        if(val === "custom-li") return 4;
        if(val === "custom")    return 0;
    }
    #setCellChemistry(value){
        if(value === 0) this.boardConfigBatterySelector.value = "custom";
        if(value === 1) this.boardConfigBatterySelector.value = "li-ion";
        if(value === 2) this.boardConfigBatterySelector.value = "li-po";
        if(value === 3) this.boardConfigBatterySelector.value = "li-fe-po";
        if(value === 4) this.boardConfigBatterySelector.value = "custom-li";
    }

    #getCurrentSpikeSensitivity(){
        let val = sf.getId("spike-sensitivity-select").value;
        if(val === "0") return 0; // low
        if(val === "1") return 1; // normal
        if(val === "2") return 2; // high

        // default is normal
        return 1;
    }
    #setCurrentSpikeSensitivity(value){
        if(value === 0) sf.getId("spike-sensitivity-select").value = "0";
        if(value === 1) sf.getId("spike-sensitivity-select").value = "1";
        if(value === 2) sf.getId("spike-sensitivity-select").value = "2";
    }


    #getEnabledChannelsByte() {
        let byte = 0;
        byte += sf.getIdChecked("enabled-channels-1") * 1;
        byte += sf.getIdChecked("enabled-channels-2") * 2;
        byte += sf.getIdChecked("enabled-channels-3") * 4;
        return byte;
    }

    #setEnabledChannelsByte(byte) {
        sf.getId("enabled-channels-1").checked = byte & 0b00000001;
        sf.getId("enabled-channels-2").checked = byte & 0b00000010;
        sf.getId("enabled-channels-3").checked = byte & 0b00000100;
    }


    indicateSuccess(duration){
        this.boardConfigTable.classList.add("success");
        setTimeout(() => {
            this.boardConfigTable.classList.remove("success");
        }, duration);
    }

    indicateWarning(duration){
        this.boardConfigTable.classList.add("warning");
        setTimeout(() => {
            this.boardConfigTable.classList.remove("warning");
        }, duration);
    }

    indicateFailure(){
        this.boardConfigTable.classList.add("fault");
        setTimeout(() => {
            this.boardConfigTable.classList.remove("fault");
        }, 2000);
    }


    readInGUIValues(){
        this.initialised = true;

        // guaranteed fields
        this.battCellCount = sf.getIdValue("battery-cells");
        this.battNomCapacity = sf.getIdValue("battery-capacity");

        if(!this.fullyCustomChemistryOption.disabled){
            this.cellChemistry = this.#getCellChemistry();
        }

        // if chemistry is set to completely custom
        if(this.#getCellChemistry() === 0){
            this.protMaxCellVoltage = sf.getIdValue("pack-overvoltage") * 100;
            this.protMinCellVoltage = sf.getIdValue("pack-undervoltage") * 100;
        }else{
            this.protMaxCellVoltage = sf.getIdValue("max-cell-voltage");
            this.protMinCellVoltage = sf.getIdValue("min-cell-voltage");
        }

        this.protMaxImbalanceCurrent = sf.getIdValue("strand-max-imbalance-current") * 100;
        this.protMaxCurrent = sf.getIdValue("strand-max-current") * 100;
        this.protMaxReverseCurrent = sf.getIdValue("strand-max-reverse-current") * 100;

        this.protMaxPowerTemp = sf.getIdValue("power-max-temp");
        this.protMaxLogicTemp = sf.getIdValue("logic-max-temp");

        this.prechargeCurrentLimit = 0;//getIdValue("precharge-current-limit");
        this.prechargeNomCapacity = sf.getIdValue("load-capacitance");

        this.dataloggingUpdateInterval = 1000/sf.getIdValue("datalogging-update-frequency");

        this.boardAutoStart = sf.getIdChecked("autostart");
        this.boardPowerOffTime = sf.getIdValue("bms-config-auto-power-off");
        this.boardEnabledChannels = this.#getEnabledChannelsByte();
        this.boardUpdateCount = sf.getId("updatecount").innerHTML;

        // fields that might not be there dependent on the firmware version

        if(!this.boardConfigSpikeSensitivitySelector.disabled){
            this.protSpikeSensitivity = this.#getCurrentSpikeSensitivity();
        }
    }


    readInBuffer(buffer){
        let bufferBefore = this.getBuffer();

        // ATTENTION: PARENTHESES FOR BIT SHIFTING ARE VERY IMPORTANT

        // Battery stuff
        this.battCellCount = buffer.getUint8(0);
        this.battNomCapacity = ((buffer.getUint8(2) << 8) | buffer.getUint8(1)) * 100;
        //Protection stuff
        this.protMaxCellVoltage = (buffer.getUint8(4) << 8) | buffer.getUint8(3);
        this.protMinCellVoltage = (buffer.getUint8(6) << 8) | buffer.getUint8(5);
        this.protMaxCurrent = (buffer.getUint8(8) << 8) | buffer.getUint8(7);
        this.protMaxImbalanceCurrent = (buffer.getUint8(10) << 8) | buffer.getUint8(9);
        // Temperatures
        this.protMaxLogicTemp = buffer.getUint8(11);
        this.protMaxPowerTemp = buffer.getUint8(12);
        // Precharge stuff
        this.prechargeCurrentLimit = (buffer.getUint8(14) << 8) | buffer.getUint8(13);
        this.prechargeNomCapacity = (buffer.getUint8(16) << 8) | buffer.getUint8(15);
        // Data logging stuff
        this.dataloggingUpdateInterval = (buffer.getUint8(18) << 8) | buffer.getUint8(17);
        // Board stuff
        this.boardAutoStart = buffer.getUint8(19);
        this.boardPowerOffTime = (buffer.getUint8(21) << 8) | buffer.getUint8(20);
        this.boardEnabledChannels = buffer.getUint8(22);
        this.boardUpdateCount = (buffer.getUint8(24) << 8) | buffer.getUint8(23);
        this.protMaxReverseCurrent = (buffer.getUint8(26) << 8) | buffer.getUint8(25);

        // necessary for using old firmware
        try {
            this.protSpikeSensitivity = buffer.getUint8(27);
        } catch (err) {
            if (err.constructor === RangeError) {
                console.log("[bms config] fw doesn't support proSpikeSensitivity");
            }
        }
        try {
            this.cellChemistry = buffer.getUint8(28);
        } catch (err) {
            if (err.constructor === RangeError) {
                console.log("[bms config] fw doesn't support cellChemistry");
            }
        }


        this.initialised = true;

        // return if change has occurred
        // remember, that the gui values need to be read in first for a change to even occur
        return !sf.arraysEqual(bufferBefore, this.getBuffer());
    }

    #readInConfigObject(c){
        this.battCellCount = c.battCellCount;
        this.battNomCapacity = c.battNomCapacity;
        this.protMaxCellVoltage = c.protMaxCellVoltage;
        this.protMinCellVoltage = c.protMinCellVoltage;
        this.protMaxCurrent = c.protMaxCurrent;
        this.protMaxImbalanceCurrent = c.protMaxImbalanceCurrent;
        this.protMaxLogicTemp = c.protMaxLogicTemp;
        this.protMaxPowerTemp = c.protMaxPowerTemp;
        this.prechargeCurrentLimit = c.prechargeCurrentLimit;
        this.prechargeNomCapacity = c.prechargeNomCapacity;
        this.dataloggingUpdateInterval = c.dataloggingUpdateInterval;
        this.boardAutoStart = c.boardAutoStart;
        this.boardPowerOffTime = c.boardPowerOffTime;
        this.boardEnabledChannels = c.boardEnabledChannels;
        this.boardUpdateCount = c.boardUpdateCount;
        this.protMaxReverseCurrent = c.protMaxReverseCurrent;

        // necessary for using old firmware
        try {
            this.protSpikeSensitivity = c.protSpikeSensitivity;
        } catch (err) {
            if (err.constructor === RangeError) {
                console.log("[bms config] fw doesn't support proSpikeSensitivity");
            }
        }
        try {
            this.cellChemistry = c.cellChemistry;
        } catch (err) {
            if (err.constructor === RangeError) {
                console.log("[bms config] fw doesn't support cellChemistry");
            }
        }

    }

    #getConfigObject(){
        let conf = {};

        conf.battCellCount = this.battCellCount;
        conf.battNomCapacity = this.battNomCapacity;
        conf.protMaxCellVoltage = this.protMaxCellVoltage;
        conf.protMinCellVoltage = this.protMinCellVoltage;
        conf.protMaxCurrent = this.protMaxCurrent;
        conf.protMaxImbalanceCurrent = this.protMaxImbalanceCurrent;
        conf.protMaxLogicTemp = this.protMaxLogicTemp;
        conf.protMaxPowerTemp = this.protMaxPowerTemp;
        conf.prechargeCurrentLimit = this.prechargeCurrentLimit;
        conf.prechargeNomCapacity = this.prechargeNomCapacity;
        conf.dataloggingUpdateInterval = this.dataloggingUpdateInterval;
        conf.boardAutoStart = this.boardAutoStart;
        conf.boardPowerOffTime = this.boardPowerOffTime;
        conf.boardEnabledChannels = this.boardEnabledChannels;
        conf.boardUpdateCount = this.boardUpdateCount;
        conf.protMaxReverseCurrent = this.protMaxReverseCurrent;

        // necessary for using old firmware
        try {
            conf.protSpikeSensitivity = this.protSpikeSensitivity;
        } catch (err) {
            if (err.constructor === RangeError) {
                console.log("[bms config] fw doesn't support proSpikeSensitivity");
            }
        }
        try {
            conf.cellChemistry = this.cellChemistry;
        } catch (err) {
            if (err.constructor === RangeError) {
                console.log("[bms config] fw doesn't support cellChemistry");
            }
        }

        return conf;
    }

    readInJSON(json){
        let readInConfigObject;
        try{
            readInConfigObject =  JSON.parse(json);
        }catch(e){
            console.log("[config] unable to read in JSON: " + e);
        }

        if(typeof readInConfigObject === "undefined"){
            return;
        }

        this.#readInConfigObject(readInConfigObject);
    }

    getJSON(){
        if(!this.initialised){
            return;
        }

        return JSON.stringify(this.#getConfigObject());
    }

    getBuffer(){
        if(!this.initialised){
            return;
        }

        let configValues = [];

        //Battery stuff
        configValues[0] = parseInt(this.battCellCount);

        configValues[1] = sf.to16bit(Math.round(this.battNomCapacity / 100))[0];
        configValues[2] = sf.to16bit(this.battNomCapacity / 100)[1];

        //Protection
        configValues[3] = sf.to16bit(this.protMaxCellVoltage)[0];
        configValues[4] = sf.to16bit(this.protMaxCellVoltage)[1];

        configValues[5] = sf.to16bit(this.protMinCellVoltage)[0];
        configValues[6] = sf.to16bit(this.protMinCellVoltage)[1];

        configValues[7] = sf.to16bit(this.protMaxCurrent)[0];
        configValues[8] = sf.to16bit(this.protMaxCurrent)[1];

        configValues[9] = sf.to16bit(this.protMaxImbalanceCurrent)[0];
        configValues[10] = sf.to16bit(this.protMaxImbalanceCurrent)[1];

        configValues[11] = parseInt(this.protMaxLogicTemp);

        configValues[12] = parseInt(this.protMaxPowerTemp);

        //Precharge
        configValues[13] = sf.to16bit(this.prechargeCurrentLimit)[0];
        configValues[14] = sf.to16bit(this.prechargeCurrentLimit)[1];

        configValues[15] = sf.to16bit(this.prechargeNomCapacity)[0];
        configValues[16] = sf.to16bit(this.prechargeNomCapacity)[1];

        //Data logging
        configValues[17] = sf.to16bit(this.dataloggingUpdateInterval)[0];
        configValues[18] = sf.to16bit(this.dataloggingUpdateInterval)[1];

        //Board
        configValues[19] = ((this.boardAutoStart) ? 1 : 0);

        configValues[20] = sf.to16bit(this.boardPowerOffTime)[0];
        configValues[21] = sf.to16bit(this.boardPowerOffTime)[1];

        configValues[22] = parseInt(this.boardEnabledChannels);

        configValues[23] = sf.to16bit(this.boardUpdateCount)[0];
        configValues[24] = sf.to16bit(this.boardUpdateCount)[1];

        configValues[25] = sf.to16bit(this.protMaxReverseCurrent)[0];
        configValues[26] = sf.to16bit(this.protMaxReverseCurrent)[1];

        try {
            if(typeof this.protSpikeSensitivity === "undefined" || isNaN(this.protSpikeSensitivity)){
                console.log("firmware doesn't support protSpikeSensitivity");
            }else{
                configValues[27] = parseInt(this.protSpikeSensitivity);
            }
        }catch (e) {
            console.log("firmware doesn't support protSpikeSensitivity");
        }

        try {
            if(typeof this.cellChemistry === "undefined" || isNaN(this.cellChemistry)){
                console.log("firmware doesn't support cellChemistry");
            }else{
                configValues[28] = parseInt(this.cellChemistry);
            }
        }catch (e) {
            console.log("[bms config] firmware doesn't support cellChemistry");
        }

        return configValues;
    }

    setGUIValues(){
        if(!this.initialised){
            return;
        }

        sf.getId("battery-cells").value = this.battCellCount;
        sf.getId("battery-capacity").value = this.battNomCapacity;


        if(typeof this.cellChemistry !== 'undefined') {
            this.#setCellChemistry(this.cellChemistry);
            this.#blurAppropriateVoltageFields();

            // 0 is cell chemistry fully custom
            // this then ignores the amount of cells
            if(this.cellChemistry === 0){
                this.packOverVoltage = this.protMaxCellVoltage / 100;
                this.packUnderVoltage = this.protMinCellVoltage / 100;

            }else{

                sf.getId("max-cell-voltage").value = this.protMaxCellVoltage;
                sf.getId("min-cell-voltage").value = this.protMinCellVoltage;

                this.packOverVoltage = this.protMaxCellVoltage * this.battCellCount / 1000;
                this.packUnderVoltage = this.protMinCellVoltage * this.battCellCount / 1000;
            }
        }else{
            this.fullyCustomChemistryOption.disabled = true;

            sf.getId("max-cell-voltage").value = this.protMaxCellVoltage;
            sf.getId("min-cell-voltage").value = this.protMinCellVoltage;

            this.packOverVoltage = this.protMaxCellVoltage * this.battCellCount / 1000;
            this.packUnderVoltage = this.protMinCellVoltage * this.battCellCount / 1000;
        }


        sf.getId("pack-overvoltage").value = this.packOverVoltage;
        sf.getId("pack-undervoltage").value = this.packUnderVoltage;


        sf.getId("strand-max-current").value = this.protMaxCurrent / 100;
        sf.getId("strand-max-reverse-current").value = this.protMaxReverseCurrent / 100;
        sf.getId("strand-max-imbalance-current").value = this.protMaxImbalanceCurrent / 100;

        if(typeof this.protSpikeSensitivity !== 'undefined') {
            this.boardConfigSpikeSensitivitySelector.disabled = false;

            this.#setCurrentSpikeSensitivity(this.protSpikeSensitivity);
        }else{
            this.boardConfigSpikeSensitivitySelector.disabled = true;
        }

        sf.getId("datalogging-update-frequency").value = 1000/this.dataloggingUpdateInterval;

        sf.getId("logic-max-temp").value = this.protMaxLogicTemp;
        sf.getId("power-max-temp").value = this.protMaxPowerTemp;

        //getId("precharge-current-limit").value = config.prechargeCurrentLimit;

        sf.getId("load-capacitance").value = this.prechargeNomCapacity;
        sf.getId("autostart").checked = this.boardAutoStart;
        sf.getId("bms-config-auto-power-off").value = this.boardPowerOffTime;
        this.#setEnabledChannelsByte(this.boardEnabledChannels);
        sf.getId("updatecount").innerHTML = this.boardUpdateCount;
    }



    //MAX VALUES
    #checkBMSConfigPlausibility() {
        sf.setValueBackToBoundaries("battery-cells", 4, 30);
        sf.setValueBackToBoundaries("battery-capacity", 0, 10000000);

        sf.setValueBackToBoundaries("max-cell-voltage", 100, 6000);
        sf.setValueBackToBoundaries("min-cell-voltage", 100, 6000);
        sf.setValueBackToBoundaries("strand-max-imbalance-current", 0, 100);

        const shunt1 = this.bmsObj.bmsCalibObj.shunt1;
        const shunt2 = this.bmsObj.bmsCalibObj.shunt2;
        const shunt3 = this.bmsObj.bmsCalibObj.shunt3;

        //todo: make this dependant on the bms topology!!

        // take the largest configuration shunt value as the voltage at the current measurement can't be above 100mV
        // for any of the shunts (at max current)
        const maxShuntValue = Math.max(shunt1, shunt2, shunt3); // in uR
        // 100000V / 1uR = 0.1V / 1R = A
        const maxConfigurationCurrent = (100000 / maxShuntValue) - 1;

        sf.setValueBackToBoundaries("strand-max-current", 1, maxConfigurationCurrent);
        sf.setValueBackToBoundaries("strand-max-reverse-current", 1, maxConfigurationCurrent);

        sf.setValueBackToBoundaries("power-max-temp", 0, 90);
        sf.setValueBackToBoundaries("logic-max-temp", 0, 90);

        //setValueBackToBoundaries("precharge-current-limit", 1, 50);
        //setValueBackToBoundaries("load-capacitance", 1, 100000);

        sf.setValueBackToBoundaries("datalogging-update-frequency", 1, 120);
        sf.setValueBackToBoundaries("bms-config-auto-power-off", 0, 65534);
    }


    #blurAppropriateVoltageFields(){
        const val = this.boardConfigBatterySelector.options[this.boardConfigBatterySelector.selectedIndex].value;

        if(val === "li-ion" || val === "li-po" || val === "li-fe-po"){
            let toBeBlurred = document.getElementsByClassName("pack-field");
            for (let i = 0; i < toBeBlurred.length; i++) {
                toBeBlurred[i].style.opacity = "0.5";
                toBeBlurred[i].style.pointerEvents = "none";
            }
            toBeBlurred = document.getElementsByClassName("battery-field");
            for (let i = 0; i < toBeBlurred.length; i++) {
                toBeBlurred[i].style.opacity = "1";
                toBeBlurred[i].style.pointerEvents = "";
            }
            toBeBlurred = document.getElementsByClassName("cell-field");
            for (let i = 0; i < toBeBlurred.length; i++) {
                toBeBlurred[i].style.opacity = "0.5";
                toBeBlurred[i].style.pointerEvents = "none";
            }
            return;
        }else{
            let toBeBlurred = document.getElementsByClassName("pack-field");
            for (let i = 0; i < toBeBlurred.length; i++) {
                toBeBlurred[i].style.opacity = "1";
                toBeBlurred[i].style.pointerEvents = "";
            }
            toBeBlurred = document.getElementsByClassName("battery-field");
            for (let i = 0; i < toBeBlurred.length; i++) {
                toBeBlurred[i].style.opacity = "0.5";
                toBeBlurred[i].style.pointerEvents = "none";
            }
            toBeBlurred = document.getElementsByClassName("cell-field");
            for (let i = 0; i < toBeBlurred.length; i++) {
                toBeBlurred[i].style.opacity = "1";
                toBeBlurred[i].style.pointerEvents = "";
            }
        }

        if(val === "custom-li"){
            let toBeBlurred = document.getElementsByClassName("battery-field");
            for (let i = 0; i < toBeBlurred.length; i++) {
                toBeBlurred[i].style.opacity = "1";
                toBeBlurred[i].style.pointerEvents = "";
            }
            toBeBlurred = document.getElementsByClassName("pack-field");
            for (let i = 0; i < toBeBlurred.length; i++) {
                toBeBlurred[i].style.opacity = "0.5";
                toBeBlurred[i].style.pointerEvents = "none";
            }
            return;
        }else{
            let toBeBlurred = document.getElementsByClassName("battery-field");
            for (let i = 0; i < toBeBlurred.length; i++) {
                toBeBlurred[i].style.opacity = "1";
                toBeBlurred[i].style.pointerEvents = "";
            }
            toBeBlurred = document.getElementsByClassName("pack-field");
            for (let i = 0; i < toBeBlurred.length; i++) {
                toBeBlurred[i].style.opacity = "1";
                toBeBlurred[i].style.pointerEvents = "";
            }
        }


        if(val === "custom"){
            let toBeBlurred = document.getElementsByClassName("battery-field");
            for (let i = 0; i < toBeBlurred.length; i++) {
                toBeBlurred[i].style.opacity = "0.5";
                toBeBlurred[i].style.pointerEvents = "none";
            }
        }else{
            let toBeBlurred = document.getElementsByClassName("battery-field");
            for (let i = 0; i < toBeBlurred.length; i++) {
                toBeBlurred[i].style.opacity = "1";
                toBeBlurred[i].style.pointerEvents = "";
            }
        }
    }

    //STANDART BATTERY CELL VALUES
    #setStandardBatteryValues(){
        let val = this.boardConfigBatterySelector.options[this.boardConfigBatterySelector.selectedIndex].value;

        if(val === "li-ion"){
            sf.getId("max-cell-voltage").value = "4250";
            sf.getId("min-cell-voltage").value = "2600";

            this.#calculatePackVoltages();
            return;
        }
        if(val === "li-po"){
            sf.getId("max-cell-voltage").value = "4250";
            sf.getId("min-cell-voltage").value = "2500";

            this.#calculatePackVoltages();
            return;
        }
        if(val === "li-fe-po"){
            sf.getId("max-cell-voltage").value = "3500";
            sf.getId("min-cell-voltage").value = "2400";

            this.#calculatePackVoltages();
        }
    }

    #calculatePackVoltages(){
        sf.getId("pack-overvoltage").value = parseFloat(sf.getId("battery-cells").value) *
            parseFloat(sf.getId("max-cell-voltage").value) / 1000;
        sf.getId("pack-undervoltage").value = parseFloat(sf.getId("battery-cells").value) *
            parseFloat(sf.getId("min-cell-voltage").value) / 1000;
    }


    readBMSConfig(cb){
        this.bmsObj.characteristics.bmsConfigCharacteristic.readValue().then(configValues => {

            // first read in GUI Values so that there is actually a change when reading in the new Buffer
            if(this.initialised){
                this.readInGUIValues();
            }
            let changeOccurred = this.readInBuffer(configValues);

            // cheap way to not make it warn on the first read
            if(!this.initialised){
                changeOccurred = true;
            }

            this.setGUIValues();

            this.updateConfigRelatedGauges(this);
            cb(true, changeOccurred);
        }).catch((error)=>{
            cb(false, false);
        });
    }


    writeBoardConfig(counter){

        // check if stateMachineState is in either "ready" or "fault", otherwise write gets aborted.
        if(typeof this.bmsObj === "undefined"){
            if(!(this.bmsObj.bmsDataProcessor.stateMachineState === 2
                || this.bmsObj.bmsDataProcessor.stateMachineState.stateMachineState === 4)){
                this.indicateFailure();
                return;
            }
        }

        if (counter >= 10){
            //alert("Failed to clear alerts: " + error);
            this.indicateFailure();

            throw new CharacteristicUnavailableError();
        }

        this.bmsObj.pauseDataLogging().then();

        // writing config characteristic
        this.readInGUIValues();

        this.bmsObj.characteristics.bmsConfigCharacteristic.writeValue(Uint8Array.from(this.getBuffer()).buffer).then(_ => {

            if(sf.getIdValue("board-config-device-name") !== this.bmsObj.name && typeof this.bmsObj.name !== "undefined"){
                let encoder = new TextEncoder();
                this.bmsObj.characteristics.bmsDeviceNameCharacteristic.writeValue(encoder.encode(sf.getIdValue("board-config-device-name")).buffer).then(_ => {

                    this.indicateSuccess(3000);

                    console.log("successfully wrote config & updated name");

                    setTimeout(() => {
                        this.readBMSConfig(async (success, change)=>{
                            if(success){
                                await this.bmsObj.getDeviceName();

                                this.updateBMSNameFields(this.bmsObj.name);

                                // there's always change in the update count
                                // warning if update count doesn't change - would indicate firmware malfunction
                                if (change) {
                                    this.indicateSuccess(3000);
                                } else {
                                    // warning on "read successful but nothing changed"
                                    this.indicateWarning(2000);
                                }

                                // as the last step - re-enables data logging
                                this.bmsObj.dataLoggingCharacteristic.startNotifications().then();


                                // recalculate SOC if cell number change is written to settings
                                if (this.#bmsConfigObjBefore.battCellCount !== this.battCellCount) {
                                    /*
                                    remainingEnergy1 = 0;
                                    remainingEnergy2 = 0;
                                    remainingEnergy3 = 0;
                                     */
                                }
                            }else{
                                this.indicateFailure();
                            }



                            // as the last step - re-enable data logging
                            this.bmsObj.unPauseDataLogging().then();

                            // recalculate SOC if cell number change is written to settings
                            if(this.#bmsConfigObjBefore.battCellCount !== this.battCellCount){
                                /*
                                remainingEnergy1 = 0;
                                remainingEnergy2 = 0;
                                remainingEnergy3 = 0;
                                 */
                            }
                        });

                    }, 200);
                }).catch(e => {
                    console.log(e);

                    this.indicateFailure();
                });
            }else{
                console.log("successfully wrote config");
                this.readBMSConfig((success, change)=>{
                    if(success){
                        if(change){
                            // indicate success on change
                            this.indicateSuccess(3000);
                        }else{
                            // warning on "read successful but nothing changed"
                            this.indicateWarning(2000);
                        }
                    }else{
                        this.indicateFailure();
                    }

                    // as the last step - re-enables data logging
                    this.bmsObj.unPauseDataLogging().then();

                    // recalculate SOC if cell number change is written to settings
                    if(this.#bmsConfigObjBefore.battCellCount !== this.battCellCount){
                        /*
                        remainingEnergy1 = 0;
                        remainingEnergy2 = 0;
                        remainingEnergy3 = 0;
                         */
                    }
                });

            }
        }).catch(error => {
            setTimeout( ()=>{
                // recursion for up to 10 tries
                this.writeBoardConfig(counter + 1);
            }, 100);
        });
    }




}