export default class sf{

    constructor(){}

    // swaps two dom elements
    static swap(node1, node2) {

        const afterNode2 = node2.nextElementSibling;
        const parent = node2.parentNode;

        // bug fix
        if (afterNode2 === node1) {
            this.swap(node2, node1);
        } else {
            node1.replaceWith(node2);
            parent.insertBefore(node1, afterNode2);
        }
    }


    // helper for waiting until a condition is met
    static until(conditionFunction) {
        const poll = resolve => {
            if (conditionFunction()) resolve();
            else setTimeout(_ => poll(resolve), 10);
        }

        return new Promise(poll);
    }


    static isScrolledIntoViewWithKeyboard(el) {
        if (this.isElementHidden(el)) {
            return false;
        }

        const rect = el.getBoundingClientRect();
        const elemTop = rect.top;
        const elemBottom = rect.bottom;

        // Only completely visible elements return true:
        // Partially visible elements return true:
        //isVisible = elemTop < window.innerHeight && elemBottom >= 0;

        // numpad appears to always be 265px high
        return elemTop < (window.innerHeight - 265) && elemBottom >= 0;
    }

    static isScrolledIntoView(el) {
        if (this.isElementHidden(el)) {
            return false;
        }

        const rect = el.getBoundingClientRect();
        const elemTop = rect.top;
        const elemBottom = rect.bottom;

        // Only completely visible elements return true:
        // Partially visible elements return true:
        //isVisible = elemTop < window.innerHeight && elemBottom >= 0;
        return elemTop < window.innerHeight && elemBottom >= 0;
    }

    static isElementHidden(obj) {
        // loop up through all the parents to see if an element is hidden
        while (!(typeof obj == "undefined" || obj === document)
        && !(obj.classList.contains("visibility-hidden") || obj.classList.contains("display-none"))) {
            obj = obj.parentNode;
        }
        return obj !== document;
    }


    /*
        Stuff for the Config/Calib and data logging data that is split into bytes
     */


    static to16bit(number) {
        if (number >= 65536) {
            return;
        }
        let lsb = number % 256;
        let msb = Math.floor(number / 256);

        return [lsb, msb];
    }

    static to32bit(number) {
        if (number >= Math.pow(32, 2)) {
            return;
        }
        let lsb = number % 256;
        let b1 = Math.floor(number / 256) % 256;
        let b2 = Math.floor(number / 65536) % 256;
        let msb = Math.floor(number / 256);

        return [lsb, b1, b2, msb];
    }


    // the notorious handleSignedBullshit
    static handleSignedBullshit(input) {
        if (input > Math.pow(2, 15)) {
            return Math.pow(2, 16) - input;
        }
        return -input;
    }

    static handleSignedBullshit32(input) {
        if (input > Math.pow(2, 31)) {
            return Math.pow(2, 32) - input;
        }
        return -input;
    }

    static handleUnsignedBullshit32(input) {
        if (input < 0) {
            return Math.pow(2, 32) + input;
        }
        return input;
    }

    static handleSignedBullshit64(input) {
        if (input > Math.pow(2, 63)) {
            return Math.pow(2, 64) - input;
        }
        return -input;
    }


    static getId(id) {
        return document.getElementById(id);
    }

    static getIdValue(id) {
        return document.getElementById(id).value;
    }

    static getIdChecked(id) {
        return document.getElementById(id).checked;
    }

    static setValueBackToBoundaries(id, min, max) {
        let element = document.getElementById(id);
        let v = parseFloat(element.value);
        if (v < min) {
            element.value = min;
            element.style.color = "red";
            return;
        }
        if (v > max) {
            element.value = max;
            element.style.color = "red";
            return;
        }
        element.style.color = "";
    }


    static arraysEqual(a, b) {
        if (a === b) return true;
        if (a == null || b == null) return false;
        if (a.length !== b.length) return false;

        // If you don't care about the order of the elements inside
        // the array, you should sort both arrays here.
        // Please note that calling sort on an array will modify that array.
        // You might want to clone your array first.

        for (let i = 0; i < a.length; ++i) {
            if (a[i] !== b[i]) return false;
        }
        return true;
    }

    static objectsEqual(obj1, obj2) {
        let props1 = Object.getOwnPropertyNames(obj1);
        let props2 = Object.getOwnPropertyNames(obj2);
        if (props1.length !== props2.length) {
            return false;
        }
        for (let i = 0; i < props1.length; i++) {
            let val1 = obj1[props1[i]];
            let val2 = obj2[props1[i]];
            let isObjects = this.isObject(val1) && this.isObject(val2);
            if (isObjects && !this.objectsEqual(val1, val2) || !isObjects && val1 !== val2) {
                return false;
            }
        }
        return true;
    }

    static isObject(object) {
        return object != null && typeof object === 'object';
    }

    static isFloat(val) {
        let floatRegex = /^-?\d+(?:[.,]\d*?)?$/;
        if (!floatRegex.test(val))
            return false;

        val = parseFloat(val);
        return !isNaN(val);
    }


    static mobileDevice() {
        return navigator.userAgentData.mobile;
    }


    static isChrome() {
        if (typeof (window) !== "undefined") {
            return !!window.chrome;
        }

        return false;
    }

    // simpler, less elegant version than with cap but doesn't induce float errors
    static cap(value, low, high) {
        if(value >= high){
            return high;
        }
        if(value <= low){
            return low;
        }
        return value
    }


    // tells if the device has a touchscreen
    static isTouchDevice() {
        return (('ontouchstart' in window) ||
            (navigator.maxTouchPoints > 0));
    }


    static getDateStamp() {
        const date = new Date();

        return date.getFullYear().toString() + "_" +
            this.pad2(date.getMonth() + 1) + "_" +
            this.pad2(date.getDate()) + "__" +
            this.pad2(date.getHours()) + "_" +
            this.pad2(date.getMinutes()) + "_" +
            this.pad2(date.getSeconds());
    }

    static getTimeStamp() {
        const d = new Date();
        return d.getHours() + ":" + d.getMinutes() + ":" + d.getSeconds() + "." + d.getMilliseconds();
    }

    static getMsToday() {
        const d = new Date();
        return (d.getHours() - 2) * 3600 * 1000 + d.getMinutes() * 60 * 1000 + d.getSeconds() * 1000 + d.getMilliseconds();
    }

    static getUnixTime() {
        return Date.now();
    }


    static averagedData(array, rounding) {
        let averagedArray = this.averagedDataNumbers(array);
        // set values for keys to averaged values rounded via.toFixed
        for (const [key, _] of Object.entries(averagedArray)) {
            averagedArray[key] = averagedArray[key].toFixed(rounding);
        }
        return averagedArray;
    }

    static averagedDataNumbers(array) {
        // safety, kids!
        if (array.length === 0) {
            return;
        }

        // copy array keys and values
        // {... original} = copy (copy by value - not by reference)
        let summedDataPacket = {...array[0]};
        // set all values to 0
        for (const [key, _] of Object.entries(summedDataPacket)) {
            summedDataPacket[key] = 0;
        }

        // loop through all data packets and add their values up in the summed data packet
        array.forEach(dataPacket => {
            //console.log(dataPacket);
            for (const [key, value] of Object.entries(dataPacket)) {
                summedDataPacket[key] = summedDataPacket[key] + value;
            }
        });

        //console.log(summedDataPacket);

        // copy array keys and values
        let averagedArray = {...array[0]};
        // set values for keys to averaged values rounded via.toFixed
        for (const [key, _] of Object.entries(averagedArray)) {
            averagedArray[key] = (summedDataPacket[key] / array.length);
        }
        return averagedArray;
    }


    static pad2(n) {
        return n < 10 ? '0' + n : n;
    }


    static getLastXSeconds(array, seconds) {
        let now = Date.now();
        return array.filter((element) => {
            return element.time > (now - 1000 * seconds);
        });
    }


    static setValueTexts(elements, value) {
        for (const element of elements) {
            if (!element.classList.contains("disabled")) {
                element.innerHTML = value;
            }
        }
    }

    static classListValueTextsAdd(elements, classString) {
        for (const element of elements) {
            element.classList.add(classString);
        }
    }

    static classListValueTextsRemove(elements, classString) {
        for (const element of elements) {
            element.classList.remove(classString);
        }
    }

    static setValueValues(elements, value) {
        for (const element of elements) {
            element.value = value;
        }
    }


    // like element.scrollInToView() but with the offset of the tilebar
    static scrollToElement(element) {
        let bodyRect = document.body.getBoundingClientRect();
        let elemRect = element.getBoundingClientRect();
        let offset = elemRect.top - bodyRect.top;

        window.scrollTo(0, offset);
    }


    static deleteOlderDataPackets(buffer, time) {
        if (buffer.length < 2) {
            return;
        }

        const nowTimeStamp = Date.now();

        // more efficient to do this recursively than looping through the whole array
        // (since the array will always be sorted from newest to oldest)
        if (buffer.at(-1).time < (nowTimeStamp - (time * 1000))) {
            buffer.pop();
            this.deleteOlderDataPackets(buffer, time);
        }
    }


    static zoomedIn() {
        return document.getElementById("zoom-content").style.transform !== '';
    }



    static async fetchWithTimeout(resource, options = {}) {
        const { timeout = 8000 } = options;

        const controller = new AbortController();
        const id = setTimeout(() => controller.abort(), timeout);

        const response = await fetch(resource, {
            ...options,
            signal: controller.signal
        }).catch(error => {
            throw error;
        });
        clearTimeout(id);

        return response;
    }


    static random10Digit(){
        return Math.floor(100000 + Math.random() * 900000);
    }


}