export default class connectionWatchDog{
    timeout;

    timeoutAction;

    startTime;
    enabled;

    timedOut;

    constructor(timeout, timeoutAction) {
        this.timeout = timeout;

        this.timeoutAction = timeoutAction;

        this.timedOut = false;
    }

    start(){
        this.startTime = Date.now();
        this.enabled = true;
        this.timedOut = false;

        setTimeout(()=>{
            if(!this.enabled){
                return;
            }
            if(Date.now() - this.startTime < this.timeout){
                // timeout was set higher manually (illegal) or the watchdog was reset
                return;
            }

            this.startTime = Date.now();
            this.timedOut = true;
            this.timeoutAction();
        }, this.timeout);
    }

    stop(){
        this.enabled = false;
    }
}
