

/* Inline Board Config functions */

import gui_elements from "../gui_elements";
import sf from "../helpers/small_functions";

export default class tachoConfig extends gui_elements{
    tempSensorType;
    divider_r;
    ntc_r;
    ntc_b;
    impulses;
    reversed;
    backwards_negative;
    motor_poles;

    initialised = false;


    initialPulseValue = 0;
    currentPulseValue = 0;

    constructor(tachoObj) {
        super();
        this.initialised = false;

        this.tachoObj = tachoObj;

        this.#blurAppropriateTempFields();
        this.#setStandardNTCValues();
    }


    addEventListeners(){

        this.tempSensorSelector.addEventListener("change", ()=>{
            this.#blurAppropriateTempFields();
            this.#setStandardNTCValues();
        });


        // for the "calibration"
        sf.getId("resetPulses").addEventListener("click", (_) => {
            this.initialPulseValue = this.currentPulseValue;
        });

        sf.getId("calculatedistperpulse").addEventListener("click", () => {
            this.#calculateDistancePerPulse();
        });

        sf.getId("inline-config-write").addEventListener("click", () => {
            // writing config characteristic
            this.readInGUIElements();
            this.tachoObj.characteristics.tachoConfigCharacteristic.writeValue(Uint8Array.from(this.getBuffer()).buffer).then(() => {
                this.indicateSuccess(3000);
                console.log("successfully wrote config");

                setTimeout(() => {
                    this.readTachoConfig(()=>{});
                }, 200);
            }).catch(_ => {
                this.indicateFailure();
            });
        });

        sf.getId("inline-config-read").addEventListener("click", () => {
            this.readTachoConfig(()=>{
                this.indicateSuccess(500);
            });
        });





// Odometer reset buttons
        this.tachoResetTripOdoButton.addEventListener("click", () => {
            this.tachoObj.characteristics.tachoOdometerCharacteristic.writeValueWithoutResponse(Uint8Array.from([0x01]).buffer).catch(_ =>{
                console.log("failed to reset trip odometer");
            });
        });

        this.tachoResetVehicleOdoButton.addEventListener("click", () => {
            this.tachoObj.characteristics.tachoOdometerCharacteristic.writeValueWithoutResponse(Uint8Array.from([0x02]).buffer).catch(_ =>{
                console.log("failed to reset vehicle odometer");
            });
        });
    }

    readTachoConfig(cb){
        this.tachoObj.characteristics.tachoConfigCharacteristic.readValue().then((buffer) => {
            this.readInBuffer(buffer);
            this.setGUIElements();

            // indicate success in the callback
            cb(true);
        }).catch(_ => {
            this.indicateFailure();

            // indicate failure in the callback
            cb(false);
        });
    }


    indicateSuccess(duration) {
        this.inlineConfigContainer.classList.add("success");
        setTimeout(() => {
            this.inlineConfigContainer.classList.remove("success");
        }, duration);
    }

    indicateFailure() {
        this.inlineConfigContainer.classList.add("fault");
        setTimeout(() => {
            this.inlineConfigContainer.classList.remove("fault");
        }, 2000);
    }



    #blurAppropriateTempFields(){
        const val = this.tempSensorSelector.options[this.tempSensorSelector.selectedIndex].value;

        if(val !== "ntc_custom" && val !== "ptc_custom"){
            let customTempInputFields = document.getElementsByClassName("customTempInput");
            for (let i = 0; i < customTempInputFields.length; i++) {
                customTempInputFields[i].style.opacity = "0.5";
                customTempInputFields[i].style.pointerEvents = "none";
            }
        }else{
            let customTempInputFields = document.getElementsByClassName("customTempInput");
            for (let i = 0; i < customTempInputFields.length; i++) {
                customTempInputFields[i].style.opacity = "1";
                customTempInputFields[i].style.pointerEvents = "";
            }
        }
    }
    #setStandardNTCValues(){
        const val = this.tempSensorSelector.options[this.tempSensorSelector.selectedIndex].value;

        if(val === "ntc10k"){
            sf.getId("r-value").value = "29400";
            sf.getId("b-value").value = "3460";
            return;
        }
        if(val === "ntc100k"){
            sf.getId("r-value").value = "350000";
            sf.getId("b-value").value = "4334";
            return;
        }

        sf.getId("r-value").value = "";
        sf.getId("b-value").value = "";
    }



    // todo: IMPLEMENT
    /*
    #checkInlineConfigPlausibility() {
        sf.setValueBackToBoundaries("battery-cells", 4, 30);
        sf.setValueBackToBoundaries("battery-capacity", 0, 10000000);

        sf.setValueBackToBoundaries("max-cell-voltage", 2000, 5000);
        sf.setValueBackToBoundaries("min-cell-voltage", 2000, 5000);
        sf.setValueBackToBoundaries("strand-max-imbalance-current", 0, 100);
        sf.setValueBackToBoundaries("strand-max-current", 1, 300);

        sf.setValueBackToBoundaries("power-max-temp", 30, 90);
        sf.setValueBackToBoundaries("logic-max-temp", 30, 90);

        sf.setValueBackToBoundaries("precharge-current-limit", 1, 50);
        sf.setValueBackToBoundaries("load-capacitance", 1, 100000);

        sf.setValueBackToBoundaries("datalogging-update-interval", 20, 255);
        sf.setValueBackToBoundaries("auto-poweroff", 30, 90);

    }
    */


    #getTempSensorType(){
        let val = sf.getId("ntc-type-select").value;
        if(val === "kty83"){
            return 1;
        }
        if(val === "kty84"){
            return 2;
        }

        // fuck ptcs, all standard ntcs get their values set into appropriate fields
        // -> they can be treated as manually input custom ntcs
        return 0;
    }
    #setTempSensorType(value){
        if(value === 1){
            sf.getId("ntc-type-select").value = "kty83";
        }
        if(value === 2){
            sf.getId("ntc-type-select").value = "kty84";
        }
        if(value === 0){
            sf.getId("ntc-type-select").value = "ntc_custom";
        }

        this.#blurAppropriateTempFields();
        this.#setStandardNTCValues();
    }



    readInBuffer(buffer){
        this.tempSensorType = buffer.getUint8(0);
        this.divider_r = (buffer.getUint8(2) << 8) | buffer.getUint8(1);
        this.ntc_r = (buffer.getUint8(4) << 8) | buffer.getUint8(3);
        this.ntc_b = (buffer.getUint8(6) << 8) | buffer.getUint8(5);
        this.impulses = (buffer.getUint8(8) << 8) | buffer.getUint8(7);
        this.reversed = buffer.getUint8(9);
        this.backwards_negative = buffer.getUint8(10);
        this.motor_poles = buffer.getUint8(11);

        // necessary for using old firmware
        // -> only makes option available to edit and write if it's available to read
        try {
            this.dataLoggingUpdateInterval = (buffer.getUint8(15) << 8) | buffer.getUint8(14);
        } catch (err) {
            if (err.constructor === RangeError) {
                console.log("[inline config] fw doesn't support dl_update_interval");
            }
        }

        this.initialised = true;
    }





    readInGUIElements(){
        this.tempSensorType = this.#getTempSensorType();
        this.ntc_r = sf.getIdValue("r-value") / 10;
        this.divider_r = sf.getIdValue("r-value-divider") / 10;
        this.ntc_b = parseInt(sf.getIdValue("b-value"));
        this.reversed = sf.getIdChecked("reversed");
        this.backwards_negative = sf.getIdChecked("back-neg");
        this.impulses = parseInt(sf.getIdValue("distperpulse"));
        this.motor_poles = parseInt(sf.getIdValue("motor-poles"));


        // fields that might not be there dependent on the firmware version

        if(!this.inlineConfigDataLoggingFrequency.disabled){
            this.dataLoggingUpdateInterval =  1000 / this.inlineConfigDataLoggingFrequency.value;
        }
    }


    getBuffer(){
        // don't even bother if not initialised
        if(!this.initialised){
            return;
        }

        let buffer = [];

        buffer[0] = parseInt(this.tempSensorType);

        buffer[1] = sf.to16bit(this.divider_r)[0];
        buffer[2] = sf.to16bit(this.divider_r)[1];

        buffer[3] = sf.to16bit(this.ntc_r)[0];
        buffer[4] = sf.to16bit(this.ntc_r)[1];

        buffer[5] = sf.to16bit(this.ntc_b)[0];
        buffer[6] = sf.to16bit(this.ntc_b)[1];

        buffer[7] = sf.to16bit(this.impulses)[0];
        buffer[8] = sf.to16bit(this.impulses)[1];

        buffer[9] = this.reversed;

        buffer[10] = this.backwards_negative;
        buffer[11] = this.motor_poles;


        //Data logging update interval (needed for older firmware)
        if(typeof this.dataLoggingUpdateInterval !== "undefined") {
            buffer[15] = sf.to16bit(this.dataLoggingUpdateInterval)[0];
            buffer[14] = sf.to16bit(this.dataLoggingUpdateInterval)[1];


            // update count - only needed when putting values behind it since it doesn't need to get written
            buffer[12] = 0;
            buffer[13] = 0;
        }else {
            console.log("[tacho config] fw doesn't support dl_update_interval");
        }

        return buffer;
    }


    setGUIElements(){
        this.#setTempSensorType(this.tempSensorType);
        sf.getId("r-value").value = this.ntc_r * 10;
        sf.getId("r-value-divider").value = this.divider_r * 10;
        sf.getId("b-value").value = this.ntc_b;
        sf.getId("reversed").checked = this.reversed;
        sf.getId("back-neg").checked = this.backwards_negative;
        sf.getId("distperpulse").value = this.impulses;
        sf.getId("motor-poles").value = this.motor_poles;

        if(typeof this.dataLoggingUpdateInterval !== 'undefined') {
            this.inlineConfigDataLoggingFrequency.disabled = false;

            sf.getId("inline-dl-frequency").value = this.dataLoggingUpdateInterval;
        }else{
            this.inlineConfigDataLoggingFrequency.disabled = true;
        }
    }


    updatePulseCount(data){
        this.currentPulseValue = data;
        sf.getId("pulses").innerHTML = String(parseFloat(data) - this.initialPulseValue);
    }

    #calculateDistancePerPulse(){
        let pulses = parseFloat(sf.getId("pulses").innerHTML);
        let dist = parseFloat(sf.getId("distance").value);
        if(pulses === 0){
            sf.getId("pulses").style.color = "red";
            setTimeout(()=>{
                sf.getId("pulses").style.color = "";
            }, 500);
            return;
        }
        sf.getId("distperpulse").value = Math.abs(Math.round(10 * dist / pulses));
    }

}