import sf from "../helpers/small_functions";
import gui_elements from "../gui_elements";


export default class serviceWorkerInit extends gui_elements{
    constructor() {
        super();

        let windowLoaded = false;
        window.onload = function () { windowLoaded = true; }


        // get version number saved in serviceworker
        navigator.serviceWorker.ready
            // get access to serviceworker registration
            .then( async (registration) => {
                if (registration.active) {
                    // wait until window is fully loaded for safety xD
                    await sf.until(_ => windowLoaded);

                    // only works because serviceworker is already active and has claimed the window client
                    registration.active.postMessage('GET_VERSION');
                }
            });


        // handle the response for the version number
        navigator.serviceWorker.addEventListener("message", (event) => {
            if(event.data.msg === "version"){

                // event.data.version is the cache name
                // example: "voltaic-interface-web-v3.93"
                const shortenedVersion = event.data.version.split("-v")[1];

                sf.setValueTexts(this.interfaceVersionFields, shortenedVersion);
            }
        });


        // ui feedback for detected serviceworker update
        function invokeServiceWorkerUpdateFlow(registration) {
            // maybe do some nice banner, the popup is enough for now though
            if(confirm("The deployment has a newer interface version. " +
                "Would you like to update now?" +
                "Note: to apply the update, after pressing ok, please close and reopen the app.")){
                if (registration.waiting) {
                    // let waiting Service Worker know it should become active
                    registration.waiting.postMessage('SKIP_WAITING');
                }
            }
        }

        // check if the browser supports serviceWorker at all
        if ('serviceWorker' in navigator) {
            // wait for the page to load
            window.addEventListener('load', async () => {
                // register the service worker from the file specified
                const registration = await navigator.serviceWorker.register('serviceworker.js');

                // ensure the case when the updatefound event was missed is also handled
                // by re-invoking the prompt when there's a waiting Service Worker
                if (registration.waiting) {
                    invokeServiceWorkerUpdateFlow(registration);
                }

                // detect Service Worker update available and wait for it to become installed
                registration.addEventListener('updatefound', () => {
                    if (registration.installing) {
                        // wait until the new Service worker is actually installed (ready to take over)
                        registration.installing.addEventListener('statechange', () => {
                            if (registration.waiting) {
                                // if there's an existing controller (previous Service Worker), show the prompt
                                if (navigator.serviceWorker.controller) {
                                    invokeServiceWorkerUpdateFlow(registration)
                                } else {
                                    // otherwise it's the first install, nothing to do
                                    console.log('Service Worker initialized for the first time');
                                }
                            }
                        })
                    }
                })

                let refreshing = false;

                // detect controller change and refresh the page
                navigator.serviceWorker.addEventListener('controllerchange', () => {
                    if (!refreshing) {
                        window.location.reload();
                        refreshing = true;
                    }
                })
            })
        }
    }
}