import gui from "../gui";
import otaController from "./otaController";
import sf from "../helpers/small_functions";

export default class otaManager extends gui{

    lastSelectedFirmware;
    bmsController;

    bmsDevice;
    tachoDevice;

    updateInProgress;

    #prevSelectedFirmware = '';

    constructor(bmsDevice, tachoDevice, deviceInfoMgr){
        super();

        this.bmsDevice = bmsDevice;
        this.tachoDevice = tachoDevice;

        this.deviceInfoMgr = deviceInfoMgr;

        this.bmsController = new otaController(bmsDevice);
        this.tachoController = new otaController(tachoDevice);

        this.getAvailableOTAImages();

    }


    getAvailableOTAImages(){
        this.getAvailableBMSOTAImages().then((res) => {
            this.bmsImages = res;
            console.log("[otaManager] got available BMS images!");
        });
        this.getAvailableTachoOTAImages().then((res) => {
            this.tachoImages = res;
            console.log("[otaManager] got available Tacho images!");
        });
    }


    addEventListeners(){

        // listener for opened select
        this.availableOTAImagesSelect.addEventListener("click", ()=>{

            // fill dynamically with the needed tag names
            switch (this.deviceInfoMgr.chosenDevice) {
                case "bms":
                    this.displayAvailableOTAImages(this.bmsImages);

                    break;
                case "tacho":
                    this.displayAvailableOTAImages(this.tachoImages);

                    break;
                default:
                    console.log("[otaManager] no device type selected!");
                    return;
            }

            // actually load in the firmware images
            if(this.availableOTAImagesSelect.value !== "" && this.#prevSelectedFirmware !== ''){
                // block all update actions if update is in progress
                if(this.updateInProgress){
                    return;
                }

                // reduce number of calls on the sad proxy
                if (this.availableOTAImagesSelect.value === this.lastSelectedFirmware) {
                    return;
                }
                this.lastSelectedFirmware = this.availableOTAImagesSelect.value;


                // disable the upload button while firmware file is downloaded and parsed
                this.deviceInfoMgr.blockChooser();
                this.updateOTAButton.disabled = true;
                this.availableOTAImagesSelect.disabled = true;

                // inform user lol
                this.otaUpdateInfoText.innerText = 'Downloading selected Firmware...';


                switch (this.deviceInfoMgr.chosenDevice) {
                    case "bms":
                        this.loadBMSFirmware();

                        break;
                    case "tacho":
                        this.loadTachoFirmware();

                        break;
                    default:

                        // reset everything so the user isn't stuck

                        this.deviceInfoMgr.unblockChooser();
                        this.updateOTAButton.disabled = false;
                        this.availableOTAImagesSelect.disabled = false;

                        break;
                }
            }

            this.#prevSelectedFirmware = this.availableOTAImagesSelect.value;
        });

            // listener for update button
        this.updateOTAButton.addEventListener("click", ()=>{

            switch (this.deviceInfoMgr.chosenDevice) {
                case "bms":
                    console.log("bms selected");

                    this.performUpdateBMS();

                    break;
                case "tacho":
                    console.log("tacho selected");

                    this.performUpdateTacho();

                    break;
                default:
                    break;
            }
        });
    }


    performUpdateTacho(){
        // block all update actions if update is in progress
        if(this.updateInProgress){
            return;
        }

        this.updateOTAButton.disabled = true;

        this.updateInProgress = true;

        this.tachoDevice.pauseDataLogging().then(()=>{
            this.tachoController.startUpdateProcess(()=>{

                this.tachoDevice.unPauseDataLogging().then(()=>{

                    console.log("OTA update finished!");

                    this.updateInProgress = false;
                });

            }).catch(()=>{
                this.updateInProgress = false;
            });
        });
    }

    loadTachoFirmware() {
        const url = "https://api.github.com/repos/Dev-Voltaic/Voltaic_Tacho/releases/tags/" + this.availableOTAImagesSelect.value;
        this.loadFirmware(url, this.tachoController);
    }

    loadBMSFirmware() {
        const url = "https://api.github.com/repos/Dev-Voltaic/Voltaic_BMS/releases/tags/" + this.availableOTAImagesSelect.value;
        this.loadFirmware(url, this.bmsController);
    }

    loadFirmware(url, controller){
        // get the link to the bin file
        sf.fetchWithTimeout(url).then((response) => {
            if(response.status !== 200){
                this.#firmwareDownloadFailed();
                return;
            }

            response.text().then((text) => {
                let responseJSON = JSON.parse(text);

                // go through all assets and get the first one that is a .bin file
                responseJSON.assets.forEach((asset) => {
                    if (asset.name.endsWith(".bin")) {
                        // FUCK CORS
                        let proxyUrl = 'https://api.codetabs.com/v1/proxy?quest=' + asset.browser_download_url;

                        // download image file via cors proxy
                        sf.fetchWithTimeout(proxyUrl,
                            {
                                method: "GET",
                                headers: {},
                                // follow redirect requests!!!
                                redirect: "follow"
                            }).then((fileResponse) => {

                                if(fileResponse.status !== 200){
                                    this.#firmwareDownloadFailed();
                                    return;
                                }

                                // read the image file into the mcumgr
                                fileResponse.blob().then(fileResponseBlob => {
                                    controller.updateSelectedFile(fileResponseBlob, ()=>{

                                        // re-enable user interaction
                                        this.deviceInfoMgr.unblockChooser();
                                        this.updateOTAButton.disabled = false;
                                        this.availableOTAImagesSelect.disabled = false;

                                        this.otaUpdateInfoText.innerText = 'Firmware ' + this.availableOTAImagesSelect.value + ' downloaded!';
                                    });
                                });
                        }).catch(()=>{
                            this.#firmwareDownloadFailed();
                        });
                    }
                });
            });
        }).catch(()=>{
            this.#firmwareDownloadFailed();
        });
    }

    #firmwareDownloadFailed(){
        // re-enable user interaction
        this.deviceInfoMgr.unblockChooser();
        this.updateOTAButton.disabled = true;
        this.availableOTAImagesSelect.disabled = false;

        this.otaUpdateInfoText.innerText = 'Firmware download failed!';
    }


    performUpdateBMS(){
        // block all update actions if update is in progress
        if(this.updateInProgress){
            return;
        }

        this.updateOTAButton.disabled = true;

        this.updateInProgress = true;

        this.bmsDevice.pauseDataLogging().then(()=>{
            this.bmsController.startUpdateProcess(()=>{

                this.bmsDevice.unPauseDataLogging().then(()=>{

                    console.log("OTA update finished!");

                    this.updateInProgress = false;
                });

            }).catch(()=>{
                this.updateInProgress = false;
            });
        });
    }


    displayAvailableOTAImages(responseJSON){

        // when the server responds with a rate limit response, the select can't be filled
        // - user must be warned
        if(typeof responseJSON.forEach !== "function"){
            // clear-out select
            this.availableOTAImagesSelect.innerHTML = "";

            let opt = document.createElement("option");
            opt.innerHTML = "Rate limit exceeded!"; // just a simple warning

            // append created warning to select
            this.availableOTAImagesSelect.appendChild(opt);

            return;
        }

        // before filling, check if it is already filled correctly
        let alreadyFilledCorrectly = true;
        this.availableOTAImagesSelect.childNodes.forEach((option) => {
            let match = false;
            responseJSON.forEach((release) => {
                if(release.tag_name === option.value){
                    match = true;
                }
            });

            if(!match){
                alreadyFilledCorrectly = false;
            }
        });

        if(alreadyFilledCorrectly){
            return;
        }

        // clear-out select
        this.availableOTAImagesSelect.innerHTML = "";

        // programmatically fill select
        responseJSON.forEach((release) => {
            const firmwareVersion = release.tag_name;

            let opt = document.createElement("option");
            opt.value = firmwareVersion;
            opt.innerHTML = firmwareVersion; // whatever property it has

            // then append it to the select element
            this.availableOTAImagesSelect.appendChild(opt);
        });
    }

    async getAvailableTachoOTAImages(){
        const url = 'https://api.codetabs.com/v1/proxy?quest=https://api.github.com/repos/Dev-Voltaic/Voltaic_Tacho/releases';

        try{
            // get all available releases from repo
            let response = await sf.fetchWithTimeout(url);
            if(response.status !== 200){
                this.availableOTAImagesSelect.innerHtml = "Failed to get versions!";
            }else{
                return JSON.parse(await response.text());
            }
        }catch(e){
            if (e instanceof TypeError){
                this.availableOTAImagesSelect.innerHtml = "No Internet connection!";
            }

            return {};
        }

    }

    async getAvailableBMSOTAImages(){
        const url = 'https://api.codetabs.com/v1/proxy?quest=https://api.github.com/repos/Dev-Voltaic/Voltaic_BMS/releases';

        try{
            // get all available releases from repo
            let response = await sf.fetchWithTimeout(url);
            if(response.status !== 200){
                this.availableOTAImagesSelect.innerHtml = "Failed to get versions!";
            }else{
                return JSON.parse(await response.text());
            }
        }catch(e){
            if (e instanceof TypeError){
                this.availableOTAImagesSelect.innerHtml = "No Internet connection!";
            }

            return {};
        }

    }



}