import bleDevice from "./bleDevice";
import bmsConfig from "../settings/board-config";
import bmsCalib from "../settings/board-calib";
import bmsDataProcessor from "../dataProcessing/bms_processing";
import bleConnectionStates, {CharacteristicUnavailableError} from "./ble";
import {zoom} from "../helpers/zoom";
import sf from "../helpers/small_functions";

// noinspection ShiftOutOfRangeJS

export default class bms extends bleDevice{

    services = {
        bmsDataLoggingServiceUuid: {
            serviceUuid: "e9ea0200-e19b-482d-9293-c7907585fc48",
            characteristics: {
                bmsDataLoggingCharacteristicUuid: "e9ea0201-e19b-482d-9293-c7907585fc48"
            },
            required: true
        },

        bmsConfigServiceUuid: {
            serviceUuid: "e9ea0100-e19b-482d-9293-c7907585fc48",
            characteristics: {
                bmsConfigCharacteristicUuid: "e9ea0101-e19b-482d-9293-c7907585fc48",
                bmsCalibCharacteristicUuid: "e9ea0102-e19b-482d-9293-c7907585fc48",
                bmsDeviceNameCharacteristicUuid: "e9ea0103-e19b-482d-9293-c7907585fc48"
            },
            required: true
        },

        developmentServiceUuid: {
            serviceUuid: "e9ea0400-e19b-482d-9293-c7907585fc48",
            characteristics: {
                prechargeControlCharacteristicUuid: "e9ea0403-e19b-482d-9293-c7907585fc48",
                channelControlCharacteristicUuid: "e9ea0401-e19b-482d-9293-c7907585fc48",
                uptimeCharacteristicUuid: "e9ea0404-e19b-482d-9293-c7907585fc48"
            },
            required: true
        },

        // mostly obsolete now
        runtimeControlServiceUuid: {
            serviceUuid: "e9ea0500-e19b-482d-9293-c7907585fc48",
            characteristics: {
                turnOnCharacteristicUuid: "e9ea0503-e19b-482d-9293-c7907585fc48",
                shutdownControlCharacteristicUuid: "e9ea0502-e19b-482d-9293-c7907585fc48",
                userGPOCharacteristicUuid: "e9ea0501-e19b-482d-9293-c7907585fc48"
            },
            required: false
        },

        alertWarningServiceUuid: {
            serviceUuid: "e9ea0300-e19b-482d-9293-c7907585fc48",
            characteristics: {
                warningCharacteristicUuid: "e9ea0301-e19b-482d-9293-c7907585fc48",
                alertCharacteristicUuid: "e9ea0302-e19b-482d-9293-c7907585fc48"
            },
            required: true
        },

        otaUpdateServiceUuid: {
            serviceUuid: '8d53dc1d-1db7-4cd3-868b-8a527460aa84',
            characteristics: {},
            required: false
        }
    }


    connectionWatchDog;

    stateMachineState;

    bmsConfigObj;
    bmsCalibObj;

    #lastUptime = 0;
    #countingUptime = 0;


    constructor(interfaceConfig, dataLoggingMgr, deviceInfoMgr){
        super("BMS", interfaceConfig);

        this.connectionState = bleConnectionStates.disconnected;

        // initialize constructor arguments to class attributes
        this.interfaceConfig = interfaceConfig;
        this.dataLoggingMgr = dataLoggingMgr;
        this.deviceInfoMgr = deviceInfoMgr;


        // create config & calib objects
        this.bmsConfigObj = new bmsConfig(this, true);
        this.bmsConfigObj.addEventListeners();

        this.bmsCalibObj = new bmsCalib(this);
        this.bmsCalibObj.addEventListeners();

        this.bmsDataProcessor = new bmsDataProcessor(this.bmsConfigObj, this.bmsCalibObj, this.interfaceConfig, this.dataLoggingMgr);


        // initialize the callbacks and bms specific stuff for the bleDevice
        let guiOps = {};
        guiOps.setOverlayText = (text)=>{ this.setAutoconnectBMSText(text); }
        guiOps.resetOverlayText = ()=>{ this.resetAutoconnectBMSSilentInstant(); }
        this.setGUIOperations(guiOps);

        this.setServices(this.services);
        this.setServiceInitDoneCallback(()=>{ this.#serviceInitDone() });
        this.setDisconnectedCallback(()=>{ this.#deviceDisconnected() });


        // initial call for looped functions
        this.pollFaults();
        this.#pollUptime(true);
        this.#setUptime();

        //this.print();
    }

    print() {
        setTimeout(()=>{
            console.log("bms: " + this.firstReconnectFailDone);
            this.print();
        }, 200);
    }


    initDataDistribution(dd){
        this.dd = dd;
        this.bmsDataProcessor.gatherer.initDataDistribution(this.dd);
        this.bmsDataProcessor.initDataDistribution(this.dd);
    }

    initTachoObj(tachoObj){
        this.tachoObj = tachoObj;
    }



    // this function is called when the bluetooth connection and service initialization is over
    // that of course does not mean the device is ready to use though, still the config needs to be read,
    // and the data logging needs to be enabled
    async #serviceInitDone(){

        try{
            await this.#getDeviceInfo();
        }catch(error){
            console.warn("[bms connection] failed to get device info!");
        }

        this.setAutoconnectBMSText("Reading Config&Calib");
        console.log("[bms connection] auto-reading bms config/calib");

        try{
            this.bmsCalibObj.readBMSCalib((success) => {
                if(this.connectionWatchDog.timedOut){return;}

                // this will get taken care of by the watchdog!
                if(!success){
                    console.warn("[bmsConnection] calib characteristic unavailable!");
                    return;
                }

                this.connectionWatchDog.start();
                this.bmsConfigObj.readBMSConfig(async (success) => {
                    if(this.connectionWatchDog.timedOut){return;}

                    // this will get taken care of by the watchdog!
                    if(!success){
                        console.warn("[bmsConnection] config characteristic unavailable!");
                        return;
                    }

                    // enabling data logging
                    this.setAutoconnectBMSText("Enabling data logging");
                    this.connectionWatchDog.start();
                    await this.characteristics.bmsDataLoggingCharacteristic.startNotifications();
                    if(this.connectionWatchDog.timedOut){return;}
                    this.connectionWatchDog.stop();

                    this.setAutoconnectBMSText("Successfully connected!");
                    this.characteristics.bmsDataLoggingCharacteristic.addEventListener('characteristicvaluechanged', (event)=>{
                        this.bmsDataProcessor.processData(event);
                    });

                    //console.log(this.characteristics);
                });
            });
        }catch(error){
            if(error instanceof CharacteristicUnavailableError){
                console.log("[bms] how did it come this far?");
                await this.disconnect();
            }
        }

        this.connectionWatchDog.start();



        // wait until all initializations are done
        // (or in case of disconnect during reading - block the execution until the watchdog runs out)
        await sf.until(()=>{
            return this.bmsCalibObj.initialised && this.bmsConfigObj.initialised;
        });

        // unsure what happens with a new connection - does this just get executed twice?


        this.reconnect = true;
        this.connectionState = bleConnectionStates.connected;



        this.bmsDataProcessor = new bmsDataProcessor(this.bmsConfigObj, this.bmsCalibObj, this.interfaceConfig, this.dataLoggingMgr);
        this.bmsDataProcessor.gatherer.initDataDistribution(this.dd);
        this.bmsDataProcessor.initDataDistribution(this.dd);

        if(typeof this.serviceInitDoneCB !== "undefined" && this.serviceInitDoneCB !== null){
            this.serviceInitDoneCB();
        }



        setTimeout(() => {
            // catch if disconnect happened between success and enabling!
            if(!this.isConnected()){
                return;
            }
            this.enableBoardGauges();
            this.disableNothingConnectedOverlay();

            if (!this.tachoObj.isDisconnected()) {
                zoom.to({element: this.table, padding: 0, pan: false});
            }
        }, 1000);
    }

    #deviceDisconnected(){
        this.disableBoardGauges();

        this.resetBMSValues();

        console.log(this.connectionState);

        // enable nothingConnectedOverlay if bms was the last device to disconnect
        if (!this.tachoObj.isConnected()) {
            this.enableNothingConnectedOverlay();
        }

        this.resetAutoconnectBMSSilentInstant();

    }



    async #getDeviceInfo(){
        let decoder = new TextDecoder('utf-8');


        // Device information service
        let service = await this.gattServer.getPrimaryService('device_information');

        let deviceInfo = {};

        await service.getCharacteristic(BluetoothUUID.getCharacteristic('hardware_revision_string')).then(async (characteristic) => {
            await characteristic.readValue().then(value => {
                deviceInfo.hardwareRevision = decoder.decode(value);
            });
        });

        await service.getCharacteristic(BluetoothUUID.getCharacteristic('firmware_revision_string')).then(async (characteristic) => {
            await characteristic.readValue().then(value => {
                deviceInfo.firmwareRevision = decoder.decode(value);
            });
        });

        await service.getCharacteristic(BluetoothUUID.getCharacteristic('model_number_string')).then(async (characteristic) => {
            await characteristic.readValue().then(value => {
                deviceInfo.modelNumber = decoder.decode(value);
            });
        }).catch(()=>{
            deviceInfo.modelNumber = "not supported";
        });

        await service.getCharacteristic(BluetoothUUID.getCharacteristic('software_revision_string')).then(async (characteristic) => {
            await characteristic.readValue().then(value => {
                deviceInfo.serialNumber = decoder.decode(value);
            });
        }).catch(()=>{
            deviceInfo.serialNumber =  "not supported";
        });

        this.#updateDeviceInfo(deviceInfo);
    }


    #updateDeviceInfo(deviceInfo) {
        if (deviceInfo.modelNumber.toLowerCase().includes("relay")) {
            deviceInfo.switchType = "relay";
        } else if (deviceInfo.modelNumber.toLowerCase().includes("mosfet")) {
            deviceInfo.switchType = "mosfet";
        } else {
            deviceInfo.switchType = "unknown";
        }
        if (deviceInfo.modelNumber.toLowerCase().includes("single")) {
            deviceInfo.channelCount = 1;
        } else if (deviceInfo.modelNumber.toLowerCase().includes("double")) {
            deviceInfo.channelCount = 2;
        } else if (deviceInfo.modelNumber.toLowerCase().includes("triple")) {
            deviceInfo.channelCount = 3;
        }

        this.deviceInfoMgr.devicesInfo.updateDeviceInfo("BMS", deviceInfo);

        // temporarily disabled
        this.#checkFWUpdate(deviceInfo);


        if(deviceInfo.modelNumber === "not supported"){
            return;
        }

        // if the model string is supported, check for topology and do specific stuff
        if(deviceInfo.switchType === "relay"){
            this.#setRelaisSpecificLabels();
        }
        if(deviceInfo.switchType === "mosfet"){
            this.#setMosfetSpecificLabels();
        }
    }

    #setRelaisSpecificLabels(){
        sf.setValueTexts(this.bmsPrechargeTempLabels, "BMS-Extern Temp:");
        sf.setValueTexts(this.bmsMaxPrechargeTempLabels, "max BMS<br>Extern Temp:");
    }

    #setMosfetSpecificLabels(){
        sf.setValueTexts(this.bmsPrechargeTempLabels, "BMS-Precharge<br>Temp:");
        sf.setValueTexts(this.bmsMaxPrechargeTempLabels, "max Precharge<br>Temp:");
    }

    #checkFWUpdate(deviceInfo){
        const url = 'https://api.github.com/repos/Dev-Voltaic/Voltaic_BMS/releases/latest';

        try {
            fetch(url).then((response) => {
                response.text().then((text) => {
                    const newestFirmwareVersion = JSON.parse(text)["tag_name"];

                    if(deviceInfo.firmwareRevision !== newestFirmwareVersion){
                        this.firmwareUpdateText.innerHTML = "<a href='https://github.com/Dev-Voltaic/Voltaic_BMS'>"
                            + "Newest Firmware Version is: "
                            + newestFirmwareVersion
                            + "<br>Have you considered updating</a>?";

                        this.firmwareUpdateText.classList.add("fault");
                        this.firmwareUpdateText.classList.remove("hidden");
                    }
                });
            });
        }catch (e){
            // TypeError - failed to fetch
            if(e instanceof TypeError){
                this.firmwareUpdateText.innerHTML = "Failed to get remote version info";
            }
        }

    }


    async pauseDataLogging(){
        await this.characteristics.bmsDataLoggingCharacteristic.stopNotifications();
    }
    async unPauseDataLogging(){
        await this.characteristics.bmsDataLoggingCharacteristic.startNotifications();
    }





    // poll faults from characteristic when data logging gives state 4 (fault)
    pollFaults(){
        // with no logging data, there is no need to poll the faults
        if(!this.bmsDataProcessor.isInitialised()){
            setTimeout(() => {
                this.pollFaults();
            }, 500);
            return;
        }

        // only poll faults if bms is connected and data logging suggests fault state
        if(this.bmsDataProcessor.stateMachineState === 4 && this.isConnected()){
            // try to catch for "gatt operation already in progress"
            this.characteristics.alertCharacteristic.readValue().then(alertValue => {
                this.alertBuffer = (((alertValue.getUint8(3) << 24) | (alertValue.getUint8(2) << 16)) | (alertValue.getUint8(1) << 8)) | (alertValue.getUint8(0));
            }).catch((e) => {
                if(e instanceof DOMException){
                    console.log("[bms] poll faults error: " + e)
                }
            });

            // try to catch for "gatt operation already in progress"
            this.characteristics.warningCharacteristic.readValue().then(warningValue => {
                this.warningBuffer = (((warningValue.getUint8(3) << 24) | (warningValue.getUint8(2) << 16)) | (warningValue.getUint8(1) << 8)) | (warningValue.getUint8(0))
            }).catch((e) => {
                if(e instanceof DOMException){
                    console.log("[bms] poll warnings: " + e)
                }
            });
            this.updateWarningFields(this.warningBuffer, this.alertBuffer);
        }else{
            // do not display any faults if not in fault mode & not connected
            this.alertBuffer = 0;
            this.warningBuffer = 0;
            this.updateWarningFields(this.warningBuffer, this.alertBuffer);
        }
        // recursive call
        setTimeout(() => {
            this.pollFaults();
        }, 500);
    }

    // get uptime from development service every 15 seconds
    #pollUptime(recursive) {
        // not really able to filter this, has to be done all the time while connected
        if (this.isConnected()) {
            try {
                this.characteristics.uptimeCharacteristic.readValue().then(alertValue => {
                    let uptimeValue =
                        sf.handleSignedBullshit64(
                            (alertValue.getUint8(7) << 56) | (alertValue.getUint8(6) << 48) |
                            (alertValue.getUint8(5) << 40) | (alertValue.getUint8(4) << 32) |
                            (alertValue.getUint8(3) << 24) | (alertValue.getUint8(2) << 16) |
                            (alertValue.getUint8(1) << 8) | (alertValue.getUint8(0)));
                    this.#lastUptime = -uptimeValue;
                    this.#countingUptime = 0;
                }).catch((e) => {
                    if(e instanceof DOMException){
                        console.log("[poll Uptime] error: " + e)
                    }
                });
            } catch (error) { //"gatt operation already in progress"
            }

        }
        if(recursive){
            setTimeout(() => {
                this.#pollUptime(true);
            }, 15000);
        }
    }

    // set uptime gauge every second - gets synced uptime every 15 seconds, "interpolates" between the syncs
    #setUptime() {
        if (this.isConnected()) {
            if(this.#lastUptime === 0){
                // call pollUptime if the bms just connected
                this.#pollUptime(false);
            }else{
                this.#countingUptime += 1000;
                this.setOnTime(Math.floor((this.#lastUptime + this.#countingUptime) / 1000));
            }
        }
        setTimeout(()=>{
            this.#setUptime();
        }, 1000);
    }


    clearAlerts(counter, _){
        if (counter === 10){
            //alert("Failed to clear alerts: " + error);
            return;
        }

        // NOT SURE WHICH ERROR COMES BACK IF IT FAILS!
        this.characteristics.alertCharacteristic.writeValue(Uint8Array.from([0]).buffer)
            .then(() => {
                // clear everything (might not be needed as they will automagically clear on the next poll)
                this.updateWarningFields(this.warningBuffer, this.alertBuffer);
            })
            .catch(error => {
                setTimeout( ()=>{
                    this.clearAlerts(counter + 1, error);
                }, 100);
            });
    }




    resetBMSValues() {
        this.bmsDataProcessor.remainingEnergy1 = 0;
        this.bmsDataProcessor.remainingEnergy2 = 0;
        this.bmsDataProcessor.remainingEnergy3 = 0;

        // reset the warning buffer and also update the fields
        this.alertBuffer = 0;
        this.warningBuffer = 0;
        this.updateWarningFields(this.warningBuffer, this.alertBuffer);

        // reset the bms dataLoggingBuffer
        this.bmsDataProcessor.bmsAveragingBuffer = [];

        // reset uptime gauge
        this.setOnTime(0);

        this.bmsDataProcessor.maxValueBuffer = [];

        // hide board information, calibration & configuration if not already hidden
        this.hideAllSettings();
    }


}