import fileObject from "../helpers/fileObject";
import sf from "../helpers/small_functions";


export default class dataLoggingManager{

    loggingFileObj;

    loggingDataBuffer;

    dataLoggingEnabled;

    constructor() {

        this.dataLoggingEnabled = false;

        this.loggingDataBuffer = [];

        // initial call
        this.saveDataLoggingData();
    }

    async newFile(){
        this.loggingFileObj = new loggingDataFileObject(this);

        await this.loggingFileObj.getHandle();

        this.loggingDataBuffer = ["psn;ms_today;stamp;u1;u2;u3;uOut;i1;i2;i3;tShunt;tPch;speed;rpm;motorTemp;externTemp;tripOdo;"];
    }

    closeFile(){
        this.dataLoggingEnabled = false;

        this.loggingFileObj.closeObject();
        this.loggingFileObj = null;

        this.loggingDataBuffer = [];
    }

    saveDataLoggingData(){
        // don't even bother if data logging isn't enabled
        if(!this.dataLoggingEnabled && this.loggingDataBuffer.length === 0){
            setTimeout(()=>{
                this.saveDataLoggingData();
            }, 1000);
            return;
        }

        this.loggingFileObj.saveLoggingData(this.loggingDataBuffer);
        this.loggingDataBuffer = [];

        // recursively execute every second
        setTimeout(()=>{
            this.saveDataLoggingData();
        }, 1000);
    }
}









class loggingDataFileObject extends fileObject{
    constructor(managerObj){
        super("csv");

        this.manager = managerObj;

        this.suggestedFileName = this.getSuggestedFileName();

        this.dataBuffer = [];
    }

    async getHandle(){
        await this.getSaveFileHandle(this.suggestedFileName);
    }

    saveLoggingData(data){

        // push all data into the buffer
        this.dataBuffer = this.dataBuffer.concat(data);

        // then decide what to do with it
        if(!this.hasFileAccess()){

            // lord have mercy for the ram

            // to limit the file size and thereby the ram size, the files will be split

            // limit to about 10MB
            const maxBufferLength = (10E6 / 115);
            if(this.dataBuffer.length > maxBufferLength){
                this.dataBuffer.push("");

                this.saveLegacy(this.suggestedFileName, this.dataBuffer);

                this.dataBuffer = [];
            }

            return;
        }

        if(typeof this.writableStream === "undefined"){
            console.log("initialise object before writing to it");
            return;
        }

        // constantly write data to the file system in 20 packet chunks
        // if the user actually has FS access
        if (this.dataBuffer.length > 20 || !this.manager.dataLoggingEnabled) {
            this.dataBuffer.push("");

            try{
                this.writableStream.write(this.dataBuffer.join("\n"));
            }catch(e){
            }

            this.dataBuffer = [];
        }
    }

    closeObject(){
        if(this.hasFileAccess()){
            if(typeof this.writableStream === "undefined"){
                console.log("initialise object before closing it");
                return;
            }

            this.saveLoggingData([]);
            this.writableStream.close();
        }else{
            this.saveLegacy(this.suggestedFileName, this.dataBuffer);
        }
    }

    getSuggestedFileName(){
        let bmsName = "";

        if(typeof sf.getIdValue("board-config-device-name") !== "undefined"){
            bmsName = sf.getIdValue("board-config-device-name");
        }

        const date = new Date();

        return bmsName +
            date.getFullYear().toString() + "_" +
            sf.pad2(date.getMonth() + 1) + "_" +
            sf.pad2(date.getDate()) + "__" +
            sf.pad2(date.getHours()) + "_" +
            sf.pad2(date.getMinutes()) + "_" +
            sf.pad2(date.getSeconds());
    }
}


