let bleConnectionStates = {
        disconnected: 'disconnected',
        searching: 'searching',
        connecting: 'connecting',
        connected: 'connected',
        reconnecting: 'reconnecting'
    };

export default bleConnectionStates;


export class WrongDeviceError extends Error {
        constructor(message = "This device does not support the required services and characteristics.") {
                super(message);
                this.message = message;
        }
}

export class CharacteristicsInitFailedError extends Error {
        constructor(message = "Initializing the characteristic was unsuccessful.") {
                super(message);
                this.message = message;
        }
}

export class CharacteristicUnavailableError extends Error {
        constructor(message = "For whatever reason, this characteristic is inaccessible.") {
                super(message);
                this.message = message;
        }
}

export class ServicesInitFailedError extends Error {
        constructor(message = "Initializing the characteristic was unsuccessful.") {
                super(message);
                this.message = message;
        }
}