import gui_elements from "../gui_elements";

import sf from "../helpers/small_functions";



export class canId{
    constructor(){

    }

    getBytes(){
        let bytes = [];

        if(this.extended === false){
            bytes[0] = sf.to16bit(this.id)[1];
            bytes[1] = sf.to16bit(this.id)[0];
        }else{
            // set the upmost bit to 1 to indicate extended ID
            bytes[0] = sf.to32bit(this.id)[3] | 0b10000000;
            bytes[1] = sf.to32bit(this.id)[2];
            bytes[2] = sf.to32bit(this.id)[1];
            bytes[3] = sf.to32bit(this.id)[0];
        }

        return bytes;
    }

    init(extended, id){
        this.extended = extended;
        this.id = id;
    }

    initFromBytes(bytes){
        // return if number of bytes can't be an ID
        if(bytes.length < 2){
            return;
        }

        // read MSB of first byte as it indicates an extended id
        if((bytes[0] >> 8) === 1){
            // return if byte count doesn't satisfy indicated extended id
            if(bytes.length < 4){
                return;
            }

            this.extended = true;
            // delete the indicator bit so the byte data isn't corrupted
            bytes[0] = bytes[0] & 0b01111111;
        }

        if(this.extended){
            this.id = ((((bytes[0] << 24) | (bytes[1] << 16)) | (bytes[2] << 8)) | bytes[3])
        }else{
            this.id = ((bytes[0] << 8) | bytes[1])
        }
    }
}


export class canSubFilter{

    start;
    stop;
    condition;
    dataLength;
    data;

    dataBytes;

    validConditions = {
        match: 0,
        // more to come!
    };

    constructor(){

    }

    init(start, stop, condition, data){
        if(!Object.keys(this.validConditions).includes(condition)){
            console.log("[canSubFilter] invalid condition!");
            return;
        }

        // reject all invalid arguments
        if(start < 0 || start > 28){ return; }
        if(stop < 0 || stop > 28){ return; }
        if(stop < start){ return; }

        // start bit number
        this.start = start;
        // stop bit number
        this.stop = stop;
        this.condition = this.validConditions[condition];

        // number of bytes in the data object
        this.dataLength = (Math.ceil((this.stop - this.start) / 8) - 1);

        if(data > Math.pow(2, ((this.dataLength+1)*8))){
            console.log("[canSubFilter] data doesn't fit between start and stop!");
            return;
        }

        this.data = data;
    }

    print(){
        console.log("SubFilter: ");
        console.log(this);

        let byteString = "";

        for (let i = 0; i < this.dataBytes.length; i++) {

            byteString += (" " + Number(this.dataBytes[i]).toString(2));
        }

        console.log("data bytes:");
        console.log(byteString);
    }

    // please look at the documentation in the Tacho_BLE_Firmware GitHub repository for details!
    getBytes(){
        let bytes = [];

        // set the first byte
        bytes[0] = 0;
        bytes[0] |= ((this.start << 0) & 0b00011111);
        bytes[0] |= ((this.stop << 5)  & 0b11100000);

        // set the second byte
        bytes[1] = 0;
        bytes[1] |= ((this.stop >> 3)       & 0b00000011);
        bytes[1] |= ((this.condition << 3)  & 0b00111000);
        bytes[1] |= ((this.dataLength << 6) & 0b11000000);

        // dynamically add the data bytes
        for (let i = 0; i <= this.dataLength; i++) {
            bytes[i+2] = 0;
            bytes[i+2] = (this.data >> 8*i) & 0b11111111;
        }

        this.dataBytes = bytes;

        return bytes;
    }

    initFromBytes(bytes){
        this.start = (bytes[0] & 0b00011111) >> 0;

        this.stop = ((bytes[1] & 0b00000011) << 3 ) | ((bytes[0] & 0b11100000) >> 5);

        this.condition = (bytes[1] & 0b00111000) >> 3;

        this.dataLength = (bytes[1] & 0b11000000) >> 6;


        let dataNumber = 0;
        // dynamically add the data bytes
        for (let i = this.dataLength; i >= 0 ; i--) {
            dataNumber |= bytes[i+2] << 8*i;
        }
        this.data = dataNumber;

        this.dataBytes = bytes;
    }
}

export class canFilter{

    subFilters = [];

    constructor(){
    }

    addSubFilter(subFilter){
        if(this.subFilters.length >= 4){
            console.log("[canFilter] too many subFilters!");
            return;
        }

        if(!(subFilter instanceof canSubFilter)){
            console.log("[canFilter] parameter is not a canSubFilter")
            return;
        }

        this.subFilters.push(subFilter);
    }

    getWriteCommandBytes(){
        let bytes = [];

        bytes[0] |= 0b00000011 & this.subFilters.length;
    }

    // todo: complete

}


export class canMessage{
    constructor(){
    }

    init(extended, id, data){
        this.cId = new canId();
        this.cId.init(extended, id);

        // check for a too long data object
        if(data.length > 7){
            return;
        }

        this.data = data;
        this.dataLength = data.length;
    }

    initFromBytes(bytes){
        this.cId = new canId();
        this.cId.initFromBytes(bytes);

        let dataBytes;

        if(this.cId.extended){
            dataBytes = bytes.slice(4);
        }else{
            dataBytes = bytes.slice(2);
        }

        this.data = dataBytes;
    }

    getBytes(){
        let outgoingBytes = this.cId.getBytes();

        for (let i = 0; i < this.dataLength; i++) {
            outgoingBytes.push(this.data[i]);
        }

        return outgoingBytes;
    }
}


export default class canBridge extends gui_elements{

    constructor(txCharacteristic, rxConfigCharacteristic, rxStreamCharacteristic) {
        super();
        sf.classListValueTextsRemove(this.canBridgeElements, "disabled");

        this.txChar = txCharacteristic;
        this.rxConfigChar = rxConfigCharacteristic;
        this.rxStreamChar = rxStreamCharacteristic;

        // in this case, canBridge is not yet supported by the firmware
        if(typeof this.txChar === "undefined"){

            console.log("[Tacho canBridge] canBridge not supported by firmware!");
            return;
        }

        // add tx event listeners
        this.#addEventListeners();

        if(typeof this.rxConfigChar === "undefined"
            || typeof this.rxStreamChar === "undefined") {

            console.log("[Tacho canBridge] canBridge RX not supported, TX supported!");
        }

        // add rx event listeners
        this.addStreamListener();
    }

    sendMessage(canMessage){
        let outgoingBytes = canMessage.getBytes();

        this.txChar.writeValueWithoutResponse(Uint8Array.from(outgoingBytes).buffer);
    }

    async addStreamListener(){
        this.rxStreamChar.addEventListener('characteristicvaluechanged', (event)=>{this.onRXReceive(event)});
        await this.rxStreamChar.startNotifications();
    }

    onRXReceive(event){
        let value = event.target.value;

        // catch broken messages
        if(value.byteLength < 2){
            return;
        }

        let data = [];

        for (let i = 0; i < value.byteLength; i++) {
            data.push(value.getUint8(i));
        }

        let message = new canMessage();
        message.initFromBytes(data);

        console.log(message);


    }

    addCanIDToWhiteList(extended, id){
        let cId = new canId();
        cId.init(extended, id);

        let cIdBytes = this.addCanIdToWhiteList(cId);

        // let


        // going to be changed to the new rx filter design
        return cIdBytes;
    }

    // returns true upon success, false upon error
    addCanIdToWhiteList(canId){

        // 0 for adding to the list
        let outgoingBytes = [0].concat(canId.getBytes());

        this.rxConfigChar.writeValue(Uint8Array.from(outgoingBytes).buffer).then(async _ => {
            let listAfter = await this.getWhiteList();

            console.log("list after:");
            console.log(listAfter);

            let included = false;
            for (const el of listAfter) {
                if(el.id === canId.id){
                    included = true;
                    break;
                }
            }

            if(included){
                return true;
            }

            console.log("[Tacho canBridge] successful write but not included in whitelist after")
        }).catch(error => {
            console.log("[Tacho canBridge] " + error);
        });

        return false;
    }

    async getWhiteList(){

        let dataBuffer = [];
        try{
            dataBuffer = await this.rxConfigChar.readValue();
        }catch (e) {
            console.log(e);
            return [];
        }

        if(dataBuffer.byteLength < 3){
            return;
        }

        let data = [];

        for (let i = 0; i < dataBuffer.byteLength; i++) {
            data.push(dataBuffer.getUint8(i));
        }

        console.log("[Tacho canBridge] whiteList length: " + data[0]);

        let whiteList = [];

        // the data that is left to be interpreted
        let iteratedData = data.slice(1);

        console.log(iteratedData);

        while(iteratedData.length >= 2){
            let id = new canId();
            id.initFromBytes(iteratedData);

            whiteList.push(id);

            // shorten the available data according to how many bytes the prev id took up
            if(id.extended){
                iteratedData = iteratedData.slice(4);
            }else{
                iteratedData = iteratedData.slice(2);
            }
        }

        return whiteList;
    }


    #addEventListeners(){
        this.canTxGaugeSend.addEventListener("click", ()=>{
            let message = this.#getCanTxMessageFromGUI();

            this.sendMessage(message);
        });

        this.canRxGaugeSend.addEventListener("click", () => {
            this.#addCanRxMessageFromGUI();
        });
    }

    #getCanTxMessageFromGUI(){
        let extended = this.canTxGaugeExtended.checked;
        let id = parseInt(this.canTxGaugeId.value);

        let data = [];
        if(parseInt(this.canTxGaugeD0.value) >= 0){
            data.push(parseInt(this.canTxGaugeD0.value));
        }
        if(parseInt(this.canTxGaugeD1.value) >= 0){
            data.push(parseInt(this.canTxGaugeD1.value));
        }


        // TEMPORARILY COMMENTED OUT FOR THE SHORTENED CAN INTERFACE TILE
        /*
        if(parseInt(this.canTxGaugeD2.value) >= 0){
            data.push(parseInt(this.canTxGaugeD2.value));
        }
        if(parseInt(this.canTxGaugeD3.value) >= 0){
            data.push(parseInt(this.canTxGaugeD3.value));
        }
        if(parseInt(this.canTxGaugeD4.value) >= 0){
            data.push(parseInt(this.canTxGaugeD4.value));
        }
        if(parseInt(this.canTxGaugeD5.value) >= 0){
            data.push(parseInt(this.canTxGaugeD5.value));
        }
        if(parseInt(this.canTxGaugeD6.value) >= 0){
            data.push(parseInt(this.canTxGaugeD6.value));
        }
        if(parseInt(this.canTxGaugeD7.value) >= 0){
            data.push(parseInt(this.canTxGaugeD7.value));
        }

        */

        let cM = new canMessage();
        cM.init(extended, id, data);
        return cM;
    }

    #addCanRxMessageFromGUI(){
        let extended = this.canRxGaugeExtended.checked;
        let id = parseInt(this.canRxGaugeId.value);

        return this.addCanIDToWhiteList(extended, id);
    }

}