import gui from "../gui";

import sf from "../helpers/small_functions";
import tachoGatherer from "./tacho_gathering";





export default class tachoDataProcessor extends gui{

    tachoConfigObj;


    tachoAveragingBuffer = [];
    maxInlineValueBuffer = [];

    // values for calculation of wh/km and therefore also range
    usedEnergyOffset;


    lastInlineLoggingDataTimeStamp = Date.now();
    drivenDistanceOffset;


    providedDataLabels = [

    ]


    // this constructor takes the tachoObj,
    // because the bmsObj is used when calculating economy and stuff
    constructor(tachoObj, interfaceConfig, dataLoggingMgr) {
        super();

        this.tachoObj = tachoObj;
        this.tachoConfigObj = tachoObj.tachoConfigObj;

        this.interfaceConfig = interfaceConfig;

        this.dataLoggingMgr = dataLoggingMgr;

        this.gatherer = new tachoGatherer(this.tachoConfigObj);

        this.drivenDistanceOffset = -1;
    }


    initDataDistribution(dd){
        this.dd = dd;
        this.dd.addProvidedDataLabels(this.providedDataLabels);
    }


    isInitialised() {
        return typeof this.tachoAveragingBuffer.at(0) === "undefined";
    }

    processData(event) {
        let tachoLoggingPacket = this.gatherer.gatherData(event);

        // catch invalid / dropped packets
        if (typeof (tachoLoggingPacket) === "undefined") {
            return;
        }

        const stamp = sf.getTimeStamp();
        const ms_today = sf.getMsToday();


        // always have the newest packet at the first index of the buffer
        this.tachoAveragingBuffer.unshift(tachoLoggingPacket);
        // delete all packets that are older than needed
        sf.deleteOlderDataPackets(this.tachoAveragingBuffer, this.interfaceConfig.maxTachoAveragingInterval);

        const averagedDataSpeeds = sf.averagedData(sf.getLastXSeconds(this.tachoAveragingBuffer, this.interfaceConfig.tachoDataAveragingIntervalV), 1);
        const averagedDataT = sf.averagedData(sf.getLastXSeconds(this.tachoAveragingBuffer, this.interfaceConfig.tachoDataAveragingIntervalT), 1);


        // ECONOMY CALCULATION
        if (this.tachoObj.bmsObj.isConnected()
            && typeof this.tachoObj.bmsObj.bmsDataProcessor.bmsAveragingBuffer.at(-1) !== "undefined") {
            // get bms data for economy calculation if bms connected
            let bmsLoggingPacket = this.tachoObj.bmsObj.bmsDataProcessor.bmsAveragingBuffer.at(-1);
            const totalUsedEnergy = bmsLoggingPacket.eU1
                + bmsLoggingPacket.eU2
                + bmsLoggingPacket.eU3;

            // kinda pfusch
            // used for the "reset economy" button
            if (this.drivenDistanceOffset === -1) {
                if (this.tachoObj.bmsObj.isConnected()) {
                    this.drivenDistanceOffset = (tachoLoggingPacket.tripOdo / 1000); // in meters

                    this.usedEnergyOffset = totalUsedEnergy;
                }
            }


            // distance driven in the session in meters
            let drivenDistanceSession = (tachoLoggingPacket.tripOdo / 1000) - this.drivenDistanceOffset;
            // energy used in the session in mWh
            let energyUsedSession = (totalUsedEnergy - this.usedEnergyOffset) / 3.6;

            // wh/km for the session
            let sessionEconomy = energyUsedSession / drivenDistanceSession;

            let range = Math.abs(
                (this.tachoObj.bmsObj.bmsDataProcessor.remainingEnergy1
                    + this.tachoObj.bmsObj.bmsDataProcessor.remainingEnergy2
                    + this.tachoObj.bmsObj.bmsDataProcessor.remainingEnergy3)
                / sessionEconomy);

            this.setEconomyGauges({
                whkmSession: sessionEconomy,
                range: range
            });
        } else {
            this.disableEconomyGauges();
        }


        // Set values
        this.tachoObj.tachoConfigObj.updatePulseCount(tachoLoggingPacket.calibPulseCount);


        this.setInlineTempValues({
            motor: averagedDataT.motorTemp,
            extern: averagedDataT.integratedTemp
        });

        this.setTachoGauges({
            tripOdo: tachoLoggingPacket.tripOdo,
            vehicleOdo: tachoLoggingPacket.vehicleOdo
        });

        this.setSpeedGaugeValues(averagedDataSpeeds);

        let maxTachoValueInfo = {
            time: tachoLoggingPacket.time,
            speed: tachoLoggingPacket.speed,
            motorTemp: tachoLoggingPacket.motorTemp,
            integratedTemp: tachoLoggingPacket.integratedTemp
        }


        // always have the newest packet at the first index of the buffer
        this.maxInlineValueBuffer.unshift(maxTachoValueInfo);
        // delete all packets that are older than needed
        sf.deleteOlderDataPackets(this.maxInlineValueBuffer, this.interfaceConfig.maxValueHoldOnTime);

        let speedsArray = [];
        let motorTempArray = [];
        let externTempArray = [];

        this.maxInlineValueBuffer.forEach((sample) => {
            if (!isNaN(sample.speed)) {
                speedsArray.unshift(sample.speed);
            }
            if (!isNaN(sample.motorTemp)) {
                motorTempArray.unshift(sample.motorTemp).toFixed(1);
            }
            if (!isNaN(sample.integratedTemp)) {
                externTempArray.unshift(sample.integratedTemp).toFixed(1);
            }
        });


        this.setInlineMaxValues({
            speed: Math.max(...speedsArray),
            motorTemp: Math.max(...motorTempArray).toFixed(1),
            externTemp: Math.max(...externTempArray).toFixed(1),
        });


        // this has to be after both setInlineTempValues and setInlineMaxValues
        // as it disables and overwrites the values put in by the functions above in case of disabled temps
        this.setTempGaugeVisibility(this.interfaceConfig);

        if (this.dataLoggingMgr.dataLoggingEnabled && this.tachoObj.isConnected()) {
            let dataStringInline = [
                "",
                ms_today,
                stamp,
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                tachoLoggingPacket.speed,
                tachoLoggingPacket.rpm,
                tachoLoggingPacket.motorTemp,
                tachoLoggingPacket.integratedTemp,
                tachoLoggingPacket.tripOdo
            ].join(";");

            this.dataLoggingMgr.loggingDataBuffer.push(dataStringInline);
        }
    }
}