import gui from "../gui";

import sf from "../helpers/small_functions";
import bmsGatherer from "./bms_gathering";


export default class bmsDataProcessor extends gui{

    bmsAveragingBuffer = [];

    maxValueBuffer = [];


    providedDataLabels = [
        "bmsITotal",
        "bmsPTotal",
        "bmsPLoss",
        "soc1",
        "soc2",
        "soc3",
        "minSOC"
    ];


    usedEnergyOffset1 = -1; // Watt-hours
    usedEnergyOffset2 = -1; // Watt-hours
    usedEnergyOffset3 = -1; // Watt-hours

    remainingEnergy1 = 0; // Watt-hours
    remainingEnergy2 = 0; // Watt-hours
    remainingEnergy3 = 0; // Watt-hours

    stateMachineState = 4;

    constructor(bmsConfigObj, bmsCalibObj, interfaceConfig, dataLoggingMgr) {
        super();
        this.bmsConfigObj = bmsConfigObj;
        this.bmsCalibObj = bmsCalibObj;

        this.interfaceConfig = interfaceConfig;

        this.dataLoggingMgr = dataLoggingMgr;

        this.gatherer = new bmsGatherer(this.bmsConfigObj);
    }

    initDataDistribution(dd){
        this.dd = dd;
        this.dd.addProvidedDataLabels(this.providedDataLabels);
    }

    isInitialised(){
        return (typeof this.bmsAveragingBuffer.at(0)) !== "undefined";
    }

    processData(event) {

        const bmsLoggingPacket = this.gatherer.gatherData(event);

        // catch invalid / dropped packets
        if(typeof(bmsLoggingPacket) === "undefined"){
            return;
        }

        // always have the newest packet at the first index of the buffer
        this.bmsAveragingBuffer.unshift(bmsLoggingPacket);
        // delete all packets that are older than needed
        sf.deleteOlderDataPackets(this.bmsAveragingBuffer, this.interfaceConfig.maxAveragingInterval)

        const averagedDataU = sf.averagedData(sf.getLastXSeconds(this.bmsAveragingBuffer, this.interfaceConfig.averagingIntervalU), 1);
        const averagedDataI = sf.averagedData(sf.getLastXSeconds(this.bmsAveragingBuffer, this.interfaceConfig.averagingIntervalI), 2);
        const averagedDataT = sf.averagedData(sf.getLastXSeconds(this.bmsAveragingBuffer, this.interfaceConfig.averagingIntervalT), 1);
        const averagedDataCalib = sf.averagedData(sf.getLastXSeconds(this.bmsAveragingBuffer, this.interfaceConfig.averagingIntervalCalib), 4);

        const averagedDataPLoss = sf.averagedDataNumbers(sf.getLastXSeconds(this.bmsAveragingBuffer, this.interfaceConfig.averagingIntervalT));


        this.stateMachineState = bmsLoggingPacket.stateMachineState;


        // calculating further values

        let P_total = (parseFloat(averagedDataU.uOut) / 1000 * (parseFloat(averagedDataI.i1) + parseFloat(averagedDataI.i2) + parseFloat(averagedDataI.i3))); // Power in kW

        let P_loss = Math.abs(
            (averagedDataPLoss.u1 * averagedDataPLoss.i1) +
            (averagedDataPLoss.u2 * averagedDataPLoss.i2) +
            (averagedDataPLoss.u3 * averagedDataPLoss.i3) -
            (averagedDataPLoss.uOut * (averagedDataPLoss.i1 + averagedDataPLoss.i2 + averagedDataPLoss.i3))
        );



        let totalAmps = parseFloat(averagedDataI.i1) + parseFloat(averagedDataI.i2) + parseFloat(averagedDataI.i3);
        if(isNaN(totalAmps)){
            totalAmps = 0;
        }



        // init energy used offsets at first received data logging packet
        if(this.usedEnergyOffset1 === -1){
            this.usedEnergyOffset1 = bmsLoggingPacket.eU1;
        }
        if(this.usedEnergyOffset2 === -1){
            this.usedEnergyOffset2 = bmsLoggingPacket.eU2;
        }
        if(this.usedEnergyOffset3 === -1){
            this.usedEnergyOffset3 = bmsLoggingPacket.eU3;
        }




        // BATTERY SOC CALCULATION (very fucked atm)
        // _______________________________________

        let CellCount = this.bmsConfigObj.battCellCount;
        let Capacity = this.bmsConfigObj.battNomCapacity;

        let soc;
        if (this.remainingEnergy1 === 0 && (CellCount * 2.5 <= bmsLoggingPacket.u1 <= CellCount * 4.2)) {
            let cell_voltage = bmsLoggingPacket.u1 / CellCount;
            /*
            let soc;
            if(cell_voltage > 3.5){
                soc = -25.54 * cell_voltage ** 5 + 488.74 * cell_voltage ** 4 - 3736 * cell_voltage ** 3 + 14258 * cell_voltage ** 2 - 27164 * cell_voltage + 20664;

                //soc = -8.09 * 0.001 * x ** 9 + 5.44 * 0.01 * x ** 8 + 7.77 * 0.01 ** 7 + 1.82 * x ** 6 - 3.2 * x ** 5 + 84.1 * x ** 4 + 393 * x ** 3 - 2400 * x ** 2 + 3770 * x - 1380
            }else{
                soc =  11.171*cell_voltage**4 - 168.65*cell_voltage**3 + 952.15*cell_voltage**2 - 2381.2*cell_voltage + 2225.1;
            }
             */
            // temporary
            soc = (cell_voltage - 3) / 1.2;
            this.remainingEnergy1 = soc * (Capacity / 1000) * (CellCount * 3.7);
        }


        if (this.remainingEnergy2 === 0 && (CellCount * 2.5 <= bmsLoggingPacket.u2 <= CellCount * 4.2)) {
            let cell_voltage = bmsLoggingPacket.u2/CellCount;
            /*
            let soc;
            if(cell_voltage > 3.5){
                soc = -25.54*cell_voltage**5 + 488.74*cell_voltage**4 - 3736*cell_voltage**3 + 14258*cell_voltage**2 - 27164*cell_voltage + 20664;
            }else{
                soc =  11.171*cell_voltage**4 - 168.65*cell_voltage**3 + 952.15*cell_voltage**2 - 2381.2*cell_voltage + 2225.1;
            }
             */
            // temporary
            soc = (cell_voltage - 3) / 1.2;
            this.remainingEnergy2 = soc * (Capacity / 1000) * (CellCount * 3.7);
        }


        if (this.remainingEnergy3 === 0 && (CellCount * 2.5 <= bmsLoggingPacket.u3 <= CellCount * 4.2)) {
            let cell_voltage = bmsLoggingPacket.u3/CellCount;
            /*
            let soc;
            if(cell_voltage > 3.5){
                soc = -25.54*cell_voltage**5 + 488.74*cell_voltage**4 - 3736*cell_voltage**3 + 14258*cell_voltage**2 - 27164*cell_voltage + 20664;
            }else{
                soc =  11.171*cell_voltage**4 - 168.65*cell_voltage**3 + 952.15*cell_voltage**2 - 2381.2*cell_voltage + 2225.1;
            }
            */
            // temporary
            soc = (cell_voltage - 3) / 1.2;
            this.remainingEnergy3 = soc * (Capacity / 1000) * (CellCount * 3.7);
        }

        let SOC_1 = 100 * ((this.remainingEnergy1 - ((bmsLoggingPacket.eU1 - this.usedEnergyOffset1) / 3600)) / ((Capacity / 1000) * CellCount * 3.7));
        let SOC_2 = 100 * ((this.remainingEnergy2 - ((bmsLoggingPacket.eU2 - this.usedEnergyOffset2) / 3600)) / ((Capacity / 1000) * CellCount * 3.7));
        let SOC_3 = 100 * ((this.remainingEnergy3 - ((bmsLoggingPacket.eU3 - this.usedEnergyOffset3) / 3600)) / ((Capacity / 1000) * CellCount * 3.7));

        if (bmsLoggingPacket.u1 < CellCount * 2.5 || SOC_1 <= 0) {
            SOC_1 = 0;
            this.remainingEnergy1 = 0;
        }
        if (bmsLoggingPacket.u2 < CellCount * 2.5 || SOC_2 <= 0) {
            SOC_2 = 0;
            this.remainingEnergy2 = 0;
        }
        if (bmsLoggingPacket.u3 < CellCount * 2.5 || SOC_3 <= 0) {
            SOC_3 = 0;
            this.remainingEnergy3 = 0;
        }



        let minSOC = 100;
        if(((this.bmsConfigObj.boardEnabledChannels & 1) === 1) && SOC_1 < minSOC){minSOC = SOC_1}
        if(((this.bmsConfigObj.boardEnabledChannels & 2) === 2) && SOC_2 < minSOC){minSOC = SOC_2}
        if(((this.bmsConfigObj.boardEnabledChannels & 4) === 4) && SOC_3 < minSOC){minSOC = SOC_3}


        //const usedEnergyTotal = (bmsLoggingPacket.eU1 - usedEnergyOffset1) + (bmsLoggingPacket.eU2 - usedEnergyOffset2) + (bmsLoggingPacket.eU3 - usedEnergyOffset3);
        const usedEnergyTotal = bmsLoggingPacket.eU1 + bmsLoggingPacket.eU2 + bmsLoggingPacket.eU3;

        //_________________________________________________



        const bmsCalculatedDataPacket = {
            iTotal: totalAmps,
            pTotal: P_total,
            pLoss: P_loss,
            energyUsed: {
                combined: usedEnergyTotal,
                ch1: bmsLoggingPacket.eU1,
                ch2: bmsLoggingPacket.eU2,
                ch3: bmsLoggingPacket.eU3,
            },
            soc: {
                ch1: SOC_1,
                ch2: SOC_2,
                ch3: SOC_3,
                min: minSOC
            }
        };

        // update data in the data distribution
        this.dd.updateProvidedData({
            bmsITotal: totalAmps,
            bmsPTotal: P_total,
            bmsPLoss: P_loss,
            soc1: SOC_1,
            soc2: SOC_2,
            soc3: SOC_3,
            minSOC: minSOC
        });


        // do the mapping
        this.dd.executeMapping();

        // update all elements expecting data
        this.dd.updateDataExpecters();

        let maxValueInfo = {
            time: bmsLoggingPacket.time,
            totalAmps: totalAmps,
            uOut: bmsLoggingPacket.uOut,
            pTotal: P_total,
            tShunt: bmsLoggingPacket.tShunt,
            tPch: bmsLoggingPacket.tPch
        }

        // always have the newest packet at the first index of the buffer
        this.maxValueBuffer.unshift(maxValueInfo);
        // delete all packets that are older than needed
        sf.deleteOlderDataPackets(this.maxValueBuffer, this.interfaceConfig.maxValueHoldOnTime);


        let powersArray = [];
        let currentsArray = [];
        let voltagesArray = [];
        let prechargeTempArray = [];
        let shuntTempArray = [];

        this.maxValueBuffer.forEach((sample) => {
            currentsArray.unshift(parseFloat(sample.totalAmps).toFixed(1));
            voltagesArray.unshift(sample.uOut);
            powersArray.unshift(sample.pTotal);
            shuntTempArray.unshift(sample.tShunt);
            prechargeTempArray.unshift(sample.tPch);
        });




        /*
            setting gui elements
         */

        this.setBMSState(bmsLoggingPacket.stateMachineState);
        this.handleTurnOnTd(bmsLoggingPacket);
        this.handleConfigWarningButtons(bmsLoggingPacket.stateMachineState);


        this.setChannelVoltageInfo(averagedDataU);
        this.setChannelCurrentInfo(averagedDataI);

        this.setBMSTempValues(averagedDataT);

        this.setBMSMaxValues({
            power: Math.max(...powersArray).toFixed(2),
            maxCurrent: Math.max(...currentsArray),
            minCurrent: Math.min(...currentsArray),
            minVoltage: Math.min(...voltagesArray).toFixed(2),
            maxVoltage: Math.max(...voltagesArray).toFixed(2),
            shuntTemp: Math.max(...shuntTempArray).toFixed(1),
            prechargeTemp: Math.max(...prechargeTempArray).toFixed(1)
        });

        this.setBMSCalculatedValues(bmsCalculatedDataPacket);


        this.bmsCalibObj.updateActualData(averagedDataCalib);


        // this has to be after both setInlineTempValues and setInlineMaxValues
        // as it disables and overwrites the values put in by the functions above in case of disabled temps
        this.setTempGaugeVisibility(this.interfaceConfig);



        if (this.dataLoggingMgr.dataLoggingEnabled) {

            let dataStringBoard;

            const stamp = sf.getTimeStamp();
            const ms_today = sf.getMsToday();

            // as there are 11 numbers and 12 chars, the rough size is
            //11*8bytes + 12*2bytes = 112 bytes

            let boardData = [
                bmsLoggingPacket.packageSequenceNumber,
                ms_today,
                stamp,
                bmsLoggingPacket.u1,
                bmsLoggingPacket.u2,
                bmsLoggingPacket.u3,
                bmsLoggingPacket.uOut,
                bmsLoggingPacket.i1,
                bmsLoggingPacket.i2,
                bmsLoggingPacket.i3,
                bmsLoggingPacket.tShunt,
                bmsLoggingPacket.tPch
            ];

            dataStringBoard = boardData.join(";") + ";;;;;";

            this.dataLoggingMgr.loggingDataBuffer.push(dataStringBoard);
        }
    }

}