import interfaceConfig from "./settings/interface-config";
import dataLoggingManager from "./dataProcessing/datalogging";
import colorMode from "./helpers/color_mode";
import virtualKeyboard from "./settings/virtualkeyboard";
import connectionGUIManager from "./connection_gui";
import rearrangeManager from "./rearrange";
import tilebar from "./tilebar";
import captureModeManager from "./captureMode";
import gui from "./gui";
import serviceWorkerInit from "./serviceworker/sw_init";
import otaManager from "./otaUpdate/otaManager";
import vehicleModel from "./vehicleModel";
import dataDistribution from "./dataDistribution/dataDistribution";
import deviceInfoManager from "./settings/deviceInfoManager";
import dataDistributionGUIManager from "./dataDistribution/dataDistributionGUIManager";
import dbcGUIManager from "./can/dbcGUIManager";

import disconnect_watchdog from "./ble/disconnect_watchdog";
import bms from "./ble/bms";
import tacho from "./ble/tacho";



let dbcGUIMgr = new dbcGUIManager();



let vehicleModelObj = new vehicleModel();


let deviceInfoMgr = new deviceInfoManager();


new serviceWorkerInit();
new virtualKeyboard();

let interfaceConfigObj = new interfaceConfig();
let colorModeObj = new colorMode(interfaceConfigObj);
interfaceConfigObj.initColorModeObj(colorModeObj);

let captureModeManagerObj = new captureModeManager();
let dataLoggingManagerObj = new dataLoggingManager();




let bmsObj = new bms(interfaceConfigObj, dataLoggingManagerObj, deviceInfoMgr);


let guiObj = new gui();

guiObj.disableBoardGauges();
guiObj.addGUIEventListeners();
guiObj.initBmsObj(bmsObj);

let rearrangeManagerObj = new rearrangeManager(bmsObj);


//let tachoObj = new tacho(bmsObj, interfaceConfigObj, dataLoggingManagerObj, deviceInfoMgr);
let tachoObj = new tacho(interfaceConfigObj, dataLoggingManagerObj, deviceInfoMgr);
tachoObj.initBMSObj(bmsObj);
bmsObj.initTachoObj(tachoObj);


let dataDistributionObj = new dataDistribution();
bmsObj.initDataDistribution(dataDistributionObj);
tachoObj.initDataDistribution(dataDistributionObj);

vehicleModelObj.initDataDistribution(dataDistributionObj);

dataDistributionObj.addMapping("bmsITotal", "torqueRL");
dataDistributionObj.addMapping("bmsITotal", "torqueRR");


let dataDistributionGUIMgr = new dataDistributionGUIManager(dataDistributionObj);
dataDistributionGUIMgr.fillMappingTable();
dataDistributionGUIMgr.fillInputTable();


let otaManagerObj = new otaManager(bmsObj, tachoObj, deviceInfoMgr);
otaManagerObj.addEventListeners();

new connectionGUIManager(bmsObj, tachoObj);


let tb = new tilebar(
    bmsObj,
    tachoObj,
    captureModeManagerObj,
    dataLoggingManagerObj,
    colorModeObj,
    interfaceConfigObj,
    rearrangeManagerObj,
    otaManagerObj,
    dataDistributionGUIMgr,
    dbcGUIMgr
);
tb.addEventListeners();








new disconnect_watchdog(500, 2000, bmsObj, tachoObj);



