import bleConnectionStates, {CharacteristicsInitFailedError, ServicesInitFailedError, WrongDeviceError} from "./ble";
import connectionWatchDog from "../helpers/connectionWatchDog";
import gui from "../gui";
import sf from "../helpers/small_functions";

// noinspection JSCheckFunctionSignatures
export default class bleDevice extends gui{

    connectionState;

    gattServer;
    device;

    services;
    characteristics;

    connectionWatchDog;

    guiOperations = {};

    reconnect = false;
    firstReconnectFailDone = true;


    disconnectedCallback;

    interfaceConfigObj;

    // important to keep the disconnect watchdog from cancelling the ota update
    otaUpdateInProgress = false;


    constructor(name, interfaceConfig){
        super();

        this.connectionState = bleConnectionStates.disconnected;

        this.interfaceConfigObj = interfaceConfig;
        this.deviceTypeName = name;

        this.disconnectedEventAbortController = new AbortController();
    }

    isConnected(){ return this.connectionState === bleConnectionStates.connected; }

    isDisconnected(){ return this.connectionState === bleConnectionStates.disconnected; }


    setDisconnectedCallback(cB){ this.disconnectedCallback = cB; }
    setServices(services){ this.services = services; }
    setServiceInitDoneCallback(cB) { this.serviceInitDoneCallback = cB; }
    setGUIOperations(guiOperations){
        this.guiOperations.setOverlayText = (text, override=false) => {
            if(this.firstReconnectFailDone || override){
                guiOperations.setOverlayText(text);
            }
        };

        this.guiOperations.resetOverlayText = () => {
            if(this.firstReconnectFailDone) {
                guiOperations.resetOverlayText();
            }
        }
    }





    connect(){
        // ignore requests to connect if already connected
        if(this.connectionState !== bleConnectionStates.disconnected) {
            console.warn("[bleDevice " + this.deviceTypeName + "] tried to connect while not disconnected!");
            return;
        }
        // check for properly set guiOperations
        if(typeof this.guiOperations.setOverlayText !== "function"
        && typeof this.guiOperations.resetOverlayText !== "function"){
            console.warn("[bleDevice " + this.deviceTypeName + "] guiOperations not set!");
            return;
        }


        // set up the connectionWatchDog for this connection attempt
        this.connectionWatchDog = new connectionWatchDog(
            this.interfaceConfigObj.connectionStepTimeout * 1000,
            async ()=>{
                console.warn("[bleDevice " + this.deviceTypeName + "] connection Watchdog timed out!");
                await this.disconnect();
            });


        // indicate searching
        this.guiOperations.setOverlayText("Searching");
        this.connectionState = bleConnectionStates.searching;

        // disable any auto-reconnects!
        this.reconnect = false;


        // watch out as this is async and waits for the previous device to disconnect!
        navigator.bluetooth.requestDevice(this.#getSearchOptions()).then(async (device)=>{

            // check if the previous device is still in memory
            // and if so, disconnect from it
            if(typeof this.device !== "undefined" && this.device !== null){
                // also unregister the disconnect listener of the previous device
                // (to prevent multiple being registered)
                this.disconnectedEventAbortController.abort();

                // and reset / disconnect it if so
                await this.disconnect();
            }


            // indicate that a device has been found
            // and set connectionState to connecting!
            this.guiOperations.resetOverlayText();
            this.guiOperations.setOverlayText("Found Device");
            this.connectionState = bleConnectionStates.connecting;



            // reset abortController to not abort the incoming registration
            this.disconnectedEventAbortController = new AbortController();

            this.device = device;

            // register the disconnect listener for the current device
            this.device.addEventListener('gattserverdisconnected', async ()=>{
                console.log("[bleDevice " + this.deviceTypeName + "] disconnected");

                // kill this exact event listener if reconnect is disabled
                if(!this.reconnect){
                    this.disconnectedEventAbortController.abort();
                }

                await this.#callDisconnectedCB();
            },{signal: this.disconnectedEventAbortController.signal});


            this.guiOperations.setOverlayText("Connecting Device");

            // connect the device and then call initialize the services
            this.connectionWatchDog.start();
            this.device.gatt.connect().then((server) => {
                this.connectionWatchDog.stop();

                this.gattServer = server;
                this.guiOperations.setOverlayText("Connected GATT Server");

                // initialize all services (sequentially!)
                if(this.connectionWatchDog.timedOut){return;}
                this.connectionWatchDog.start();
                this.#initServices().then(()=>{
                    this.connectionWatchDog.stop();

                    // a callback does config and calib reading and all the other stuff
                    try{
                        this.#callServiceInitDoneCallback();

                        // reset the indicator for the next re-connect attempt
                        this.firstReconnectFailDone = false;

                        console.log("[bleDevice " + this.deviceTypeName + "] Done with connection process!");

                    }catch(error) {
                        this.#connectionFailed(error);
                    }

                }).catch((error) => {
                    this.connectionWatchDog.stop();

                    this.#connectionFailed(error);
                });

            }).catch((error) => {
                this.#connectionFailed(error);
            });

        }).catch((error) => {
            this.#connectionFailed(error);
        });
    }


    async #initServices(attempt = 0){
        for (const serviceKey of Object.keys(this.services)) {

            // get real service name by cutting off "Uuid" from the end
            let serviceName = serviceKey.split("Uuid")[0];

            // signal the user, which service is being initialized
            this.guiOperations.setOverlayText("Initializing " + serviceName);


            // initialize the service and it's characteristics (all sequentially)
            let serviceObj = this.services[serviceKey];
            try{
                let serviceCharacteristics = await this.#initService(serviceObj);

                // merge the newly initialized characteristics into the existing object
                this.characteristics = Object.assign({}, this.characteristics, serviceCharacteristics);
            }catch(error) {
                // always good for debugging
                if(attempt > 3){
                    console.error(error);
                }

                // check if the error is "No matching UUIDs found"
                if(error instanceof WrongDeviceError && attempt > 2){
                    // DomException: No matching UUID found

                    // do not show the first re-connect try (that always fails) to the user!
                    if(this.firstReconnectFailDone) {
                        this.guiOperations.setOverlayText("Not a " + this.deviceTypeName + "!");
                    }

                    console.warn("[bleDevice " + this.deviceTypeName + "] Not a "+ this.deviceTypeName + "!");

                    // only display this error to the user if reconnect is disabled
                    if(!this.reconnect){
                        setTimeout(() => {
                            this.guiOperations.resetOverlayText();
                        }, 1000);
                    }

                    throw new WrongDeviceError();
                }

                if(error instanceof CharacteristicsInitFailedError && attempt > 2){
                    // DomException: No matching UUID found


                    // do not show the first re-connect try (that always fails) to the user!
                    if(this.firstReconnectFailDone) {
                        this.guiOperations.setOverlayText("Service init failed!");
                    }

                    console.error("[bleDevice " + this.deviceTypeName + "] Service init failed!");

                    // only display this error to the user if reconnect is disabled
                    if(!this.reconnect) {
                        setTimeout(() => {
                            this.guiOperations.resetOverlayText();
                        }, 1000);
                    }

                    throw new ServicesInitFailedError();
                }


                // if the number of attempts has not yet reached two:
                // retry the process

                // do not show the first re-connect try (that always fails) to the user!
                if(this.firstReconnectFailDone) {
                    this.guiOperations.setOverlayText("Attempt #" + (attempt + 1));
                }

                // dirty way to have a timeout without calling the initServices function from an anonymous function
                let tmp = false;
                setTimeout(()=>{
                    tmp = true;
                }, 200);

                await sf.until(()=>{return tmp;});

                await this.#initServices(attempt + 1);

                // and stop the execution of the loop
                break;

            }
        }

    }

    async #initService(serviceObj){
        let characteristics = {};

        // get the gatt service
        let service;

        if(this.connectionWatchDog.timedOut){return;}
        this.connectionWatchDog.start();
        try{
            service = await this.gattServer.getPrimaryService(serviceObj.serviceUuid);

            this.connectionWatchDog.stop();
        }catch(error){
            this.connectionWatchDog.stop();

            // trow the custom implemented WrongDeviceError if the service is required for the device to function
            if(serviceObj.required){
                throw new WrongDeviceError();
            }

            // do nothing if the error is simply that the service cannot be found
            if(error instanceof DOMException && error.message.includes("No Services matching UUID")){
                return;
            }

            // otherwise throw the error
            throw error;
        }



        for (const characteristicKey of Object.keys(serviceObj.characteristics)) {

            // cut off "Uuid" from the characteristicUuid key to get the real name of the characteristic
            let characteristicName = characteristicKey.split("Uuid")[0];

            // get the characteristic
            if(this.connectionWatchDog.timedOut){return;}
            this.connectionWatchDog.start();
            try{
                characteristics[characteristicName] = await service.getCharacteristic(serviceObj.characteristics[characteristicKey]);

                this.connectionWatchDog.stop();
            }catch(error) {
                this.connectionWatchDog.stop();

                // throw an error
                throw new CharacteristicsInitFailedError(characteristicName);
            }

        }
        return characteristics;
    }


    // requests a device disconnect
    async disconnect(){
        // in case of the device not being initialized, just do nothing
        if(typeof this.device === "undefined"){
            await this.#callDisconnectedCB();
            return;
        }

        // only disconnect if not already disconnected
        if(!this.device.gatt.connected){
            this.connectionState = bleConnectionStates.disconnected;

            await this.#callDisconnectedCB();
            return;
        }

        console.log("[bleDevice " + this.deviceTypeName + "] disconnecting");

        try{
            this.device.gatt.disconnect();

            // check if disconnect really happened and try again if not
            // BE CAUTIOUS, THIS FIRES QUITE A FEW TIMES
            setTimeout(async ()=>{
                if(!this.isDisconnected()){
                    await this.disconnect();
                }
            }, 100);

            await sf.until(()=>{ this.isDisconnected(); });
        }catch(e) {
            console.warn("[bleDevice " + this.deviceTypeName + "] problem while disconnecting - manually calling disconnected callback");

            await this.#callDisconnectedCB();

            this.connectionState = bleConnectionStates.disconnected;
        }
    }


    #handleReconnect(){
        // double-checking - redundant
        if(this.isDisconnected() && this.reconnect){
            console.log("[bleDevice " + this.deviceTypeName + "] starting reconnect-attempt");
            if(!this.firstReconnectFailDone){
                this.guiOperations.setOverlayText("Initializing Auto-Reconnect..", true);
            }


            setTimeout(async ()=>{
                this.device.gatt.connect().then(async (server) => {
                    if(this.reconnect){
                        this.gattServer = server;
                        try{

                            // same code as in the original connection process
                            this.#initServices().then(()=>{
                                this.connectionWatchDog.stop();

                                // a callback does config and calib reading and all the other stuff
                                try{
                                    this.#callServiceInitDoneCallback();

                                    // reset the indicator for the next re-connect attempt
                                    this.firstReconnectFailDone = false;

                                    console.log("[bleDevice " + this.deviceTypeName + "] Done with connection process!");

                                }catch(error) {
                                    this.#connectionFailed(error);
                                }

                            }).catch((error) => {
                                this.connectionWatchDog.stop();

                                this.#connectionFailed(error);

                            });
                        }catch(error){
                            if(error instanceof WrongDeviceError){
                                console.warn("[auto-reconnect bms] failed: " + error);

                                // the handleReconnect recursion already occurs in the disconnect function!
                                await this.disconnect();
                            }else{
                                throw error;
                            }
                        }
                    }else{
                        console.warn("[auto-reconnect bms] reconnected but reconnect is off");
                        server.disconnect();
                    }
                }).catch(async (error) => {
                    console.warn("[auto-reconnect bms] failed: " + error);

                    // the handleReconnect recursion already occurs in the disconnect function!
                    await this.disconnect();
                });
            }, 500);
        }
    }


    #connectionFailed(error){
        if(error instanceof WrongDeviceError && this.firstReconnectFailDone){
            console.error("[bleDevice " + this.deviceTypeName + "] connection failed: ");
            console.error(error);
        }

        // set the firstReconnectFailDone flag to true
        if(this.reconnect && !this.firstReconnectFailDone){
            this.firstReconnectFailDone = true;
        }

        // for safety, let's disconnect
        this.disconnect().then();
    }



    async #callDisconnectedCB(){
        // firstly, do some of the disconnect handling here, then call the outside function
        this.connectionWatchDog.stop();


        // just to make sure
        if(typeof this.device !== "undefined"){
            await sf.until(()=>{return !this.device.gatt.connected;});
        }

        this.connectionState = bleConnectionStates.disconnected;


        if(typeof this.disconnectedCallback === "function"){
            this.disconnectedCallback();
        }else{
            console.warn("[bleDevice " + this.deviceTypeName + "] disconnected callback is not a function!");
        }

        // this has to be called last as otherwise the "auto-reconnect active" banner doesn't show up
        if(this.reconnect){

            this.guiOperations.setOverlayText("Auto-reconnect active");

            this.#handleReconnect();
        }

    }

    #callServiceInitDoneCallback(){
        if(typeof this.serviceInitDoneCallback === "function"){
            try{
                this.serviceInitDoneCallback();
            }catch(error){
                throw error;
            }
        }else{
            console.warn("[bleDevice " + this.deviceTypeName + "] serviceInitDone callback is not a function!");
        }
    }



    #getSearchOptions(){

        // check for initialized services
        if(typeof this.services === "undefined"){
            console.warn("[bleDevice " + this.deviceTypeName + "] services undefined!");
            return;
        }

        let options = {
            optionalServices: [
                'device_information'
            ]
        };

        for (const service of Object.keys(this.services)) {
            options.optionalServices.push(this.services[service].serviceUuid);
        }

        if(this.interfaceConfigObj.filterBMSConnection) {
            options.filters = [{
                manufacturerData: [{
                    // nice
                    companyIdentifier: 0x6969
                }]
            }];
        }else{
            options.acceptAllDevices = true;
        }

        return options;
    }

}