import sf from "../helpers/small_functions";
import gui_elements from "../gui_elements";

import Keyboard from 'simple-keyboard';


export default class virtualKeyboard extends gui_elements{

    focussedInput;

    hideKeyboard = false;

    interfaceSettingsContainerLastSeen = 0;

    constructor() {
        super();

        if(sf.mobileDevice()) {
            this.keyboard = new Keyboard({
                onChange: input => this.onChange(input),
                layout: {
                    default: ["1 2 3", "4 5 6", "7 8 9", ". 0 {bksp}"]
                },
                theme: "hg-theme-default hg-layout-numeric numeric-theme",
                display: {
                    '{bksp}': 'del'
                }
            });




            // I don't really know about performance
            window.addEventListener('scroll', () => {
                // show keyboard if settings are scrolled into view
                if (this.keyboardShouldBeVisible() && this.keyboardContainer.classList.contains("visibility-hidden")) {
                    if(!this.hideKeyboard){
                        this.keyboardContainer.classList.remove("visibility-hidden");

                        this.keyboardContainer.addEventListener("animationend", ()=>{
                            this.keyboardContainer.classList.remove("blend-in");
                        }, {once : true});

                        this.keyboardContainer.classList.add("blend-in");
                    }
                }

                if(!this.keyboardShouldBeVisible() && !this.keyboardContainer.classList.contains("visibility-hidden")){

                    this.keyboardContainer.addEventListener("animationend", ()=>{
                        this.keyboardContainer.classList.add("visibility-hidden");
                        this.keyboardContainer.classList.remove("blend-out");
                    }, {once : true});

                    this.keyboardContainer.classList.add("blend-out");
                }



                // disable interface settings if they haven't been in view for over a second
                if(sf.isScrolledIntoView(this.interfaceSettingsContainer)){
                    this.interfaceSettingsContainerLastSeen = Date.now();
                }
                if((Date.now() - this.interfaceSettingsContainerLastSeen) > 1000
                    && !this.interfaceSettingsContainer.classList.contains("display-none")){
                    this.interfaceSettingsContainer.classList.add("display-none");
                }
            });


            // set the cursor to the right on all input fields (a bit whack but there is no nicer solution)
            document.querySelectorAll("input[type='number']").forEach(item => {
                item.addEventListener('focus', event => {
                    // disable the stock virtual keyboard
                    event.target.blur();


                    // clear all other focussed animations
                    document.querySelectorAll("input").forEach(item => {
                        item.classList.remove("accent");
                    });
                    event.target.classList.add("accent");

                    const value = event.target.value;
                    event.target.value = '';
                    setTimeout(() => {
                        event.target.value = value;
                    }, 1);

                    // set keyboard input target and keyboard input
                    this.focussedInput = event.target;
                    this.keyboard.setInput(value);
                });
            });


            // event listener for all coloris inputs to instantly blur the selected input field
            document.querySelectorAll(".coloris").forEach((element)=>{
                element.addEventListener("click", (event)=>{
                    event.target.blur();
                });
            });



            // focus logic for bms name input
            document.querySelectorAll("input[type='text']").forEach(item => {
                item.addEventListener('focus', event => {
                    document.querySelectorAll("input").forEach(item => {
                        item.classList.remove("accent");
                    });
                    event.target.classList.add("accent");

                    // hide virtual keyboard to use os keyboard (emojis and stuff)
                    this.keyboardContainer.classList.add("display-none");
                    this.hideKeyboard = true;

                    const value = event.target.value;
                    event.target.value = '';
                    setTimeout(() => {
                        event.target.value = value;
                    }, 1);
                });

                item.addEventListener('focusout', event => {
                    event.target.classList.remove("accent");

                    this.hideKeyboard = false;
                    this.keyboardContainer.classList.remove("display-none");
                });
            });

        }else{
            // i don't really know about performance
            window.addEventListener('scroll', () => {
                // disable interface settings if they haven't been in view for over a second
                if(sf.isScrolledIntoView(this.interfaceSettingsContainer)){
                    this.interfaceSettingsContainerLastSeen = Date.now();
                }
                if((Date.now() - this.interfaceSettingsContainerLastSeen) > 1000
                    && !this.interfaceSettingsContainer.classList.contains("display-none")){
                    this.interfaceSettingsContainer.classList.add("display-none");
                }
            });
        }
    }

    onChange(input) {
        // check if the value is inside view and scroll to the element if not

        // only use last "." in input
        if (input.split(".").length - 1 > 1) {
            input = input.split('.').slice(0, -1).join('') + "." + input.split('.').slice(-1);
        }

        let keyboardDecimal = false;
        if (input.charAt(input.length - 1) !== '.'){
            if (keyboardDecimal) {
                this.focussedInput.value = input.substring(0, input.length - 1);
            } else {
                this.focussedInput.value = input;
            }
        }


        // create a new change event and dispatch it
        // otherwise the input does not recognize the change,
        // and therefore the interface config does not work
        this.focussedInput.dispatchEvent(new Event('change'));
    }

    keyboardShouldBeVisible(){

        let settingsNumberInputs = [
            ...document.querySelectorAll("#board-configuration-div [type='number']"),
            ...document.querySelectorAll("#inline-configuration-div [type='number']"),
            ...document.querySelectorAll("#board-calibration-div [type='number']"),
            ...document.querySelectorAll("#interface-settings-container [type='number']")
        ];

        let settingsScrolledIntoView = false;
        settingsNumberInputs.forEach((element) => {
            if(sf.isScrolledIntoViewWithKeyboard(element)){
                settingsScrolledIntoView = true;
            }
        });

        // it's complicated
        return (settingsScrolledIntoView
            || sf.isScrolledIntoViewWithKeyboard(this.boardConfigContainer)
            || sf.isScrolledIntoViewWithKeyboard(this.boardCalibContainer)
            || sf.isScrolledIntoViewWithKeyboard(this.inlineConfigContainer)
            || sf.isScrolledIntoViewWithKeyboard(this.interfaceSettingsContainer));
    }

}
