export default class disconnect_watchdog{
    enabled = true;
    interval;

    timeout;

    constructor(interval, timeout, bmsObj, tachoObj){
        this.interval = interval;
        this.timeout = timeout;

        this.bmsObj = bmsObj;
        this.tachoObj = tachoObj;

        this.#loop();
    }

    start(){
        if(!this.enabled){
            this.enabled = true;
            this.#loop();
        }
    }

    stop(){
        this.enabled = false;
    }

    #loop(){
        setTimeout(()=>{
            if(this.enabled){
                this.#loop();
            }
        }, this.interval);

        // checking for logging data timeout on bms

        if(!this.bmsObj.isDisconnected()){
            // this only happens when the connection is broken or when data logging is paused
            if(this.bmsObj.bmsDataProcessor.bmsAveragingBuffer.length > 0){
                // don't interrupt the upload of new firmware!
                if(!this.bmsObj.otaUpdateInProgress){
                    if((Date.now() - this.bmsObj.bmsDataProcessor.bmsAveragingBuffer[0].time) > this.timeout){
                        this.bmsObj.disconnect().then();
                    }
                }
            }
        }

        if(!this.tachoObj.isDisconnected()){
            if(this.tachoObj.tachoDataProcessor.tachoAveragingBuffer.length > 0){
                // don't interrupt the upload of new firmware!
                if(!this.tachoObj.otaUpdateInProgress) {
                    if ((Date.now() - this.tachoObj.tachoDataProcessor.tachoAveragingBuffer[0].time) > this.timeout) {
                        this.tachoObj.disconnect().then();
                    }
                }
            }
        }
    }
}