import gui_elements from "../gui_elements";
import sf from "../helpers/small_functions";

export default class deviceInfoManager extends gui_elements{

    chooser;

    devicesInfo;

    chosenDevice;

    #blocked = false;

    constructor() {
        super();

        this.devicesInfo = new devicesInfos();

        // if there is more than one instance, everything is fucked anyway
        this.chooser = document.getElementsByClassName("device-info-device-chooser")[0];

        this.addEventListeners();
    }



    addEventListeners(){

        if(this.#blocked){
            return;
        }

        let options = this.#getDeviceOptions();

        this.chooser.addEventListener("click", (event)=>{

            options.forEach((option) => {

                // there are "text" nodes in between the actual h2 nodes, which for our purpose need to be ignored
                if(typeof option.classList !== "undefined"){
                    option.classList.add("device-info-device-not-chosen");
                }else{
                    return;
                }

                if(option.isSameNode(event.target)){

                    option.classList.remove("device-info-device-not-chosen");

                    this.chosenDevice = event.target.innerHTML.toLowerCase();
                }
            });

            this.#updateGUIFields(this.devicesInfo.getDeviceInfo(this.chosenDevice));

        });

    }

    #updateGUIFields(deviceInfo){
        sf.setValueTexts(this.deviceModelFields, deviceInfo.modelNumber);
        sf.setValueTexts(this.deviceHardwareRevisionFields, deviceInfo.hardwareRevision);
        sf.setValueTexts(this.deviceFirmwareRevisionFields, deviceInfo.firmwareRevision);
        sf.setValueTexts(this.deviceSerialNumberFields, deviceInfo.serialNumber);

        if(deviceInfo.serialNumber === "not supported"){
            sf.classListValueTextsAdd(this.deviceSerialNumberFields, "fault");
        }

        if(deviceInfo.modelNumber === "not supported"){
            sf.classListValueTextsAdd(this.deviceModelFields, "fault");
        }
    }

    #getDeviceOptions(){
        let deviceOptions = [];

        this.chooser.childNodes.forEach((childNode) => {
            deviceOptions.push(childNode);
        });

        return deviceOptions;
    }


    blockChooser(){
        this.#blocked = true;
    }
    unblockChooser(){
        this.#blocked = false;
    }

}






export class devicesInfos{

    deviceInfos = {};
    constructor(){
    }

    getDeviceInfo(deviceType){
        let info = {
            modelNumber: "not connected",
            hardwareRevision: "not connected",
            firmwareRevision: "not connected",
            serialNumber: "not connected"
        };

        if(Object.keys(this.deviceInfos).includes(deviceType.toLowerCase())){
            info = this.deviceInfos[deviceType.toLowerCase()];
        }

        return info;
    }

    updateDeviceInfo(deviceType, deviceInfo){
        this.deviceInfos[deviceType.toLowerCase()] = deviceInfo;
    }
}