export default class fileObject{

    metadata = {};

    // placeholder tag for legacy file save option
    aDownloadFile = document.getElementById('aDownloadFile');

    constructor(fileType){
        this.fileType = fileType;

        this.metadata.fileType = fileType;

        // hide "select file" option if there is no file access as it doesn't work anyway
        if(!this.hasFileAccess()){
            document.getElementById("tileBarSelectDataLoggingFile").classList.add("hidden");
        }
    }

    async getSaveFileHandle(suggestedName) {

        if(!this.hasFileAccess()) {
            return;
        }

        let options = {
            suggestedName: suggestedName,
            excludeAcceptAllOption: true,
            multiple: false
        }

        if(this.fileType === "csv"){
            options.types = [{
                description: "CSV File",
                accept: {"text/csv": [".csv"]},
            }]
        }

        if(this.fileType === "json"){
            options.types = [{
                description: "JSON File",
                accept: {"text/json": [".json"]},
            }]
        }

        // create a new handle
        this.fileHandle = await window.showSaveFilePicker(options);

        // create a FileSystemWritableFileStream to write to
        this.writableStream = await this.fileHandle.createWritable();
    }

    async getReadFileHandle(){
        const options = {
            types: [
                {
                    description: this.fileType + ' File',
                    accept: {
                        'text/*': ['.' + this.fileType]
                    }
                },
            ],
            excludeAcceptAllOption: true,
            multiple: false
        };

        // return value is a one element array, this deconstructs it
        [this.fileHandle] = await window.showOpenFilePicker(options);

    }

    async readFile(){
        // sets the this.fileHandle object once ready
        await this.getReadFileHandle();

        const file = await this.fileHandle.getFile();

        this.name = file.name;
        this.size = file.size;

        // this should be eventually switched to reliance on the metadata object everywhere
        this.metadata.name = file.name;
        this.metadata.size = file.size;

        return file;
    }

    saveLegacy(suggestedName, dataBuffer){
        if(this.hasFileAccess()){
            return;
        }

        if(dataBuffer.length < 2){
            return;
        }

        let options = {};
        if(this.fileType === "csv"){
            options.type = 'text/csv';
        }

        if(this.fileType === "xml"){
            options.type = 'text/xml';
        }

        const file = new File([dataBuffer.join("\n")], '', options);

        this.aDownloadFile.href = window.URL.createObjectURL(file);
        this.aDownloadFile.setAttribute('download', suggestedName);
        this.aDownloadFile.click();
    }

    hasFileAccess(){
        return 'chooseFileSystemEntries' in window || 'showOpenFilePicker' in window;
    }
}