import gui_elements from "../gui_elements";


export default class colorMode extends gui_elements{

    mode = "dark";

    constructor(interfaceConfigObj){
        super();

        this.interfaceConfigObj = interfaceConfigObj;

        this.addEventListeners();
    }

    addEventListeners(){
        document.addEventListener('coloris:pick', _ => {

            // always set the color mode to custom when editing - to see the changes live
            this.mode = "custom";

            // writing them instantly to the localStorage
            localStorage.setItem('customColorForeground', this.getCustomForegroundColor());
            localStorage.setItem('customColorBackground', this.getCustomBackgroundColor());
            localStorage.setItem('customColorAccent', this.getCustomAccentColor());
            localStorage.setItem('customColorCaptureModeBackground', this.getCustomCaptureModeBackgroundColor());

            localStorage.setItem('lastColorMode', this.mode);

            // load the stored values back in
            this.interfaceConfigObj.updateConfig();
            this.adjust(this.interfaceConfigObj.colorMode);
        });
    }

    toggle(){
        if(this.mode === "dark"){this.mode = "light";}
        else if(this.mode === "light"){this.mode = "custom";}
        else if(this.mode === "custom"){this.mode = "dark";}

        localStorage.setItem('lastColorMode', this.mode); // for the next session

        this.adjust(this.mode);
    }

    adjust(targetMode){

        this.mode = targetMode;

        switch (targetMode) {
            case "light":
                document.getElementsByTagName("link")[1].href = "css/index-light.css";
                break;

            case "dark":
                document.getElementsByTagName("link")[1].href = "css/index-dark.css";
                break;

            case "custom":
                // remove color values form css files since then the root vars aren't overwritten
                document.getElementsByTagName("link")[1].href = "";
                break;

            default:
                document.getElementsByTagName("link")[1].href = "css/index-dark.css";

        }


        // make sure color pickers are correct with the displayed color
        document.getElementById("interface-foreground-color").parentNode.style.color = this.interfaceConfigObj.customColorForeground;
        document.getElementById("interface-background-color").parentNode.style.color = this.interfaceConfigObj.customColorBackground;
        document.getElementById("interface-accent-color").parentNode.style.color = this.interfaceConfigObj.customColorAccent;
        document.getElementById("capture-mode-background-color").parentNode.style.color = this.interfaceConfigObj.customColorCaptureModeBackground;

        // set the main colors
        const fg = this.interfaceConfigObj.customColorForeground;
        const bg = this.interfaceConfigObj.customColorBackground;
        const ac = this.interfaceConfigObj.customColorAccent;
        const cpmbg = this.interfaceConfigObj.customColorCaptureModeBackground;

        this.domRoot.style.setProperty('--background-color', bg);
        this.domRoot.style.setProperty('--text-color', fg);
        this.domRoot.style.setProperty('--accent-color', ac);

        this.domRoot.style.setProperty('--capture-mode-background-color', cpmbg);


        this.domRoot.style.setProperty('--color-good', 'green');
        this.domRoot.style.setProperty('--color-bad', 'red');
        this.domRoot.style.setProperty('--color-warning', 'orange');


        // the hard part - calculating all the dependant colors from the three main colors

        let tileBarColor = new color(bg);
        tileBarColor.scale(0.7);
        this.domRoot.style.setProperty('--tilebar-background-color', tileBarColor.getString());


        let dropDownColor = new color(bg);
        tileBarColor.scale(1.2);
        this.domRoot.style.setProperty('--dropdown-background-color', dropDownColor.getString());

        let gaugeDivBorderColor = new color(bg);
        gaugeDivBorderColor.contrast(0.5);
        this.domRoot.style.setProperty('--gauge-div-border-color', gaugeDivBorderColor.getString());

        let mainTableGridColor = new color(bg);
        mainTableGridColor.contrast(0.5);
        this.domRoot.style.setProperty('--main-table-grid-color', mainTableGridColor.getString());

        let configInputBackgroundColor = new color(bg);
        configInputBackgroundColor.contrast(0.5);
        this.domRoot.style.setProperty('--config-input-background-color', configInputBackgroundColor.getString());

        let configInputTextColor = new color(fg);
        configInputTextColor.scale(1);
        this.domRoot.style.setProperty('--config-input-text-color', configInputTextColor.getString());

        let nothingConnectedOverlayColor = new color(bg);
        nothingConnectedOverlayColor.scale(1.5);
        nothingConnectedOverlayColor.a = 0.7;
        this.domRoot.style.setProperty('--nothing-connected-overlay-background-color', nothingConnectedOverlayColor.getString());

    }


    getCustomForegroundColor(){
        return document.getElementById("interface-foreground-color").parentNode.style.color;
    }
    getCustomBackgroundColor(){
        return document.getElementById("interface-background-color").parentNode.style.color;
    }
    getCustomAccentColor(){
        return document.getElementById("interface-accent-color").parentNode.style.color;
    }

    getCustomCaptureModeBackgroundColor(){
        return document.getElementById("capture-mode-background-color").parentNode.style.color;
    }
}



class color {
    constructor(colorString) {
        if(!colorString.includes("rgb")){
            console.log("fuck hex colors");
        }

        let colorStringParams = colorString
            .replaceAll(' ','')
            .replaceAll('rgb', '')
            .replaceAll('a','')
            .replaceAll('(','')
            .replaceAll(')','')
            .split(',');

        this.r = parseInt(colorStringParams[0]);
        this.g = parseInt(colorStringParams[1]);
        this.b = parseInt(colorStringParams[2]);

        // if color is set as rgba
        if(colorStringParams.length === 4){
            this.a = parseFloat(colorStringParams[3]);
        }else{
            // default to 1
            this.a = 1;
        }
    }

    scale(amount){
        this.r += Math.floor((1-amount) * this.r);
        this.g += Math.floor((1-amount) * this.g);
        this.b += Math.floor((1-amount) * this.b);

        this.containInLimits();
    }

    contrast(amount){
        this.r += Math.floor(amount * (128 - this.r));
        this.g += Math.floor(amount * (128 - this.g));
        this.b += Math.floor(amount * (128 - this.b));

        this.containInLimits();
    }

    containInLimits(){

        if(this.r > 255){ this.r = 255; }
        if(this.g > 255){ this.g = 255; }
        if(this.b > 255){ this.b = 255; }

        if(this.r < 0){ this.r = 0; }
        if(this.g < 0){ this.g = 0; }
        if(this.b < 0){ this.b = 0; }
    }

    getString(){
        return "rgba(" + this.r + "," + this.g + "," + this.b + "," + this.a + ")";
    }
}